import { createSelector } from 'reselect'
import EthercatController from "../../../ethercat/EthercatController";
import {EthercatMaster} from "../../../ethercat/EthercatMaster";
import {getXml} from "../../../selectors";
import EthercatSlave from "../../../ethercat/EthercatSlave";

export const getSetEsiPathResult = (state, {cell}) => state.robot.resultsByChannel[`${cell.i}-setesipath`]
export const getScanSlaveResult = (state, {cell}) => state.robot.resultsByChannel[`${cell.i}-scanslave`]
export const getPdoListResultForDevice = (state, {cell}) => state.robot.resultsByChannel[`${cell.i}-pdolist-for-device`]
export const getPdoListResultForSlave = (state, {cell}) => state.robot.resultsByChannel[`${cell.i}-pdolist-for-slave`]
export const getSaveResult = (state, {cell}) => state.robot.resultsByChannel[`${cell.i}-save`]

export const getEsiFileUploadingRequest = state => state.robot.esiFileUploadingRequest
export const getEsiFileUploadingSuccess = state => state.robot.esiFileUploadingSuccess
export const getEsiFileUploadingFailure = state => state.robot.esiFileUploadingFailure

export const getScanSlaveJsonResult = createSelector(
  getScanSlaveResult,
  (result) => {
    if (!!result && !!result.controller_xml) {
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(result.controller_xml, "text/xml");
      const masterNode=xmlDoc.getElementsByTagName("EthercatController");
      if(masterNode && masterNode[0]) {
        return EthercatController.FromXML(masterNode[0]).toJSON()
      }
    }
    return null
  })

export const getSetEsiPathJSONResult = createSelector(
  getSetEsiPathResult,
  (result) => {
    if (!!result && !!result.device_list_xml) {
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(result.device_list_xml, "text/xml");
      const masterNode=xmlDoc.getElementsByTagName("ethercat_master");
      if(masterNode && masterNode[0]) {
        return EthercatMaster.FromXML(masterNode[0]).toJSON()
      }
    }
    return null
  })

