import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import Row from "antd/lib/row"
import Col from "antd/lib/col"
import Input from "antd/lib/input"
import Button from "antd/lib/button"
import Select from "antd/lib/select"
import { connect } from 'react-redux'
import Popover from 'antd/lib/popover'
import Table from 'antd/lib/table'
import InputNumber from 'antd/lib/input-number'

import { createSelector } from 'reselect'


import CellBase from "./CellBase";
import {
  getMemoizeEndPE, getMemoizeResultByCell, getResultByCell, getToolIds
} from "../../selectors";
import {deleteResultByChannel} from "../../actions";
const Option = Select.Option

const HINTS = {
  "UserInput":"()",
  "CalibT4P": "",
  "CalibT5P": "   Z  ",
  "CalibT6P": ""
}
const DETAIL_HINTS = {
  "CalibT4P": (<div style={{
    maxWidth: 300,
  }}>
    <p>1) ; </p>
    <p> 2)  4  ;</p>
    <p>3)</p>
  </div>),
  "CalibT5P":(<div style={{
    maxWidth: 300,
  }}>
    <p>1) ;</p>
    <p>2)  4  ; </p>
    <p>3) 4   Z  5 ; </p>
    <p>4)</p>
  </div>),
  "CalibT6P":(<div style={{
    maxWidth: 300,
  }}>
    <p>1) ; </p>
    <p>2)  4  ;</p>
    <p>3) 4   Z  5 ;</p>
  </div>),
  "UserInput":(<div style={{
    maxWidth: 300,
  }}>
    <p>  </p>
  </div>),
}
const CALIB_METHODS = {
  "UserInput": "",
  "CalibT4P": "",
  "CalibT5P": "",
  "CalibT6P": ""
}
const POINTS_COUNT = {
  "CalibT4P": 4,
  "CalibT5P": 5,
  "CalibT6P": 6
}

class ToolCalib extends CellBase {
  state = {
    calibMethod: 'CalibT4P',
    toolId: 0,
    toolName: '',
    pes: [],
    showCalibResult: false,
    showSaveResult: false,
    userInput: {
      x: 0.0,
      y: 0.0,
      z: 0.0,
      a: 0.0,
      b: 0.0,
      c: 0.0,
    }
  }
  handleNameChange = (e) => {
    this.setState({toolName: e.target.value, showCalibResult: false,  showSaveResult: false})
  }
  handleSelectToolId = (toolId) => {
    this.setState({toolId, pes: [], showCalibResult: false, toolName: null, showSaveResult: false})
  }
  handleSelectCalibMethod = (calibMethod) => {
    this.setState({calibMethod, pes: [], showCalibResult: false, toolName: null, showSaveResult: false})
  }

  renderTable = (prefixCls) => {
    const render = (text, record) => {
      if(record.last){
        return <span
          className={`${prefixCls}-table-cell ${prefixCls}-table-cell-last`}
        >{text}</span>
      }else{
        return <span
          className={`${prefixCls}-table-cell`}
        >{text}</span>
      }
    }
    const columns = [
      {
        title: '',
        dataIndex: 'rowName',
        key: 'rowName',
        align: 'center'
      },
      {
        title: 'X[mm]',
        dataIndex: 'x',
        key: 'xi',
        render,
        align: 'center'
      },
      {
        title: 'Y[mm]',
        dataIndex: 'y',
        key: 'yi',
        render,
        align: 'center'
      },
      {
        title: 'Z[mm]',
        dataIndex: 'z',
        key: 'zi',
        render,
        align: 'center'
      },
      {
        title: 'A',
        dataIndex: 'a',
        key: 'ai',
        render,
        align: 'center'
      },
      {
        title: 'B',
        dataIndex: 'b',
        key: 'bi',
        render,
        align: 'center'
      },
      {
        title: 'C',
        dataIndex: 'c',
        key: 'ci',
        render,
        align: 'center'
      },
    ];
    const {endPE, cell} = this.props
    let data = []
    this.state.pes.map((pe, index)=>{
      data.push({
        rowName: `${index+1}`,
        rowKey: index,
        key: index,
        xi: `x${index}`,
        yi: `y${index}`,
        zi: `z${index}`,
        ai: `a${index}`,
        bi: `b${index}`,
        ci: `c${index}`,
        x: pe[0].toFixed(3),
        y: pe[1].toFixed(3),
        z: pe[2].toFixed(3),
        a: pe[3].toFixed(3),
        b: pe[4].toFixed(3),
        c: pe[5].toFixed(3),
      })
    })
    if(endPE && this.state.pes.length<POINTS_COUNT[this.state.calibMethod]){
      data.push({
        rowName: `${data.length+1}`,
        rowKey: 'last',
        key: `last${data.length+1}`,
        xi: `xlast`,
        yi: `ylast`,
        zi: `zlast`,
        ai: `alast`,
        bi: `blast`,
        ci: `clast`,
        x: endPE[0].toFixed(3),
        y: endPE[1].toFixed(3),
        z: endPE[2].toFixed(3),
        a: endPE[3].toFixed(3),
        b: endPE[4].toFixed(3),
        c: endPE[5].toFixed(3),
        last: true,
      })
    }
    const self = this
    const Footer = <div className={`${prefixCls}-table-footer`}>
      {self.state.pes.length>0 && <Button
          type={'danger'}
          // size={'small'}
          onClick={()=>{
            self.setState({
              showCalibResult: false,
              showSaveResult: false,
              pes: []
            })
          }}
        ></Button>
      }
      {!!(endPE && self.state.pes.length<POINTS_COUNT[this.state.calibMethod]) &&
      <Button
        type={'primary'}
        // size={'small'}
        onClick={() => {
          const {endPE} = self.props
          self.setState({
            pes: [...self.state.pes, endPE]
          })
        }}
      ></Button>
      }
      {self.state.pes.length === POINTS_COUNT[this.state.calibMethod] &&
      <Button
        type={'primary'}
        // size={'small'}
        onClick={() => {
          let {pes, calibMethod} = self.state
          let {cell, deleteResultByChannel} = self.props
          deleteResultByChannel(cell.i)
          // pes = pes.map(pe=>pe.map(p=>p.toLocaleString('fullwide', {useGrouping:false})))
          self.setState({
            showCalibResult: true,
            showSaveResult: false
          })
          self.sendCmd(`${calibMethod} --pose={${pes.join(',').replace(/ /g, ',')}}`)
        }}
      ></Button>
      }
    </div>
    return <Table
      className={`${prefixCls}-table`}
      dataSource={data}
      columns={columns}
      size={'small'}
      locale={{
        'emptyText':'endPE...'
      }}
      bordered
      pagination={false}
      footer={()=>Footer}
    />;
  }

  getUserInputUpdateFn = (key) => {
    let self = this
    return (value)=>{
      let userInput = {...self.state.userInput}
      userInput[key] = value
      self.setState({userInput})
    }
  }

  getCellRenderer = (key) => {
    let self = this
    return (text, record) => {
      return <InputNumber
        step={0.001}
        size={"small"}
        value={record[key]}
        onChange={self.getUserInputUpdateFn(key)}
      />
    }
  }

  renderUserInput = (prefixCls) => {
    let {userInput} = this.state
    const columns = [
      {
        title: 'X[mm]',
        dataIndex: 'x',
        key: 'xi',
        align: 'center',
        render: this.getCellRenderer('x')
      },
      {
        title: 'Y[mm]',
        dataIndex: 'y',
        key: 'yi',
        align: 'center',
        render: this.getCellRenderer('y')
      },
      {
        title: 'Z[mm]',
        dataIndex: 'z',
        key: 'zi',
        align: 'center',
        render: this.getCellRenderer('z')
      },
      {
        title: 'A',
        dataIndex: 'a',
        key: 'ai',
        align: 'center',
        render: this.getCellRenderer('a')
      },
      {
        title: 'B',
        dataIndex: 'b',
        key: 'bi',
        align: 'center',
        render: this.getCellRenderer('b')
      },
      {
        title: 'C',
        dataIndex: 'c',
        key: 'ci',
        align: 'center',
        render: this.getCellRenderer('c')
      },
    ];
    let data = [{
      xi: `xlast`,
      yi: `ylast`,
      zi: `zlast`,
      ai: `alast`,
      bi: `blast`,
      ci: `clast`,
      rowKey: 'result',
      x: userInput.x,
      y: userInput.y,
      z: userInput.z,
      a: userInput.a,
      b: userInput.b,
      c: userInput.c,
      last: true,
    }]
    const self = this
    const Footer = <div className={`${prefixCls}-table-footer`}>
      <Button
        type={'primary'}
        // size={'small'}
        onClick={()=>{
          let {toolId, toolName, userInput} = self.state
          toolName = !!toolName?toolName:`Tool${toolId}`
          let {cell, deleteResultByChannel} = self.props
          self.setState({
            showSaveResult: true
          })
          deleteResultByChannel(cell.i)
          let toolPe = [userInput.x, userInput.y, userInput.z, userInput.a, userInput.b, userInput.c]
          self.sendCmd(
            `SetTF --tool_id=${toolId} --tool_name=${toolName} --tool_pe={${toolPe.join(',').replace(/ /g, ',')}}`,
            cell.i
          )
        }}
      ></Button>
    </div>
    return <div className={`${prefixCls}-calib-userinput`}>
      <Table
        className={`${prefixCls}-table`}
        dataSource={data}
        columns={columns}
        size={'small'}
        locale={{
          'emptyText':'...'
        }}
        bordered
        pagination={false}
        footer={()=>Footer}
      />
    </div>
  }

  renderResult = (prefixCls) => {
    let {showCalibResult} = this.state
    let {result, cell} = this.props
    // result = {
    //   return_code: 0,
    //   return_message: ' 4 !(TCP):0.000mm',
    //   tool_pe:[0,0,0,0,0,0]
    // }
    // showCalibResult=true
    if (!showCalibResult || !result) {
      return null
    }
    if (result.return_code !== 0 || !result.tool_pe) {
      return <div className={`${prefixCls}-calib-result error`}>
        <div></div>
        <div className={"error"}>:{result.return_code}</div>
        <div className={"error"}>:{result.return_message}</div>
      </div>
    }
    const columns = [
      {
        title: 'X[mm]',
        dataIndex: 'x',
        key: 'xi',
        align: 'center'
      },
      {
        title: 'Y[mm]',
        dataIndex: 'y',
        key: 'yi',
        align: 'center'
      },
      {
        title: 'Z[mm]',
        dataIndex: 'z',
        key: 'zi',
        align: 'center'
      },
      {
        title: 'A',
        dataIndex: 'a',
        key: 'ai',
        align: 'center'
      },
      {
        title: 'B',
        dataIndex: 'b',
        key: 'bi',
        align: 'center'
      },
      {
        title: 'C',
        dataIndex: 'c',
        key: 'ci',
        align: 'center'
      },
    ];
    let data = [{
      xi: `xlast`,
      yi: `ylast`,
      zi: `zlast`,
      ai: `alast`,
      bi: `blast`,
      ci: `clast`,
      rowKey: 'result',
      x: result.tool_pe[0].toFixed(3),
      y: result.tool_pe[1].toFixed(3),
      z: result.tool_pe[2].toFixed(3),
      a: result.tool_pe[3].toFixed(3),
      b: result.tool_pe[4].toFixed(3),
      c: result.tool_pe[5].toFixed(3),
      last: true,
    }]
    const self = this
    const Footer = <div className={`${prefixCls}-table-footer`}>
      <Button
        type={'primary'}
        // size={'small'}
        onClick={()=>{
          let {toolId, toolName} = self.state
          // let tool_pe = result.tool_pe.map(p=>p.toLocaleString('fullwide', {useGrouping:false}))
          let tool_pe = result.tool_pe.map(p=>p)
          toolName = !!toolName?toolName:`Tool${toolId}`
          let {cell, deleteResultByChannel} = self.props
          self.setState({
            showCalibResult: false,
            showSaveResult: true
          })
          deleteResultByChannel(cell.i)
          self.sendCmd(
            `SetTF --tool_id=${toolId} --tool_name=${toolName} --tool_pe={${tool_pe.join(',').replace(/ /g, ',')}}`,
            cell.i
          )
        }}
      ></Button>
    </div>
    return <div className={`${prefixCls}-calib-result`}>
      <div></div>
      <Table
      className={`${prefixCls}-table`}
      dataSource={data}
      columns={columns}
      size={'small'}
      locale={{
        'emptyText':'...'
      }}
      bordered
      pagination={false}
      footer={()=>Footer}
    />
    <div className={`${prefixCls}-calib-result-message`}>{result.CalibrationInfo}</div>
    </div>
  }

  renderSaveResult = (prefixCls) => {
    let {showSaveResult} = this.state
    let {result, cell} = this.props
    if (!showSaveResult || !result) {
      return null
    }
    if (result.return_code !== 0) {
      return <div className={`${prefixCls}-save-result error`}>
        <div></div>
        <div className={"error"}>:{result.return_code}</div>
        {!!result.CalibrationInfo &&
        <div className={"error"}>:{result.CalibrationInfo}</div>
        }
        {!!result.return_message &&
        <div className={"error"}>:{result.return_message}</div>
        }
      </div>
    }else{
      return <div className={`${prefixCls}-save-result success`}>
        <div></div>
        {!!result.CalibrationInfo &&
        <div className={`${prefixCls}-save-result-message success`}>{result.CalibrationInfo}</div>
        }
        {!!result.return_message &&
        <div className={"success"}>{result.return_message}</div>
        }
      </div>
    }
  }

  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      toolIds,
    } = this.props
    const prefixCls = getPrefixCls('calib-tool', customizePrefixCls);
    const self = this
    return <div
      className={`${prefixCls}`}
    >
      <Row>
        <Col lg={10} className={`${prefixCls}-user-input-container`}>
          <div>
            <span></span>
            <Select
              size={"small"}
              placeholder={""}
              onChange={this.handleSelectCalibMethod}
              value={this.state.calibMethod}
            >
              {Object.keys(CALIB_METHODS).map(name=> {
                return <Option value={name} key={name}>{CALIB_METHODS[name]}</Option>
              })}
            </Select>
            <Popover placement="bottom" title={<span>{CALIB_METHODS[this.state.calibMethod]}</span>} content={DETAIL_HINTS[this.state.calibMethod]} trigger="click">
              <Icon type="question" />
            </Popover>
          </div>
          <div>
            <span></span>
            <Select
              size={"small"}
              placeholder={""}
              onChange={this.handleSelectToolId}
              value={this.state.toolId}
            >
              {toolIds.map(id=> {
                return <Option value={id} key={id}>{id}</Option>
              })}
            </Select>
          </div>
          <div>
            <span></span>
            <Input
              value={this.state.toolName}
              size={"small"}
              placeholder={`Tool${this.state.toolId}`}
              onChange={this.handleNameChange}
            ></Input>
            { !!this.state.toolName && <Button
              size={"small"}
              type={"primary"}
              style={{marginLeft:5}}
              onClick={()=>{
                self.setState({
                  pes:[],
                  showCalibResult: false,
                })
                let {toolName, toolId} = self.state
                self.sendCmd(`RenameT --tool_id=${toolId} --tool_name=${toolName}`)
              }}
            ></Button>}
          </div>
        </Col>
        <Col lg={14} className={`${prefixCls}-hints-container`}>
          {HINTS[this.state.calibMethod]}
        </Col>
      </Row>
      {this.state.calibMethod === 'UserInput' &&
      <Row>
        {this.renderUserInput(prefixCls)}
      </Row>
      }
      {this.state.calibMethod !== 'UserInput' &&
      <Row>
        {this.renderTable(prefixCls)}
      </Row>
      }
      {this.state.calibMethod !== 'UserInput' &&
      <Row>
        {this.renderResult(prefixCls)}
      </Row>
      }
      <Row>
        {this.renderSaveResult(prefixCls)}
      </Row>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }
}

ToolCalib.propTypes = {
  endPE: PropTypes.array,
  result: PropTypes.object,
  toolIds: PropTypes.array,
}


const getToolCalibState = createSelector(
  getMemoizeEndPE,
  getResultByCell,
  getToolIds,
  (endPE, result, toolIds) => {
    return {
      endPE,
      result,
      toolIds,
    }
  }
)

function mapStateToProps(state, props) {
  return getToolCalibState(state, props)
}

const ConnectedToolCalib = connect(
  mapStateToProps,
  {
    deleteResultByChannel
  }
)(ToolCalib)

ConnectedToolCalib.NAME = ''
export default ConnectedToolCalib

