import XMLJSONBase from "./XMLJSONBase";
import EthercatSlave from "./EthercatSlave";
import EthercatMotion from "./EthercatMotion";

export class SlavePoolObject extends XMLJSONBase {
  constructor(name, slaves, motions){
    super()
    this.name = name
    this.slaves = slaves
    this.motions = motions
  }

  static FromXML = (node) => new SlavePoolObject(
    node.getAttribute("name"),
    [...node.getElementsByTagName("EthercatSlave")].filter(node=>node.nodeType===1).map(EthercatSlave.FromXML),
    [...node.getElementsByTagName("EthercatMotion")].filter(node=>node.nodeType===1).map(EthercatMotion.FromXML),
  )

  static FromJSON = ({name, slaves, motions}) => new SlavePoolObject(
    name,
    slaves && slaves.map(EthercatSlave.FromJSON),
    motions && motions.map(EthercatMotion.FromJSON)
  )

  toXML = () => `<SlavePoolObject ${SlavePoolObject.ToXMLAttrString("name", this.name)}>${this.slaves ? this.slaves.map(s=>s.toXML()).join(''):''}${this.motions ? this.motions.map(m=>m.toXML()).join(''):''}</SlavePoolObject>`

  toJSON = () => ({
    name: this.name,
    slaves: this.slaves && this.slaves.map(s=>s.toJSON()),
    motions: this.motions && this.motions.map(m=>m.toJSON()),
  })
}

export default SlavePoolObject