import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Row from "antd/lib/row"
import Input from "antd/lib/input"
import Button from "antd/lib/button"
import Select from "antd/lib/select"
import Upload from "antd/lib/upload"
import Icon from "antd/lib/icon"
import Modal from "antd/lib/modal"
import Form from 'antd/lib/form'


import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import CellBase from "./CellBase";
import {
  getCreateRobotModelModalVisible,
  getCreatingRobotModel,
  getCreatingRobotModelError,
  getCreatingRobotModelResult,
  getCurrentRobotBrand,
  getCurrentRobotModel,
  getResultByCell,
  getRobotModels
} from "../../selectors";
import {
  deleteResultByChannel,
  loadRobotModels,
  robotModelPartUploadSuccess,
  deleteRobotModelPart,
  deleteRobotModel,
  createRobotModel,
  showCreateRobotModelModal,
  hideCreateRobotModelModal,
  selectCurrentRobotBrand,
  selectCurrentRobotModel,
} from "../../actions";

const Option = Select.Option

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8 },
};
const FormItem = Form.Item

class CreateRobotForm extends Component {
  handleSubmit = e =>{
    e.preventDefault();
    const {createRobotModel} = this.props
    this.props.form.validateFields((err, values) => {
      if (!err) {
        createRobotModel(values)
      }
    });
  }

  render(){
    const { getFieldDecorator } = this.props.form;
    const {
      prefixCls,
      visible,
      creatingRobotModel,
      creatingRobotModelError,
      hideCreateRobotModelModal,
    } = this.props
    return <Modal
      visible={visible}
      title=""
      onOk={this.handleSubmit}
      okText={""}
      cancelText={""}
      onCancel={()=>{
        hideCreateRobotModelModal()
      }}
      centered={true}
    >
      {<Form onSubmit={this.handleSubmit} className="login-form">
        <FormItem
          {...formItemLayout}
          label={""}
        >
          {getFieldDecorator('brand', {
            rules: [{required: true, message: '!'}],
          })(
            <Input
              placeholder="Brand"
            />,
          )}
        </FormItem>
        <FormItem
          {...formItemLayout}
          label={""}
        >
          {getFieldDecorator('model', {
            rules: [{required: true, message: '!'}],
          })(
            <Input
              placeholder="Model"
            />,
          )}
        </FormItem>
      </Form>
      }
      {!!creatingRobotModel && <span className={"info"}>
        ...
      </span>}
      {!!creatingRobotModelError && <span className={"error"}>
        {creatingRobotModelError}
      </span>}
    </Modal>
  }
}

const CreateRobotModel = Form.create()(CreateRobotForm)

class ModelConfiguration extends CellBase {
  name = ''
  state = {
    showCalibResult: false,
    showSaveResult: false,
    brand: null,
    model: null,
    showCreateModal: false,
  }

  componentWillMount() {
    this.props.loadRobotModels()
  }

  renderContent = ({getPrefixCls}) => {
    const self = this
    let {
      prefixCls: customizePrefixCls,
      cell,
      deleteResultByChannel,
      robotModels,
      robotModelPartUploadSuccess,
      deleteRobotModelPart,
      deleteRobotModel,
      creatingRobotModel,
      creatingRobotModelError,
      creatingRobotModelResult,
      showCreateRobotModelModal,
      modalVisible,
      createRobotModel,
      selectCurrentRobotBrand,
      selectCurrentRobotModel,
      hideCreateRobotModelModal,
      model,
      brand,
    } = self.props
    const {motionPos} = self.state
    const prefixCls = getPrefixCls('model-configuration', customizePrefixCls);
    if(!robotModels){
      return <div
        className={`${prefixCls} loading`}
      >
        ...
      </div>
    }
    if(!brand || !robotModels[brand]){
      brand = Object.keys(robotModels)[0]
    }
    if(brand){
      if(!model || !robotModels[brand][model]){
        model = Object.keys(robotModels[brand])[0]
      }
    }
    let missingParts = []
    if(brand && model){
      missingParts = Object.keys(robotModels[brand][model].parts).filter(key=>!robotModels[brand][model].parts[key])
    }
    return <div className={prefixCls}>
      {!!robotModels && model && brand && <Row className={`${prefixCls}-brand`} style={{marginBottom: 20}}>
        <span></span>
        <Select
          size={"small"}
          style={{width: 200}}
          placeholder={""}
          onChange={(brand)=>{
            this.setState({saving: false})
            deleteResultByChannel(cell.i)
            selectCurrentRobotBrand(brand)
          }}
          value={brand}
        >
          {Object.keys(robotModels).map(brand => <Option value={brand} key={brand}>{brand}</Option>)}
        </Select>
      </Row>
      }
      {!!robotModels && model && brand && model && !!robotModels[brand] && <Row className={`${prefixCls}-model`} style={{marginBottom: 20}}>
        <span></span>
        <Select
          size={"small"}
          style={{width: 200}}
          placeholder={""}
          onChange={(model)=>{
            this.setState({saving: false})
            deleteResultByChannel(cell.i)
            selectCurrentRobotModel(model)
          }}
          value={model}
        >
          {Object.keys(robotModels[brand]).map(model => <Option value={model} key={model}>{model}</Option>)}
        </Select>
        <Button
          style={{marginLeft: 10, marginBottom: 20}}
          size={"small"}
          onClick={()=>{
            this.setState({saving: false})
            deleteResultByChannel(cell.i)
            deleteRobotModel(brand, model)
          }}
        ></Button>
      </Row>
      }
      <Row className={`${prefixCls}-model`}>
        <Button
          size={"small"}
          onClick={()=>{
            this.setState({saving: false})
            deleteResultByChannel(cell.i)
            showCreateRobotModelModal()
          }}
        ></Button>
        <CreateRobotModel
          visible={modalVisible}
          hideCreateRobotModelModal={hideCreateRobotModelModal}
          centered={true}
          createRobotModel={createRobotModel}
          creatingRobotModel={creatingRobotModel}
          creatingRobotModelError={creatingRobotModelError}
        >
        </CreateRobotModel>
      </Row>
      <Row className={`${prefixCls}-parts`}>
        {!!robotModels && !!robotModels[brand] && !!robotModels[brand][model] && !!robotModels[brand][model].parts && Object.keys(robotModels[brand][model].parts).map(key=>{
          let fileList = []
          if(!!robotModels[brand][model].parts[key]){
            fileList.push({
              uid: '-1',
              name: robotModels[brand][model].parts[key],
              status: 'done',
              url: '',
            })
          }
          return <Row className={`${prefixCls}-part ${this.state.saving && !robotModels[brand][model].parts[key] && 'error'}`} key={key}>
            <span>PART {key}:</span>
            {!!robotModels[brand][model].parts[key] && <span className={`${prefixCls}-part-file`}>
            <Icon type="file-markdown" /> {robotModels[brand][model].parts[key]}
              <Icon type="close" className={`${prefixCls}-delete`} onClick={()=>{
                deleteRobotModelPart(brand, model, key)
              }}/>
          </span>}
            {!robotModels[brand][model].parts[key] && <Upload
              className={`${prefixCls}-part-upload`}
              multiple={false}
              action={`/api/robots/${brand}/${model}/parts/${key}`}
              showUploadList={false}
              onChange={({file, fileList, event})=>{
                if(file.status === 'done'){
                  robotModelPartUploadSuccess(file.response)
                }
              }}
            >
              <Button>
                <Icon type="upload" />
              </Button>
            </Upload>}
          </Row>
        })
        }
      </Row>
      <Row className={`${prefixCls}-save`}>
        <Button size={"large"} type={"primary"} onClick={()=>{
          this.setState({saving: true})
          deleteResultByChannel(cell.i)
          if(missingParts.length === 0){
            const parts = robotModels[brand][model].parts
            const path = robotModels[brand][model].path
            this.sendCmd(`cs_stop`)
            const cmd = `setppath --file_path=`+Object.keys(parts).map(key=>{
              return `${path}/${parts[key]}`
            }).join(';')
            this.sendCmd(cmd)
            this.sendCmd(`savexml`)
          }
        }}></Button>
        {this.state.saving && missingParts.length > 0 && <p className={'error'}>
            {missingParts.join(',')}
        </p> }
      </Row>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }
}

ModelConfiguration.propTypes = {
  result: PropTypes.object,
}

const getModelConfigerationState = createSelector(
  getResultByCell,
  getRobotModels,
  getCreatingRobotModel,
  getCreatingRobotModelError,
  getCreatingRobotModelResult,
  getCreateRobotModelModalVisible,
  getCurrentRobotBrand,
  getCurrentRobotModel,
  (
    result,
    robotModels,
    creatingRobotModel,
    creatingRobotModelError,
    creatingRobotModelResult,
    modalVisible,
    brand,
    model
    ) => {
    return {
      robotModels,
      result,
      creatingRobotModel,
      creatingRobotModelError,
      creatingRobotModelResult,
      modalVisible,
      brand,
      model
    }
  }
)

function mapStateToProps(state, props) {
  return getModelConfigerationState(state, props)
}

const ConnectedModelConfiguration = connect(
  mapStateToProps,
  {
    deleteResultByChannel,
    loadRobotModels,
    robotModelPartUploadSuccess,
    deleteRobotModelPart,
    deleteRobotModel,
    createRobotModel,
    showCreateRobotModelModal,
    hideCreateRobotModelModal,
    selectCurrentRobotBrand,
    selectCurrentRobotModel,
  }
)(ModelConfiguration)

ConnectedModelConfiguration.NAME = ''

export default ConnectedModelConfiguration
