import * as actions from "../actions";
import { delay, take, put, call, fork, select, all, race } from 'redux-saga/effects'
import { eventChannel, END} from 'redux-saga'
import {getModel} from "../reducers/selectors";

let CMD_CODE_LIST = {}
let CMD_CODE_RETURN_LIST = {}
let dataMap_dataIndex=[]
let dataMap_chartID=[]
let dataSet=[]
let dataSetCount=[]
let dataSetLength=[]

function sendCmd(ws, str) {
  let strArray=str.split("&");
  if (strArray.length>3){
    let cmd_id=parseInt(strArray[0]);
    let cmd_option=parseInt((strArray[1]));
    let cmd_code=parseInt((strArray[2]));

    // console.log("res-1:"+cmd_code);
    let res_2=parseInt((strArray[3]));
    let res_3=parseInt((strArray[4]));

    let mes_length=strArray[5].length;
    let buffer=new ArrayBuffer(40+mes_length);
    let headView=new DataView(buffer);
    headView.setInt32(0,mes_length,true);
    headView.setInt32(4,cmd_id,true);
    headView.setInt32(8,cmd_option,true);
    headView.setFloat64(16,cmd_code,true);
    // console.log("cmd_code:"+headView.getFloat64(16,true));
    headView.setInt32(24,res_2,true);
    headView.setInt32(32,res_3,true);
    for (let i=0;i<mes_length;i++){
      headView.setUint8(40+i,strArray[5].charCodeAt(i));
    }
    ws.send(headView.buffer);
  }
}


//
function utf8ArrayToStr(array) {
  let out, i, len, c
  let char2, char3

  out = ""
  len = array.length
  i = 0
  while(i < len) {
    c = array[i++]
    switch(c >> 4)
    {
      case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
      // 0xxxxxxx
      out += String.fromCharCode(c)
      break
      case 12: case 13:
      // 110x xxxx   10xx xxxx
      char2 = array[i++]
      out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F))
      break
      case 14:
        // 1110 xxxx  10xx xxxx  10xx xxxx
        char2 = array[i++]
        char3 = array[i++]
        out += String.fromCharCode(((c & 0x0F) << 12) |
          ((char2 & 0x3F) << 6) |
          ((char3 & 0x3F) << 0))
        break;
    }
  }

  return out
}

function analyzeBinaryDataReturn(buffer) {
  let dataView=new DataView(buffer)
  let pq=""
  for(let i=0;i<(buffer.byteLength-40)/8;i++){
    let data=dataView.getFloat64(40+i*8,true).toString();
    if (i!==0){
      pq+=","+data;
    }
    if (i===0){
      pq+=data;
    }
  }
  return pq;
}

function createWebSocketConnection(url) {
  return new Promise((resolve, reject) => {
    const socket = new WebSocket(url || window.location.origin.replace('https','wss').replace('http', 'ws'))
    socket.onopen = function () {
      socket.binaryType='arraybuffer'
      resolve(socket);
    };
    socket.onerror = function (evt) {
      reject(evt);
    }
  });
}

function createSocketChannel(socket) {
  return eventChannel(emit => {
    //racecancel, 
    socket.onmessage = (event) => {
      if(event.data instanceof ArrayBuffer){
        emit({data:event.data, type: actions.CMD_RESPONSE})
      }
    };
    socket.onclose = () => {
      emit(actions.wsDisconnected())
    };
    socket.onerror = (error) => {
      emit(actions.wsError(error.message))
    }
    const unsubscribe = () => {
      socket.onmessage = null
      socket.onerror = null
    };
    return unsubscribe
  })
}

function* internalListener(ws) {
  while (true) {
    const {cmd, id} = yield take(actions.SEND_CMD);
    switch (ws.readyState) {
      case WebSocket.CONNECTING:
        break;
      case WebSocket.OPEN:
        if(cmd === 'get'){
          sendCmd(ws, "0&1&" + id + "&0&0&" + cmd);
        }else{
          // console.error(cmd)
          sendCmd(ws, "1&1&" + id + "&0&0&" + cmd);
        }
      case  WebSocket.CLOSING:
        break;
      case WebSocket.CLOSED:
        break;
    }
  }
}

function* externalListener(channel){
  try{
    while (true) {
      const action = yield take(channel)
      yield put(action)
    }
  }finally{
    console.log('message channel closed')
  }
}
function* reconnect(url) {
  yield delay(5000)
  yield put(actions.startWebsocket(url))
}

function* websocketSaga() {
  let socket
  let socketChannel
  let cancel
  while(true){
    const {url} = yield take(actions.START_WEBSOCKET);
    try {
      socket = yield call(createWebSocketConnection, url);
      socketChannel = yield call(createSocketChannel, socket);
      yield  put(actions.wsConnected())
      const {cancel:_cancel}= yield race({
        internal: call(internalListener, socket),
        external: call(externalListener, socketChannel),
        cancel: take(actions.STOP_WEBSOCKET)
      });
      cancel = _cancel
    } catch (error) {
      console.log(error)
      yield put(actions.wsError(error.message))
    } finally {
      if(socket){
        socket.close();
        socket = null
      }
      yield put(actions.wsDisconnected())
      if(socketChannel){
        socketChannel.close();
        socketChannel = null
      }
      if (!cancel) {
        yield fork(reconnect, url)
      }
    }
  }
}


export default [
  fork(websocketSaga)
]