import Blockly from "blockly"

Blockly.Blocks['robotcontrol_movel'] = {
  updateEndPQ: function(endPQ){
    [0,1,2,3,4,5,6].forEach(i=>this.setFieldValue(endPQ[i].toFixed(6),`END_PQ${i}`))
    if(this.workspace){
      this.workspace.render()
    }
  },
  isEndPQEmpty: function(){
    return [0,1,2,3,4,5,6].filter(i=>this.getFieldValue(`END_PQ${i}`) === Infinity).length === 7
  },
  init: function(){
    let endPQ = [Infinity,Infinity,Infinity,Infinity,Infinity,Infinity,Infinity]
    if(this.workspace && this.workspace.endPQ){
      endPQ = this.workspace.endPQ.map(e=>e.toFixed(6))
    }
    this.appendValueInput("END_PQ_INPUT")
      .setCheck(null)
      .appendField("MoveL --pq=")
      .appendField(new Blockly.FieldNumber(endPQ[0]), "END_PQ0")
      .appendField(new Blockly.FieldNumber(endPQ[1]), "END_PQ1")
      .appendField(new Blockly.FieldNumber(endPQ[2]), "END_PQ2")
      .appendField(new Blockly.FieldNumber(endPQ[3]), "END_PQ3")
      .appendField(new Blockly.FieldNumber(endPQ[4]), "END_PQ4")
      .appendField(new Blockly.FieldNumber(endPQ[5]), "END_PQ5")
      .appendField(new Blockly.FieldNumber(endPQ[6]), "END_PQ6")
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(230);
    this.setTooltip("Move L");
    this.setHelpUrl("");
  }
};

