// api services
export const fetchInterfaceConfig = () => callGetApi(`/api/config/interface`)
export const updateDashboard = (dashboard) => {
  dashboard = {...dashboard}
  if(dashboard.cells){
    dashboard.cells = dashboard.cells.map(c=>{
      c = {...c}
      if(c.options){
        c.options = JSON.stringify(c.options)
      }
      return c
    })
  }
  return callPutApi(`/api/dashboards/${dashboard.i}`, dashboard)
}
export const createCell = (dashboardId, cell) => {
  if(cell.options && typeof cell.options !== 'string'){
    cell = {...cell}
    cell.options = JSON.stringify(cell.options)
  }
  return callPostApi(`/api/dashboards/${dashboardId}/cells`, {...cell})
}
export const deleteCell = (dashboardId, cellId) => callDeleteApi(`/api/dashboards/${dashboardId}/cells/${cellId}`, {})

export const fetchRobotModels = () => callGetApi(`/api/robots`)
export const deleteRobotModelPart = (brand, model, part) => callDeleteApi(`/api/robots/${brand}/${model}/parts/${part}`)

export const deleteRobotModel = (brand, model) => callDeleteApi(`/api/robots/${brand}/${model}`)

export const createRobotModel = (model) => callPostApi(`/api/robots`, model)

export const fetchESIPath = () => callGetApi(`/api/esi/path`)

export const fetchConfigXml = () => callRawGetApi(`/api/config/robot/xml`)


export const fetchPrograms = () => callGetApi(`/api/programs`)
export const fetchProgram = (name) => callGetApi(`/api/programs/${encodeURIComponent(name)}`)
export const createProgram = (name) => callPostApi(`/api/programs`, {name})
export const updateProgram = (name, program) => callPutApi(`/api/programs/${encodeURIComponent(name)}`, program)
export const deleteProgram = (name) => callDeleteApi(`/api/programs/${encodeURIComponent(name)}`)
export const renameProgram = (name, newName, copy) => callPatchApi(!!copy?`/api/programs/${encodeURIComponent(name)}?copy=true`:`/api/programs/${encodeURIComponent(name)}`, {name:newName})
