import React from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Row from "antd/lib/row"
import Button from "antd/lib/button"
import { createSelector } from 'reselect'
import { connect } from 'react-redux'
import {
  sendCmd
} from '../../../actions'
import CellBase from "../CellBase";
import {
  getMemoizeEndPE,
  getCurrentMotionVelPercent,
  getCurrentCoordinateId, getCurrentToolId, getCurrentWorkObjectId
} from "../../../selectors";


class JogCoordinate extends CellBase {
  renderJogCoordinate = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      endPE,
      motionVelPercent,
      coordinateId,
      toolId,
      workObjectId,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('jog-coordinate', customizePrefixCls);
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
    };
    const names = ['X', 'Y', 'Z', 'RX', 'RY', 'RZ']
    const types = ['x', 'y', 'z', 'rx', 'ry', 'rz']
    return <div
      className={`${prefixCls}`}
    >
      {
        names.map((name, index)=>(<Row key={name}>
          <Button
            type="primary"
            size={"large"}
            onClick={()=>{
              this.getSendCmdFn(
                `j${types[index]} --vel_percent=${motionVelPercent} --direction=-1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                cell.i
              )()
            }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${types[index]} --vel_percent=${motionVelPercent} --direction=-1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
            }}
            onMouseOut={()=>{
              this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
            }}
          >{name}-</Button>
          <Row className={`${prefixCls}-end_pe`}>
          <div>
          {/* <span>{name}</span> */}
          {/* {console.log(index)} */}
          {!!(name == 'X'||'Y'||'Z')?<span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]*1000).toFixed(3):'null'}</span>:<span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]/Math.PI*180).toFixed(3):'null'}</span>}
        </div>
        </Row>
            <Button
              type="primary"
              size={"large"}
              onClick={()=>{
                this.getSendCmdFn(
                  `j${types[index]} --vel_percent=${motionVelPercent} --direction=1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                  cell.i
                )()
              }}
              onMouseDown={()=>{
                this.getOnMouseDownFn(
                  `j${types[index]} --vel_percent=${motionVelPercent} --direction=1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                  cell.i
                )()
              }}
              onMouseUp={()=>{
                this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
              }}
              onMouseOut={()=>{
                this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
              }}
            >{name}+</Button>
          </Row>)
        )
      }
      {/* <Row className={`${prefixCls}-end_pe`}>
        <div>
          <span>X</span>
          <span>{!!endPE && (!!endPE[0] || endPE[0]===0)?(endPE[0]*1000).toFixed(3):'null'}</span>
        </div>
        <div>
          <span>Y</span>
          <span>{!!endPE && (!!endPE[1] || endPE[1]===0)?(endPE[1]*1000).toFixed(3):'null'}</span>
        </div>
        <div>
          <span>Z</span>
          <span>{!!endPE && (!!endPE[2] || endPE[2]===0)?(endPE[2]*1000).toFixed(3):'null'}</span>
        </div>
      </Row>
      <Row className={`${prefixCls}-end_pe`}>
        <div>
          <span>RX</span>
          <span>{!!endPE && (!!endPE[3] || endPE[3]===0)?(endPE[3]/Math.PI*180).toFixed(3):'null'}</span>
        </div>
        <div>
          <span>RY</span>
          <span>{!!endPE && (!!endPE[4] || endPE[4]===0)?(endPE[4]/Math.PI*180).toFixed(3):'null'}</span>
        </div>
        <div>
          <span>RZ</span>
          <span>{!!endPE && (!!endPE[5] || endPE[5]===0)?(endPE[5]/Math.PI*180).toFixed(3):'null'}</span>
        </div>
      </Row> */}
      
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogCoordinate}</ConfigConsumer>
  }
}

JogCoordinate.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  motionVelPercent: PropTypes.number,
  coordinateId: PropTypes.number.isRequired,
  toolId: PropTypes.number.isRequired,
  workObjectId: PropTypes.number.isRequired,
  endPE: PropTypes.array,
}


export const getJogCoordinateState = createSelector(
  getCurrentMotionVelPercent,
  getCurrentCoordinateId,
  getCurrentToolId,
  getCurrentWorkObjectId,
  getMemoizeEndPE,
  (motionVelPercent, coordinateId, toolId, workObjectId, endPE) => {
    return {
      coordinateId,
      motionVelPercent,
      toolId,
      workObjectId,
      endPE
    }
  }
)


function mapStateToProps(state) {
  return getJogCoordinateState(state)
}

const ConnectedJogCoordinate = connect(
  mapStateToProps,
  {
    sendCmd,
  },
)(JogCoordinate)

ConnectedJogCoordinate.NAME = 'Jog'


export default  ConnectedJogCoordinate
