import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import { connect } from 'react-redux'
import Switch from "antd/lib/switch"
import brace from 'brace';
import AceEditor from 'react-ace';

import 'brace/mode/java';
import 'brace/theme/github';
import 'brace/theme/monokai';

class CodeEditor extends Component {
  onChange = (newValue) => {
    console.log('change',newValue);
  }
  renderJogJoint = ({getPrefixCls}) => {

    let value = `
function onLoad(editor) {
  console.log("i've loaded");
}
function onLoad(editor) {
  console.log("i've loaded");
}
function onLoad(editor) {
  console.log("i've loaded");
}
function onLoad(editor) {
  console.log("i've loaded");
}
function onLoad(editor) {
  console.log("i've loaded");
}
function onLoad(editor) {
  console.log("i've loaded");
}
    `

    const {
      prefixCls: customizePrefixCls,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('code-editor', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
    >
      <AceEditor
        mode="javascript"
        theme="monokai"
        width={"100%"}
        height={"100%"}
        value={value}
        onChange={this.onChange}
        name="UNIQUE_ID_OF_DIV"
        editorProps={{$blockScrolling: true}}
      />
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogJoint}</ConfigConsumer>
  }
}

CodeEditor.propTypes = {
  cell: PropTypes.object.isRequired,
}

function mapStateToProps(state) {
  return {
    robotStatus: state.robot.robotStatus,
  }
}

const ConnectedCodeEditor = connect(
  mapStateToProps,
)(CodeEditor)

ConnectedCodeEditor.NAME = ''

export  default ConnectedCodeEditor

