import React, {Fragment} from "react"
import CellBase from "../CellBase"
import SideBar from "./SideBar"
import HeaderBar from "./HeaderBar"
import isEqual from "lodash/isEqual"
import isEmpty from "lodash/isEmpty"
import isArray from "lodash/isArray"
import { connect } from 'react-redux'

import { createSelector } from 'reselect'
import {
  deleteResultByChannel,
  loadEsiPath,
} from "../../../actions";
import {
  getPdoListForDeviceJSONResult,
  getPdoListForSlaveJSONResult,
  getScanSlaveJsonResult,
  getSetEsiPathJSONResult,
  getSaveResult
} from "./selectors";

import Row from 'antd/lib/row'
import Form from 'antd/lib/form'
import Checkbox from 'antd/lib/checkbox'
import Input from 'antd/lib/input'
import InputNumber from 'antd/lib/input-number'
import Col from 'antd/lib/col'
import Button from 'antd/lib/button'
import EthercatController from "../../../ethercat/EthercatController";
import ResultError from "../../../components/ResultError";
const FormItem = Form.Item

const slaveFormItemLayout = {
  labelCol: {
    span: 10
  },
  wrapperCol: {
    span: 14
  },
};

const hexValidator = (rule, value, callback)=>{
  if(typeof value === 'string' && !!value.match(/^(?:0[xX])?[0-9a-fA-F]+$/)){
    callback()
  }else{
    callback('not hex')
  }
}

const transform = obj => {
  return Object.keys(obj).reduce((acc, cv) => {
    return {
      ...acc,
      [cv]:
        typeof obj[cv] === "object" && !("value" in obj[cv])
          ? isArray(obj[cv])
          ? obj[cv].map(item => transform(item))
          : transform(obj[cv])
          : Form.createFormField({
            ...obj[cv],
            value: obj[cv] && obj[cv].value
          })
    };
  }, {});
};


class EthercatConfiguration extends CellBase {
  name = 'Ethercat'
  width = 24
  height = 24
  state = {
    selectedVendor: null,
    selectedDevice: null,
    selectedSlave: null
  }

  getEsiPdoListForDevice(vendorId, productCode, revisionNum) {
    const {deleteResultByChannel, sendCmd, cell} = this.props
    deleteResultByChannel(`${cell.i}-pdolist`)
    sendCmd(`getesipdolist --vendor_id=${vendorId} --product_code=${productCode} --revision_num=${revisionNum}`, `${cell.i}-pdolist-for-device`)
  }

  getEsiPdoListForSlave(vendorId, productCode, revisionNum) {
    const {deleteResultByChannel, sendCmd, cell} = this.props
    deleteResultByChannel(`${cell.i}-pdolist`)
    sendCmd(`getesipdolist --vendor_id=${vendorId} --product_code=${productCode} --revision_num=${revisionNum}`, `${cell.i}-pdolist-for-slave`)
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    if(!isEqual(nextProps.ethercatMaster, this.props.ethercatMaster)){
      if(nextProps.ethercatMaster && nextProps.ethercatMaster.vendors){
        this.setState({vendors: nextProps.ethercatMaster.vendors, saveError: null})
      }
    }
    if(!isEqual(nextProps.ethercatController, this.props.ethercatController)){
      if(nextProps.ethercatController && nextProps.ethercatController.slavePoolObjects && nextProps.ethercatController.slavePoolObjects[0]){
        const slaves = {}
        nextProps.ethercatController.slavePoolObjects[0].motions && nextProps.ethercatController.slavePoolObjects[0].motions.forEach(m=>{
          slaves[m.uuid] = m
        })
        nextProps.ethercatController.slavePoolObjects[0].slaves && nextProps.ethercatController.slavePoolObjects[0].slaves.forEach(m=>{
          slaves[m.uuid] = m
        })
        this.setState({slaves, saveError: null})
      }
      return false
    }
    let {selectedSlave, slaves} = this.state
    if(!isEqual(nextProps.pdoListForDevice,  this.props.pdoListForDevice) && !isEmpty(nextProps.pdoListForDevice) && selectedSlave && slaves && slaves[selectedSlave]){
      slaves = {...slaves}
      slaves[selectedSlave] = {...nextProps.pdoListForDevice, phyId: slaves[this.state.selectedSlave].phyId, uuid: selectedSlave}
      this.setState({slaves, saveError: null})
      return false
    }
    return true
  }

  addSlave = () => {
    let slaves = {...this.state.slaves}
    if(isEmpty(slaves)){
      return
    }
    let newPhyId = Math.max(...Object.keys(slaves).map(uuid=>slaves[uuid].phyId)) + 1
    let newSlave = {...slaves[0], phyId: newPhyId, uuid: Math.random().toString(36), }
    delete newSlave['syncManagerPoolObjects']
    slaves[newSlave.uuid] = newSlave
    this.setState({slaves, saveError: null})
  }

  selectSlave = (uuid) => {
    const {slaves} = this.state
    if(slaves[uuid]){
      let {vendorId, productCode, revisionNum} = slaves[uuid]
      if(vendorId && productCode && revisionNum){
        this.getEsiPdoListForDevice(vendorId, productCode, revisionNum)
      }
      this.setState({selectedSlave: uuid, saveError: null})
    }else{
      this.setState({selectedSlave: null, saveError: null})
    }
  }

  selectVendor = (vendorId) => {
    const {vendors} = this.state
    this.setState({
      selectedVendor: vendors.filter(v=>v.id===vendorId)[0],
      selectedDevice: null,
      saveError: null
    })
  }

  selectDevice = (deviceName) => {
    const {selectedVendor} = this.state
    if(selectedVendor){
      const selectedDevice = selectedVendor.devices.filter(d=>d.name===deviceName)[0]
      this.setState({
        selectedDevice,
        saveError: null
      })
      if(selectedDevice){
        const vendorId = selectedVendor.id
        const productCode = selectedDevice.productCode
        const revisionNum = selectedDevice.revisionNo
        if(vendorId && productCode && revisionNum){
          this.getEsiPdoListForDevice(vendorId, productCode, revisionNum)
        }
      }
    }
  }

  removeSlave = (uuid) => {
    let slaves = {...this.state.slaves}
    if(isEmpty(slaves)){
      return
    }
    delete slaves[uuid]
    this.setState({slaves, saveError: null})
  }

  addPdo = (syncManagerIndex) => {
    let slaves = {...this.state.slaves}
    let selectedSave = this.state.selectedSlave
    let slave = {...slaves[selectedSave]}
    slave.syncManagerPoolObjects = [...(slave.syncManagerPoolObjects || [])]
    slave.syncManagerPoolObjects[0].syncManagers = [...(slave.syncManagerPoolObjects[0].syncManagers || [])]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] = {...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] || {})}
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes = [...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes || []), {}]
    slaves[selectedSave] = slave
    slave.rnd = Math.random()
    this.setState({slaves, saveError: null})
  }

  removePdo = (syncManagerIndex,pdoIndex ) => {
    let slaves = {...this.state.slaves}
    let selectedSave = this.state.selectedSlave
    let slave = {...slaves[selectedSave]}
    slave.syncManagerPoolObjects = [...(slave.syncManagerPoolObjects || [])]
    slave.syncManagerPoolObjects[0].syncManagers = [...(slave.syncManagerPoolObjects[0].syncManagers || [])]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] = {...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] || {})}
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes = [...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes || [])]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes.splice(pdoIndex, 1)
    slaves[selectedSave] = slave
    slave.rnd = Math.random()
    this.setState({slaves, saveError: null})
  }

  addPdoEntry = (syncManagerIndex, pdoIndex) => {
    let slaves = {...this.state.slaves}
    let selectedSave = this.state.selectedSlave
    let slave = {...slaves[selectedSave]}
    slave.syncManagerPoolObjects = [...(slave.syncManagerPoolObjects || [])]
    slave.syncManagerPoolObjects[0].syncManagers = [...(slave.syncManagerPoolObjects[0].syncManagers || {})]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] = {...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] || {})}
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes = [...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes || [])]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex] = {
      ...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex] || {})
    }
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex].entries = [
      ...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex].entries || []), {}
    ]
    slaves[selectedSave] = slave
    slave.rnd = Math.random()
    this.setState({slaves, saveError: null})
  }

  removePdoEntry = (syncManagerIndex, pdoIndex, pdoEntryIndex) => {
    let slaves = {...this.state.slaves}
    let selectedSave = this.state.selectedSlave
    let slave = {...slaves[selectedSave]}
    slave.syncManagerPoolObjects = [...(slave.syncManagerPoolObjects || [])]
    slave.syncManagerPoolObjects[0].syncManagers = [...(slave.syncManagerPoolObjects[0].syncManagers || {})]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] = {...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex] || {})}
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes = [...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes || [])]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex] = {
      ...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex] || {})
    }
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex].entries = [
      ...(slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex].entries || [])
    ]
    slave.syncManagerPoolObjects[0].syncManagers[syncManagerIndex].pdoes[pdoIndex].entries.splice(pdoEntryIndex, 1)
    slaves[selectedSave] = slave
    slave.rnd = Math.random()
    this.setState({slaves, saveError: null})
  }

  componentWillMount() {
    this.props.loadEsiPath()
  }

  renderFormItemInput(prefixCls, key, label, initialValue, rules, span, required){
    const { getFieldDecorator} = this.props.form;
    return <Col span={span || 7} offset={1} pull={1} key={key}>
      <FormItem {...slaveFormItemLayout} label={label} hasFeedback>
        {getFieldDecorator(`slave.${key}`, {
          initialValue: initialValue,
          rules: [
            {
              required: !!required,
              message: `Please input ${label}`,
            },
          ],
        })(<Input placeholder={`${label}`} size={'small'}/>)}
      </FormItem>
    </Col>
  }

  renderFormItemNumberInput(prefixCls, key, label, initialValue, span, required, step, postfix, min, max){
    const { getFieldDecorator} = this.props.form;
    return <Col span={span || 7} offset={1} pull={1} key={key}>
      <FormItem {...slaveFormItemLayout} label={label} hasFeedback>
        {getFieldDecorator(`slave.${key}`, {
          initialValue: initialValue,
          rules: [
            {
              required: !!required,
              message: `Please input ${label}`,
            },
          ],
        })(<InputNumber
          size={'small'}
          step={step || 0.01}
          min={min || -360}
          max={max || 360}
          placeholder={`${label}`}
        />)}
        {!!postfix && <span className={`${prefixCls}-form-postfix`}>{postfix}</span>}
      </FormItem>
    </Col>
  }

  renderSlaveInputs = (prefixCls, slave) => {
    return <Row>
      {[
        ["name", "name", slave.name, []],
        ["vendorId", "vendor id", slave.vendorId, [{validator:hexValidator, message:'Please input hex'}]],
        ["productCode", "productCode", slave.productCode, [{validator:hexValidator, message:'Please input hex'}]],
        ["phyId", "physical id", slave.phyId, [{type:'integer', message:'Please input integer'}]],
        ["revisionNum", "revision No.", slave.revisionNum, [{validator:hexValidator, message:'Please input hex'}]],
        ["dcAssignActivate", "dc assign activate", slave.dcAssignActivate, [{validator:hexValidator, message: 'Please input hex'}]],
      ].map(d=>this.renderFormItemInput(prefixCls, ...d, 7, true))}
    </Row>
  }

  renderMotionInputs = (prefixCls, slave) => {
    return <Fragment>
      {this.renderSlaveInputs(prefixCls, slave)}
      <Row>
        {[
          ['maxPos', 'max pos', slave.maxPos, 7, true, 0.01, '', -360, 360],
          ['maxVel', 'max vel', slave.maxVel, 7, true, 0.01, '\/s', -360, 360],
          ['maxAcc', 'max acc', slave.maxAcc, 7, true, 0.01, '\/s2', -360, 360],
          ['minPos', 'min pos', slave.minPos, 7, true, 0.01, '', -360, 360],
          ['minVel', 'min vel', slave.minVel, 7, true, 0.01, '\/s', -360, 360],
          ['minAcc', 'min acc', slave.minAcc, 7, true, 0.01, '\/s2', -360, 360],
          ['posFactor', 'pos factor', slave.posFactor, 7, true, 0.01, '', -Number.MAX_VALUE, Number.MAX_VALUE],
          ['posOffset', 'pos offset', slave.posOffset, 7, true, 0.01, '', -360, 360],
          ['homePos', 'home pos', slave.homePos, 7, true, 0.01, '', -360, 360],
          ['maxPosFollowingError', 'max pos following error', slave.maxPosFollowingError, 11, true, 0.0001, '', -Number.MAX_VALUE, Number.MAX_VALUE],
          ['maxVelFollowingError', 'max vel following error', slave.maxVelFollowingError, 11, true, 0.0001, '', -Number.MAX_VALUE, Number.MAX_VALUE],
        ].map(d=>this.renderFormItemNumberInput(prefixCls, ...d))}
      </Row>
    </Fragment>
  }

  renderPdoEntry = (prefixCls, pdoEntry, syncMangerIndex, pdoIndex, pdoEntryIndex) => {
    const { getFieldDecorator} = this.props.form;
    const rules = {
      'name': [],
      'index': [{validator: hexValidator, message: 'Please input hex'}],
      'subIndex': [{validator: hexValidator, message: 'Please input hex'}],
      'size': [{type: 'integer', message: 'Please input integer',   transform: (value)=>{
          return Number(value)?Number(value):0;
        }}],
    }
    return <Row key={pdoEntryIndex} className={`${prefixCls}-sync-manager-pdo-entry`}>
      <Col span={4}>PDO Entry</Col>
      {
        ['name', 'index', 'subIndex', 'size'].map(key=>(<Col key={key} span={4}>
            <FormItem hasFeedback={true}>
              {getFieldDecorator(`slave.syncManagerPoolObjects[0].syncManagers[${syncMangerIndex}].pdoes[${pdoIndex}].entries[${pdoEntryIndex}].${key}`, {
                initialValue: pdoEntry[key],
                rules: [
                  ...rules[key],
                  {
                    required: true,
                    message: `Please input ${key}`,
                  },
                ],
              })(<Input placeholder={`${key}`} size={"small"}/>)}
            </FormItem>
          </Col>
        ))
      }
      <Col span={4}>
        <Button size={"small"} type="danger" onClick={()=>{
          this.removePdoEntry(syncMangerIndex, pdoIndex, pdoEntryIndex)
        }}>DEL</Button>
      </Col>
    </Row>
  }

  renderPdo = (prefixCls, pdo, syncMangerIndex, pdoIndex) => {
    const { getFieldDecorator} = this.props.form;
    const rules = {
      'name': [],
      'index': [{validator: hexValidator, message:'Please input hex'}]
    }
    return <div className={`${prefixCls}-sync-manager-pdo`} key={pdoIndex}>
      <Row className={`${prefixCls}-sync-manager-pdo-header`}>
        <Col span={2}>
          Pdo {pdoIndex+1}
        </Col>
        {['name', 'index'].map(key=><Col span={6} key={key}>
          <FormItem {...slaveFormItemLayout} label={key} hasFeedback={true}>
            {getFieldDecorator(`slave.syncManagerPoolObjects[0].syncManagers[${syncMangerIndex}].pdoes[${pdoIndex}].${key}`, {
              initialValue: pdo[key],
              rules: [
                ...rules[key],
                {
                  required: true,
                  message: `Please input ${key}`,
                },
              ],
            })(<Input placeholder={`${key}`} size={"small"} />)}
          </FormItem>
        </Col>)}
        <Col span={8} offset={2} className={`${prefixCls}-sync-manager-pdo-delete`}>
          <FormItem>
            <Button size={"small"} type={"danger"} onClick={()=>{
              this.removePdo(syncMangerIndex, pdoIndex)
            }}>DEL</Button>
          </FormItem>
        </Col>
      </Row>
      <div className={`${prefixCls}-sync-manager-pdo-entries`}>
        <Row className={`${prefixCls}-sync-manager-pdo-entries-header`}>
          <Col span={4}></Col>
          {
            ['name', 'index', 'subIndex', 'size'].map(key => (<Col key={key} span={4}>
                {key}
              </Col>
            ))
          }
        </Row>
        {!!pdo.entries && pdo.entries.map((pdoEntry, pdoEntryIndex) => this.renderPdoEntry(prefixCls, pdoEntry, syncMangerIndex, pdoIndex, pdoEntryIndex))}
        <Row className={`${prefixCls}-sync-manager-pdo-entries-add`}>
          <Col span={20}>
            <FormItem>
              <Button size={"small"} type={"primary"} onClick={()=>{
                this.addPdoEntry(syncMangerIndex, pdoIndex)
              }}>Add Pdo Entry</Button>
            </FormItem>
          </Col>
        </Row>
      </div>
    </div>
  }

  renderSyncManager = (prefixCls, syncManager, syncManagerIndex) => {
    const { getFieldDecorator} = this.props.form;
    return <div className={`${prefixCls}-sync-manager`} key={syncManagerIndex}>
      <Row className={`${prefixCls}-sync-manager-header`}>
        Sync Manager {syncManagerIndex+1}
        {getFieldDecorator(`slave.syncManagerPoolObjects[0].syncManagers[${syncManagerIndex}].isTx`, {
          initialValue: !!syncManager.isTx,
        })(<Checkbox>is_tx</Checkbox>)}
        <Button size={"small"} type={"primary"} onClick={this.addPdo.bind(this, syncManagerIndex)}>
          Add PDO
        </Button>
      </Row>
      <div className={`${prefixCls}-sync-manager-pdoes`}>
        {!!syncManager.pdoes && syncManager.pdoes.map((pdo, pdoIndex)=>this.renderPdo(
          prefixCls, pdo, syncManagerIndex, pdoIndex
        ))}
      </div>
    </div>
  }

  renderForm = (prefixCls) => {
    let {slaves, selectedSlave} = this.state
    let slave = slaves[selectedSlave]
    return <Form>
      {!!slave.isMotion && this.renderMotionInputs(prefixCls, slave)}
      {!slave.isMotion && this.renderSlaveInputs(prefixCls, slave)}
      <Row className={`${prefixCls}-is-motion`}>
        <Col span={23}>
          <Checkbox checked={slave.isMotion} onChange={(e)=>{
            slaves = {...slaves}
            slaves[selectedSlave] = {...slave, isMotion: e.target.checked}
            this.setState({slaves, saveError: null})
          }}>isMotion</Checkbox>
        </Col>
      </Row>
      {!!slave && slave.syncManagerPoolObjects && slave.syncManagerPoolObjects[0] && slave.syncManagerPoolObjects[0].syncManagers && slave.syncManagerPoolObjects[0].syncManagers.map((sm, index)=>this.renderSyncManager(prefixCls, sm, index))}
    </Form>
  }

  save = (e) => {
    e.preventDefault()
    const self = this
    const {sendCmd, cell, deleteResultByChannel} = this.props
    this.props.form.validateFields((err, {slave: values}) => {
      if (!err) {
        const {slaves, selectedSlave} = this.state
        const slave = slaves[selectedSlave]
        slaves[selectedSlave] = {syncManagerPoolObjects: [{syncManagers:[{},{}, {}, {}]}], ...values, uuid: slave.uuid, isMotion: slave.isMotion}
        const slavePoolObject = {
          name: "slave_pool",
          motions: [],
          slaves: [],
        }
        Object.keys(slaves).map(key=>{
          const slave = slaves[key]
          if(slave.isMotion){
            slavePoolObject.motions.push(slave)
          }else{
            slavePoolObject.slaves.push(slave)
          }
        })
        const ethercatController = {
          name: 'ethercat_controller',
          slavePoolObjects: [slavePoolObject]
        }
        const ec = EthercatController.FromJSON(ethercatController)
        this.setState({saveError: null})
        deleteResultByChannel(`${cell.i}-save`)
        sendCmd(`set_xml --xml={<ControlServer>${ec.toXML()}</ControlServer>}`, `${cell.i}-save`)
      }else{
        self.setState({saveError: ""})
      }
    })
  }

  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      saveResult,
    } = this.props
    const {
      slaves,
      selectedSlave,
      vendors,
      selectedVendor,
      selectedDevice,
      saveError,
    } = this.state
    const prefixCls = getPrefixCls('ethercat-configuration', customizePrefixCls);
    return <div className={prefixCls}>
      <SideBar
        prefixCls={prefixCls}
        slaves = {slaves}
        addSlave={this.addSlave}
        removeSlave={this.removeSlave}
        selectSlave={this.selectSlave}
        selectedSlave={selectedSlave}
        getEsiPdoList={this.getEsiPdoListForSlave}
        cell={cell}
      />
      {!!slaves && !!selectedSlave && !!slaves[selectedSlave] && <div className={`${prefixCls}-content`} onSubmit={()=>{}}>
        <HeaderBar
          cell={cell}
          vendors={vendors}
          prefixCls={prefixCls}
          selectDevice={this.selectDevice}
          selectVendor={this.selectVendor}
          selectedVendor={selectedVendor}
          selectedDevice={selectedDevice}
          save={this.save}
          getEsiPdoList={this.getEsiPdoListForDevice}
        ></HeaderBar>
        <Row className={`${prefixCls}-error`}>
          {saveError && <div className={'error'}>{saveError}</div>}
          {saveResult && saveResult.return_code !== 0 && <ResultError result={saveResult} className={'error'} />}
        </Row>
        {this.renderForm(prefixCls)}
      </div>
      }
    </div>
  }
}

const getModelConfigurationState = createSelector(
  getSetEsiPathJSONResult,
  getScanSlaveJsonResult,
  getPdoListForDeviceJSONResult,
  getPdoListForSlaveJSONResult,
  getSaveResult,
  (
    ethercatMaster,
    ethercatController,
    pdoListForDevice,
    pdoListForSlave,
    saveResult,
  ) => {
    return {
      ethercatMaster,
      ethercatController,
      pdoListForDevice,
      pdoListForSlave,
      saveResult,
    }
  }
)

function mapStateToProps(state, props) {
  return getModelConfigurationState(state, props)
}

export default connect(
  mapStateToProps,
  {
    loadEsiPath,
    deleteResultByChannel,
  }
)(Form.create({
  mapPropsToFields(props) {
    const { slave } = props;
    const transformed = transform({ slave });
    return transformed;
  },
  onValuesChange(_, values) {
  }
})(EthercatConfiguration))

