let cmdTimestamp = Date.now()

function action(type, payload = {}) {
  return {type, ...payload}
}

export const LOAD_INTERFACE_CONFIG = 'LOAD_INTERFACE_CONFIG'
export const INTERFACE_CONFIG_FETCH_REQUEST = 'INTERFACE_CONFIG_FETCH_REQUEST'
export const INTERFACE_CONFIG_FETCH_FAILURE = 'INTERFACE_CONFIG_FETCH_FAILURE'
export const INTERFACE_CONFIG_FETCH_SUCCESS = 'INTERFACE_CONFIG_FETCH_SUCCESS'
export const interfaceConfig = {
  request: () => action(INTERFACE_CONFIG_FETCH_REQUEST, {}),
  success: (response) => {
    response = {...response}
    if(response.dashboards){
      response.dashboards = response.dashboards.map(d=>{
        d = {...d}
        d.cells = d.cells.map(c=>{
          c = {...c}
          console.log(c.type, "before", c.options)
          try{
            c.options = JSON.parse(c.options)
          }catch(e){
            c.options = {}
          }
          console.log(c.options)
          return c
        })
        return d
      })
    }
    return action(INTERFACE_CONFIG_FETCH_SUCCESS, {response})
  },
  failure: (error) => action(INTERFACE_CONFIG_FETCH_FAILURE, {error}),
}
export const loadInterfaceConfig = (requiredFields = []) => action(LOAD_INTERFACE_CONFIG, {requiredFields})


export const UPDATE_DASHBOARD = 'UPDATE_DASHBOARD'
export const DASHBOARD_UPDATE_REQUEST = 'DASHBOARD_UPDATE_REQUEST'
export const DASHBOARD_UPDATE_FAILURE = 'DASHBOARD_UPDATE_FAILURE'
export const DASHBOARD_UPDATE_SUCCESS = 'DASHBOARD_UPDATE_SUCCESS'
export const dashboardUpdate = {
  request: (dashboard) => action(DASHBOARD_UPDATE_REQUEST, {dashboard}),
  success: (dashboard) => {
    if(dashboard.cells){
      dashboard = {...dashboard}
      dashboard.cells = dashboard.cells.map(c=>{
        c = {...c}
        if(c.options){
          console.log(c.type, "before", c.options)
          try{
            c.options = JSON.parse(c.options)
          }catch (e) {
            c.options = {}
          }
          console.log(c.type, c.options)
        }
        return c
      })
    }
    return action(DASHBOARD_UPDATE_SUCCESS, {dashboard})
  },
  failure: (error) => action(DASHBOARD_UPDATE_FAILURE, {error}),
}
export const updateDashboard = (dashboard) => {
  return action(UPDATE_DASHBOARD, {dashboard})
}


export const CREATE_CELL = 'CREATE_CELL'
export const CELL_CREATE_REQUEST = 'CELL_CREATE_REQUEST'
export const CELL_CREATE_FAILURE = 'CELL_CREATE_FAILURE'
export const CELL_CREATE_SUCCESS = 'CELL_CREATE_SUCCESS'
export const TOGGLE_CREATE_CELL_MODAL = 'TOGGLE_CREATE_CELL_MODAL'
export const cellCreate = {
  request: (dashboardId) => action(CELL_CREATE_REQUEST, {dashboardId}),
  success: (dashboardId, cell) => {
    cell = {...cell}
    if(cell.options){
      try{
        cell.options = JSON.parse(cell.options)
      }catch (e) {
        cell.options = {}
      }
    }
    return action(CELL_CREATE_SUCCESS, {dashboardId, cell})
  },
  failure: (error) => action(CELL_CREATE_FAILURE, {error}),
}
export const createCell = (dashboardId, cell) => action(CREATE_CELL, {dashboardId, cell})
export const toggleCreateCellModal = () => action(TOGGLE_CREATE_CELL_MODAL)

