export const STOP_WEBSOCKET = 'STOP_WEBSOCKET'
export const SEND_CMD = 'SEND_CMD'
export const CMD_RESPONSE = 'CMD_RESPONSE'
export const WEBSOCKET_CONNECTED = 'WEBSOCKET_CONNECTED'
export const WEBSOCKET_DISCONNECTED = 'WEBSOCKET_DISCONNECTED'
export const WEBSOCKET_ERROR = 'WEBSOCKET_ERROR'
export const startWebsocket = (url) => action(START_WEBSOCKET,{url})
export const stopWebsocket = () => action(STOP_WEBSOCKET, {stopped:true})
export const wsConnected = () => action(WEBSOCKET_CONNECTED)
export const wsDisconnected = () => action(WEBSOCKET_DISCONNECTED)
export const wsError = (error) => action(WEBSOCKET_ERROR, {error})
export const sendCmd = (cmd, channel, callback) => {
  let now = Date.now()
  if(now <= cmdTimestamp){
    cmdTimestamp = cmdTimestamp + 1
  }else{
    cmdTimestamp = now
  }
  return action(SEND_CMD, {cmd, id:cmdTimestamp, channel, callback})
}

