import React, {Fragment} from "react"
import ConfigBase from "../ConfigBase";
import Form from "antd/lib/form"
import Modal from 'antd/lib/modal'
import Radio from 'antd/lib/radio'
import Select from 'antd/lib/select'
import Button from 'antd/lib/button'
import Icon from 'antd/lib/icon'
import Row from 'antd/lib/row'
import Col from 'antd/lib/col'
import Input from 'antd/lib/input'
import InputNumber from 'antd/lib/input-number'
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import isArray from 'lodash/isArray'
import isEqual from 'lodash/isEqual'
import {
  getMemoizeDataSources,
  getMemoizeDataIndexesBySource,
} from "../../../selectors";
import {
  sendCmd,
  deleteResultByChannel
} from "../../../actions";

const { Search } = Input;


const RadioGroup = Radio.Group
const FormItem = Form.Item
const Option = Select.Option


class Config extends ConfigBase {
  state = {
    sources: []
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const {cell:oldCell,dataSources:oldDataSources} = prevProps
    const {cell:newCell,dataSources:dataSources} = this.props
    if(!isEqual(oldCell, newCell) && newCell && newCell.options){
      if(newCell.options && newCell.options.sources){
        try{
          this.setState({sources: newCell.options.sources})
        }catch (e) {
          
        }
      }
    }
    if(!isEqual(oldDataSources, dataSources)){
      this.setState({sources: this.state.sources.filter(s=>{
          if(s.source && dataSources.indexOf(s.source) === -1){
            return false
          }
          return true
        })
      })
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, fieldsValue) => {
      if (err) {
        return;
      }
      let options = this.getOptions()
      this.updateOptions({
        ...options,
        ...fieldsValue,
        sources: fieldsValue.sources,
        showConfig: false
      })
    })
  }

  remove = k => {
    this.setState({
      sources: this.state.sources.filter((s, i)=> i!==k)
    })
  };

  add = () => {
    this.setState({
      sources: [...this.state.sources, {}]
    })
  };

  renderDataSources(options){
    const { getFieldDecorator, getFieldValue, getFieldsValue} = this.props.form;
    const {dataSources, dataIndexes} = this.props
    let selectedSources = options.sources || []
    selectedSources.forEach((source, index)=>{
      getFieldDecorator(`sources[${index}].source`, {
        initialValue: source.source
      });
    })
    const {sources} = this.state
    if(!dataSources || !dataIndexes){
      return <div>
        ...
      </div>
    }
    if(Object.keys(dataSources).length === 0){
      return <div style={{textAlign: "center"}} className={"error"}>
        
      </div>
    }
    let {sources:formSources} = getFieldsValue()
    if(!formSources){
      formSources=[]
    }
    const sourceItems = sources.map((source, index)=>{
      return <Row key={index}>
        <Col span={10}>
        <FormItem
          label={''}
          labelCol={{span:8}}
          wrapperCol={{span:16}}
        >
          {getFieldDecorator(`sources[${index}].source`, {
            initialValue: source.source,
            rules: [{
              type: 'string',
              required: true,
              message: ''
            }]
          })(<Select
            style={{width: "100%"}}
            size={"small"}
            placeholder={""}
          >
            {
              dataSources.map(c => <Option key={c} value={c}>{c}</Option>)
            }
          </Select>)
          }
        </FormItem>
        </Col>
        <Col span={10}>
        {!!formSources[index] && !!dataIndexes[formSources[index].source] &&
        <FormItem
          label={'index'}
          labelCol={{span:8}}
          wrapperCol={{span:16}}
        >
          {getFieldDecorator(`sources[${index}].index`, {
            initialValue: source.index,
            rules: [{
              type: 'number',
              required: true,
              message: 'index'
            }]
          })(<Select
            style={{width: "100%"}}
            size={"small"}
            placeholder={"index"}
          >
            {
              dataIndexes[formSources[index].source].map(c => <Option key={c.toString()} value={c}>{c}</Option>)
            }
          </Select>)
          }
        </FormItem>
        }
        </Col>
        <Col span={4}>
          <Form.Item style={{paddingLeft:20}}>
            <Button onClick={this.remove.bind(this, index)} type={"danger"}>
              <Icon type="minus" /> 
            </Button>
          </Form.Item>
        </Col>
      </Row>
    })
    return <Fragment>
      {sourceItems}
      <Form.Item wrapperCol={{span:24}} style={{textAlign:'center'}}>
        <Button type="dashed" onClick={this.add}>
          <Icon type="plus" /> 
        </Button>
      </Form.Item>
    </Fragment>
  }

  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      sendCmd
    } = this.props
    const options = this.getOptions()
    const { getFieldDecorator} = this.props.form;
    const prefixCls = getPrefixCls('realtime-data-config', customizePrefixCls);
    const interval = options.interval || 1000
    return <Modal
      title=""
      visible={!!options.showConfig}
      maskClosable={false}
      okText={""}
      cancelText={""}
      width={"80%"}
      onOk={this.handleSubmit}
      onCancel={this.hideConfig}
      wrapClassName={`${prefixCls}`}
    >
      <Form layout={"horizontal"}>
        <FormItem label="">
          {getFieldDecorator('channel', {
            initialValue: options.channel === "command"?"command":"get",
            rules: [{
              type: 'string',
              required: true,
              message: ''
            }]
          })(
            <RadioGroup
              onChange={e=>{
                this.updateOptions({...options, channel:e.target.value})
              }}
            >
              <Radio value={"get"}>GET</Radio>
              <Radio value={"command"}></Radio>
            </RadioGroup>,
          )}
        </FormItem>
        {options.channel==="command" && <Row>
          <Col span={20}>
            <FormItem
              label={''}
              labelCol={{span:4}}
              wrapperCol={{span:20}}
            >
              {getFieldDecorator('command', {
                initialValue: options.command,
                rules: [{
                  type: 'string',
                  required: true,
                  message: ''
                }]
              })(
                <Search
                  onSearch={value => {
                    sendCmd(value, cell.i)
                  }}
                  enterButton={""}
                />
              )}
            </FormItem>
          </Col>
          <Col span={4}>
            <FormItem
              label={''}
              labelCol={{span:12}}
              wrapperCol={{span:12}}
            >
              {getFieldDecorator('interval', {
                initialValue: interval,
                rules: [{
                  required: true,
                  message: ''
                }]
              })(
                <InputNumber
                  min={30} step={1}
                  formatter={value => `${value}`}
                  parser={value => value.replace('', '')}
                />
              )}
            </FormItem>
          </Col>
        </Row>
          }
        {this.renderDataSources(options)}
      </Form>
    </Modal>
  }

}


const getState = createSelector(
  getMemoizeDataSources,
  getMemoizeDataIndexesBySource,
  (dataSources, dataIndexes) => {
    return {
      dataSources,
      dataIndexes,
    }
  }
)

function mapStateToProps(state, props) {
  return getState(state, props)
}

const transform = obj => {
  return Object.keys(obj).reduce((acc, cv) => {
    return {
      ...acc,
      [cv]:
        typeof obj[cv] === "object" && !("value" in obj[cv])
          ? isArray(obj[cv])
          ? obj[cv].map(item => transform(item))
          : transform(obj[cv])
          : Form.createFormField({
            ...obj[cv],
            value: obj[cv] && obj[cv].value
          })
    };
  }, {});
};

export default connect(
  mapStateToProps,
  {
    sendCmd,
    deleteResultByChannel,
  }
)(Form.create({
  mapPropsToFields(props) {
    let { sources, cell } = props;
    const transformed = transform({ sources });
    return transformed;
  },
  onValuesChange(_, values) {
  }
})(Config))

