import Blockly from "blockly"

Blockly.Robot['robotcontrol_movejr'] = function(block) {
  let dropdown_m_select = block.getFieldValue('M_SELECT');
  let angle_jointangle = block.getFieldValue('JOINT_ANGLE');
  let dropdown_dir = block.getFieldValue('DIR');
  let cmd_str="moveJR -m="+dropdown_m_select+" --pos="+angle_jointangle/180*3.14*dropdown_dir;
  return `${block.id}${cmd_str}`;
};

Blockly.Robot['robotcontrol_movel'] = function(block) {
  const endPQ = [0,1,2,3,4,5,6].map(i=>block.getFieldValue(`END_PQ${i}`)).join(',')
  return `${block.id}mvl --pq={${endPQ}}`;
};


Blockly.Robot['robotcontrol_movej'] = function(block) {
  const endPQ = [0,1,2,3,4,5,6].map(i=>block.getFieldValue(`END_PQ${i}`)).join(',')
  return `${block.id}mvj --pq={${endPQ}}`;
};

