import React, {Component, Fragment} from 'react'
import PropTypes from "prop-types"
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import CellBase from "../CellBase"
import Button from "antd/lib/button"
import Icon from "antd/lib/icon"
import Tooltip from "antd/lib/tooltip"
import {
  getCurrentPlan,
  getCurrentPlanId,
  getResultByCell,
  getEndPQ
} from "../../../selectors"
import {
  deleteResultByChannel,
  loadPrograms,
  loadProgram,
  createProgram,
  deleteProgram,
  updateProgram,
  renameProgram,
  sendCmd
} from "../../../actions"
import ReactBlocklyComponent from "react-blockly"
// import parseWorkspaceXml from "react-blockly/dist-modules/BlocklyHelper.js"

import Blockly from 'blockly'
import { Resizable, ResizableBox } from 'react-resizable';
import {VARIABLES_TOOLBOX_CATEGORIES, PROGRAM_TOOLBOX_CATEGORIES} from "./initContent"
import "./blocks/index"
import "./generators/index"
import { withSize } from 'react-sizeme'

class Editors extends Component {
  state = {
    variablesToolboxCategories: VARIABLES_TOOLBOX_CATEGORIES,
    programToolboxCategories: PROGRAM_TOOLBOX_CATEGORIES,
    looping: false,
  }
  rowIdMap = {}
  componentWillUnmount() {
    clearTimeout(this.timer)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if(prevProps.size.width !== this.props.size.width || prevProps.size.height !== this.props.size.height){
      const self = this
      clearTimeout(self.timer)
      self.timer = setTimeout(()=>{
        if(self.programWorkspace){
          Blockly.svgResize(self.programWorkspace)
        }
        if(self.variablesWorkspace){
          Blockly.svgResize(self.variablesWorkspace)
        }
      }, 50)
    }
    if(prevProps.currentPlanId !== this.props.currentPlanId){
      if(this.rowIdMap[this.props.currentPlanId] && this.programWorkspace){
        this.programWorkspace.highlightBlock(this.rowIdMap[this.props.currentPlanId], true)
      }
      if(this.rowIdMap[prevProps.currentPlanId] && this.programWorkspace){
        this.programWorkspace.highlightBlock(this.rowIdMap[prevProps.currentPlanId], false)
      }
      if(this.props.currentPlanId>Object.keys(this.rowIdMap).length && this.state.looping){
        this.tryStart()
      }
    }
  }
  shouldComponentUpdate(nextProps, nextState, nextContext) {
    if(nextProps.endPQ !== this.props.endPQ){
      if(this.programWorkspace && nextProps.endPQ){
        this.programWorkspace.endPQ = nextProps.endPQ
        this.programWorkspace.getAllBlocks().forEach(block=> {
          if(block.updateEndPQ && block.isEndPQEmpty && block.isEndPQEmpty()){
            block.updateEndPQ(nextProps.endPQ)
          }
        })
      }
    }
    return true
  }

  debug = () => {
    const {sendCmd, cell, currentPlanId} = this.props
    const isRunning = currentPlanId <= Object.keys(this.rowIdMap).length && currentPlanId > 0
    if(!isRunning){
      this.setState({
        running: false,
        debugging: true,
        looping: false,
      })
      this.rowIdMap = {}
      let cmds = []
      if(this.variablesWorkspace){
        let code = Blockly.Robot.workspaceToCode(this.variablesWorkspace);
        if(code && code.trim() !== ''){
          code.split('\n').forEach(l=>{
            if(l.slice(0,20).trim() !== ''){
              cmds.push(`${cmds.length+1}:${l.slice(20)}\\r\\n `)
              this.rowIdMap[cmds.length] = l.slice(0,20)
            }
          })
        }
      }
      if(this.programWorkspace){
        let code = Blockly.Robot.workspaceToCode(this.programWorkspace);
        if(code && code.trim() !== ''){
          code.split('\n').forEach(l=>{
            if(l.slice(0,20).trim() !== ''){
              cmds.push(`${cmds.length+1}:${l.slice(20)}\\r\\n `)
              this.rowIdMap[cmds.length] = l.slice(0,20)
            }
          })
        }
      }
      if(cmds.length>0){
        sendCmd(`collectcmd --cmdlist={${cmds.join('').trim()}}`, cell.i)
        sendCmd(`run --forward`, cell.i)
      }
    }else{
      if(this.state.debugging && isRunning){
        sendCmd(`run --forward`, cell.i)
      }
    }
  }

  tryStart = () => {
    const {sendCmd, cell, currentPlanId} = this.props
    const isRunning = currentPlanId <= Object.keys(this.rowIdMap).length && currentPlanId > 0
    if(isRunning){
      return
    }
    const cmds = []
    this.rowIdMap = {}
    if(this.variablesWorkspace){
      let code = Blockly.Robot.workspaceToCode(this.variablesWorkspace);
      if(code && code.trim() !== ''){
        code.split('\n').forEach(l=>{
          if(l.slice(0,20).trim() !== ''){
            cmds.push(`${cmds.length+1}:${l.slice(20)}\\r\\n `)
            this.rowIdMap[cmds.length] = l.slice(0,20)
          }
        })
      }
    }
    if(this.programWorkspace){
      let code = Blockly.Robot.workspaceToCode(this.programWorkspace);
      if(code && code.trim() !== ''){
        code.split('\n').forEach(l=>{
          if(l.slice(0,20).trim() !== ''){
            cmds.push(`${cmds.length+1}:${l.slice(20)}\\r\\n `)
            this.rowIdMap[cmds.length] = l.slice(0,20)
          }
        })
      }
    }
    if(cmds.length>0){
      sendCmd(`collectcmd --cmdlist={${cmds.join('').trim()}}`, cell.i)
      sendCmd(`run --start`, cell.i)
    }
  }
  tryStop = () => {
    const {cell, currentPlanId,sendCmd} = this.props
    const isRunning = currentPlanId <= Object.keys(this.rowIdMap).length && currentPlanId > 0
    if(isRunning) {
      sendCmd(`run --stop`, cell.i)
    }
  }

  loop = () => {
    const {sendCmd, cell, currentPlanId} = this.props
    let isRunning = currentPlanId <= Object.keys(this.rowIdMap).length && currentPlanId > 0
    if(!this.state.looping){
      this.setState({
        looping: true,
        running: false,
        debugging: false,
      })
      this.tryStart()
    }else{
      if(this.state.looping){
        this.setState({
          looping: false,
        })
        this.tryStop()
      }
    }
  }

  run = () => {
    if(!this.state.running){
      this.setState({
        running: true,
        debugging: false,
        looping: false,
      })
      this.tryStart()
    }else{
      if(this.state.running){
        this.setState({
          running: false,
        })
        this.tryStop()
      }
    }
  }

  variablesWorkspaceDidChange = (workspace) => {
    // console.log(Blockly.Robot.workspaceToCode(workspace))
    let {program} = this.props
    const newXml = Blockly.Xml.domToText(Blockly.Xml.workspaceToDom(workspace));
    if(!!program) {
      let newProgram = {...program}
      newProgram.files = [...newProgram.files]
      newProgram.files.forEach((f, index)=>{
        if(f.name === program.name+'.dat'){
          newProgram.files[index] = {...f, content: newXml}
        }
      })
      this.props.updateProgram(program.name, newProgram)
    }
  }
  programWorkspaceDidChange = (workspace) => {
    console.log(Blockly.Robot.workspaceToCode(workspace))
    let {program} = this.props
    const newXml = Blockly.Xml.domToText(Blockly.Xml.workspaceToDom(workspace));
    if(!!program) {
      let newProgram = {...program}
      newProgram.files = [...newProgram.files]
      newProgram.files.forEach((f, index)=>{
        if(f.name === program.name+'.pro'){
          newProgram.files[index] = {...f, content: newXml}
        }
      })
      this.props.updateProgram(program.name, newProgram)
    }
  }

  render(){
    const self = this
    const {
      prefixCls,
      size: {width, height},
      program,
      options,
      currentPlanId,
    } = self.props
    const {
      running,
      debugging,
      looping,
    } = this.state
    let isRunning = currentPlanId <= Object.keys(this.rowIdMap).length && currentPlanId > 0
    let proXml = ''
    let datXml = ''
    if(program && program.files) {
      program.files.forEach(f=>{
        if(f.name === program.name+'.dat'){
          datXml = f.content
        }
        if(f.name === program.name+'.pro'){
          proXml = f.content
        }
      })
    }
    let variablesHeight = !!options.variablesHeight?parseFloat(options.variablesHeight):200
    if (variablesHeight <= 50) {
      variablesHeight = 50;
    }
    let dividerHeight = 5
    let programHeight = height - variablesHeight - dividerHeight
    return <Fragment>
      <div className={`${prefixCls}-editors`}>
        {
          !!datXml && !options.hideVariablesWorkspace &&
          <ResizableBox
            width={width}
            height={variablesHeight}
            minConstraints={[width, 100]}
            maxConstraints={[width, height-30-100]}
            className={`${prefixCls}-editors-variables`}
            axis={'y'}
            handle={<div className={`${prefixCls}-divider`}></div>}
            onResize={(e, {size:{height,width}})=>{
              self.props.updateOptions({
                ...options, variablesHeight:height
              })
              if(self.programWorkspace){
                Blockly.svgResize(self.programWorkspace)
              }
              if(self.variablesWorkspace){
                Blockly.svgResize(self.variablesWorkspace)
              }
            }}
          >
            <ReactBlocklyComponent.BlocklyEditor
              ref={(ref) => {
                // this.variableEditor = ref
                if(ref && ref.workspace && ref.workspace.state) {
                  this.variablesWorkspace = ref.workspace.state.workspace
                }
              }}
              toolboxCategories={this.state.variablesToolboxCategories}
              initialXml={datXml}
              workspaceConfiguration={{
                grid: {
                  spacing: 20,
                  length: 3,
                  colour: '#ccc',
                  snap: true,
                },
              }}
              wrapperDivClassName={`${prefixCls}-full-height`}
              workspaceDidChange={this.variablesWorkspaceDidChange}
            />
          </ResizableBox>
        }
        {
          !!proXml && <div
            className={`${prefixCls}-editors-program ${options.hideVariablesWorkspace?`${prefixCls}-editors-program-full-height`:''}`}
            style={{height:programHeight}}
          >
            <ReactBlocklyComponent.BlocklyEditor
              ref={(ref) => {
                // this.programEditor = ref
                if(ref && ref.workspace && ref.workspace.state) {
                  this.programWorkspace = ref.workspace.state.workspace
                }
              }}
              toolboxCategories={this.state.programToolboxCategories}
              initialXml={proXml}
              workspaceConfiguration={{
                grid: {
                  spacing: 20,
                  length: 3,
                  colour: '#ccc',
                  snap: true,
                },
              }}
              wrapperDivClassName={`${prefixCls}-full-height`}
              workspaceDidChange={this.programWorkspaceDidChange}
            />
            <Tooltip placement="bottom" title={options.hideVariablesWorkspace?'':''}>
            <Icon
              type="double-right"
              className={`${prefixCls}-editors-program-toggle`}
              onClick={()=>{
                this.props.updateOptions({...options, hideVariablesWorkspace:!options.hideVariablesWorkspace})
              }}
            />
            </Tooltip>
          </div>
        }
      </div>
      <div className={`${prefixCls}-operations`}>
        <div className={`${prefixCls}-operations-left`}>
          {!!options.hideSidebar &&
          <Icon
            type="double-right"
            className={`${prefixCls}-sidebar-toggle`}
            onClick={()=>{
              this.props.updateOptions({...options, hideSidebar: ""})
            }}
          />
          }
          {!options.hideSidebar &&
          <Icon
            type="double-left"
            className={`${prefixCls}-sidebar-toggle`}
            onClick={()=>{
              this.props.updateOptions({...options, hideSidebar: "true"})
            }}
          />
          }
          <span className={`${prefixCls}-operations-left-name`}>{program.name}</span>
        </div>
        <Button
          size={'small'}
          type={'primary'}
          disabled={(debugging || running) && isRunning}
          onClick={this.loop}
        >{looping?'':''}</Button>
        <Button
          size={'small'}
          type={'primary'}
          onClick={this.debug}
          disabled={(running && isRunning) || looping}
        >{(debugging && isRunning)? '' : ''}</Button>
        {(debugging && isRunning)  &&
        <Button
          size={'small'}
          type={'primary'}
          onClick={()=>{
            // this.setState({debugging:false})
            this.tryStop()
          }}
        > </Button>
        }
        {!(debugging && isRunning) &&
        <Button
          size={'small'}
          type={'primary'}
          onClick={this.run}
          disabled={(debugging && isRunning) || looping}
        >{(running && isRunning) ? '' : ''}</Button>
        }
      </div>
    </Fragment>
  }
}

Editors.propTypes = {
  prefixCls: PropTypes.string.isRequired,
  cell: PropTypes.object.isRequired,
  program: PropTypes.object,
  updateOptions: PropTypes.func.isRequired,
}

const getState = createSelector(
  getResultByCell,
  getCurrentPlan,
  getCurrentPlanId,
  getEndPQ,
  (result, currentPlan, currentPlanId, endPQ) => {
    return {
      result,
      currentPlan,
      currentPlanId,
      endPQ,
    }
  }
)

function mapStateToProps(state, props) {
  return getState(state, props)
}

export default connect(
  mapStateToProps,
  {
    deleteResultByChannel,
    loadPrograms,
    loadProgram,
    createProgram,
    deleteProgram,
    updateProgram,
    renameProgram,
    sendCmd,
  }
)(withSize({monitorHeight: true, refreshRate: 30})(Editors))



