import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Row from "antd/lib/row"
import Col from "antd/lib/col"
import Button from "antd/lib/button"
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import Select from "antd/lib/select"
import CellBase from "./CellBase";
import Modal from "antd/lib/modal"
import Icon from "antd/lib/icon"
import {
  getResultByCell
} from "../../selectors";
import {deleteResultByChannel} from "../../actions";
import InputNumber from 'antd/lib/input-number'
import SixAxes from '../../images/six_axes.png'
import SevenAxes from '../../images/seven_axes.png'
const Option = Select.Option


class SetDH extends CellBase {
  name = ''
  width = 24
  height = 12
  icon = (props) => <Icon {...props} />

  state = {
    type: 'six_axes',
    inputs: {},
    showSaveResult: false,
  }

  handleSelectType = (type) => {
    const {deleteResultByChannel, cell} = this.props
    deleteResultByChannel(cell.i)
    this.setState({
      type,
      inputs: {},
      showSaveResult: false,
    })
  }

  updateInput = (name, value) =>{
    let inputs = {...this.state.inputs}
    inputs[name] = value
    this.setState({inputs})
  }

  renderSixAxesInput = (prefixCls) => {
    const {inputs, showSaveResult} = this.state
    const self = this
    const {deleteResultByChannel, cell} = self.props
    return <Fragment>
      {['a1', 'a2', 'a3', 'd1', 'd3', 'd4', 'tool0'].map((name, index) => <Row
        key={name}
        className={`${prefixCls}-input`}>
          <span> {name} </span>
          <InputNumber
            disabled={showSaveResult}
            step={1}
            value={inputs[name]}
            size={'small'}
            onChange={(value)=>{
              self.updateInput(name, value)
            }}
          />
      </Row>)}
      <Row
        className={`${prefixCls}-save-button`}
      >
        <Button
          type={'primary'}
          disabled={showSaveResult}
          onClick={()=>{
            self.setState({
              showSaveResult: true,
            })
            deleteResultByChannel(cell.i)
            const {inputs} = self.state
            let cmd = "setdh --six_axes"
            Object.keys(inputs).forEach(name=>{
              cmd+=` --${name}_six_axes=${inputs[name]/1000}`
            })
            self.sendCmd(cmd, cell.i)
          }}
        ></Button>
      </Row>
    </Fragment>
  }

  renderSevenAxesInput = (prefixCls) => {
    const {inputs, showSaveResult} = this.state
    const self = this
    const {deleteResultByChannel, cell} = self.props
    return <Fragment>
      {['d1', 'd3', 'd5', 'tool0'].map((name) => <Row
        key={name}
        className={`${prefixCls}-input`}>
          <span> {name} </span>
          <InputNumber
            disabled={showSaveResult}
            step={1}
            value={inputs[name]}
            size={'small'}
            onChange={(value)=>{
              self.updateInput(name, value)
            }}
          />
      </Row>)}
      <Row
        className={`${prefixCls}-save-button`}
      >
        <Button
          type={'primary'}
          disabled={showSaveResult}
          onClick={()=>{
            self.setState({
              showSaveResult: true,
            })
            deleteResultByChannel(cell.i)
            const {inputs} = self.state
            let cmd = "setdh --seven_axes"
            Object.keys(inputs).forEach(name=>{
              cmd+=` --${name}_seven_axes=${inputs[name]/1000}`
            })
            self.sendCmd(cmd, cell.i)
          }}
        ></Button>
      </Row>
    </Fragment>
  }

  renderContent = ({getPrefixCls}) => {
    const self = this
    const {
      prefixCls: customizePrefixCls,
    } = self.props
    const prefixCls = getPrefixCls('setdh', customizePrefixCls);
    const {type, showSaveResult} = this.state
    return <div
      className={`${prefixCls}`}
    >
      <Row>
        <Col span={12}>
          <Row className={`${prefixCls}-input`}>
            <Select
              size={"small"}
              placeholder={""}
              onChange={self.handleSelectType}
              value={type}
            >
              <Option value={'six_axes'} key={'six_axes'}></Option>
              <Option value={'seven_axes'} key={'seven_axes'}></Option>
            </Select>
          </Row>
          {
            type==='six_axes' && this.renderSixAxesInput(prefixCls)
          }
          {
            type==='seven_axes' && this.renderSevenAxesInput(prefixCls)
          }
        </Col>
        <Col
          span={12}
          className={`${prefixCls}-bg`}
          style={{
            backgroundImage: `url(${type==='six_axes'?SixAxes:SevenAxes})`,
          }}
        >
        </Col>
        <Modal
          visible={showSaveResult}
          title=""
          centered={true}
          onCancel={()=>{
            self.setState({showSaveResult: false})
          }}
          footer={[
            <Button key="close" onClick={()=>{
              self.setState({showSaveResult: false})
            }}>
              
            </Button>,
          ]}
        >
          {this.renderResult(prefixCls)}
        </Modal>
      </Row>
    </div>
  }

  renderResult = (prefixCls) => {
    let {result, deleteResultByChannel, cell} = this.props
    const filter = ['cmd_response_time','id']
    if (!result) {
      return <div className={`${prefixCls}-result info`}>
        <div>...</div>
      </div>
    } else if (result.return_code !== 0 || !result.link_param) {
      return <div className={`${prefixCls}-result error`}>
        <div></div>
        {
          Object.keys(result).map(key=>!!result[key] && filter.indexOf(key) === -1 && <div
            key={key}
          ><span>{key}</span>:<span>{result[key]}</span></div>)
        }
      </div>
    }else{
      return <div className={`${prefixCls}-result success`}>

        <div></div>
        {
          Object.keys(result).map(key=>!!result[key] && filter.indexOf(key) === -1 && <div
            key={key}
          ><span>{key}</span>:<span>{result[key]}</span></div>)
        }
      </div>
    }
  }

  render(){
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }
}

SetDH.propTypes = {
  result: PropTypes.object,
}

const getSetDHState = createSelector(
  getResultByCell,
  (result) => {
    return {
      result,
    }
  }
)

function mapStateToProps(state, props) {
  return getSetDHState(state, props)
}

const ConnectedSetDH = connect(
  mapStateToProps,
  {
    deleteResultByChannel
  }
)(SetDH)

ConnectedSetDH.NAME = ''
export default ConnectedSetDH

