import React, {Component} from "react"
import PropTypes from 'prop-types'
import Form from "antd/lib/form"
import Modal from 'antd/lib/modal'
import Radio from 'antd/lib/radio'
import Icon from 'antd/lib/icon'
import Input from 'antd/lib/input'
import InputNumber from 'antd/lib/input-number'
import Cells from '../shared/cells/index'

const FormItem = Form.Item
const RadioGroup = Radio.Group
const RadioButton = Radio.Button

class CreateCellModal extends  Component {
  handleSubmit = e => {
    e.preventDefault();
    const {createCell} = this.props
    this.props.form.validateFields((err, fieldsValue) => {
      if (err) {
        console.error(err)
        return
      }
      createCell(fieldsValue)
    })
  }
  render() {
    const {
      visible,
      toggleCreateCellModal,
      prefixCls,
      creatingCellStatus
    } = this.props
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const selectType = getFieldValue('type')
    return <Modal
      title=""
      visible={!!visible}
      width={"75%"}
      okText={""}
      cancelText={""}
      onOk={this.handleSubmit}
      onCancel={toggleCreateCellModal}
      wrapClassName={`${prefixCls}-create-cell-modal`}
    >
      <Form>
        <FormItem label="" className={`${prefixCls}-type-selector`}>
          {getFieldDecorator('type', {
            rules: [{ type: 'string', required: true, message: '!' }]
          })(
            <RadioGroup disabled={!!creatingCellStatus.creatingCell}>
              {Object.keys(Cells).map(key=>{
                if(key !== "Unknown"){
                  const ICON = Cells[key].ICON?Cells[key].ICON:(props)=><Icon type="block" {...props}/>
                  return <RadioButton value={key} key={key}>
                    <div>
                      <div>
                      <ICON/>
                      </div>
                      <div className={`${prefixCls}-type-name`}>
                        {!!Cells[key].NAME?Cells[key].NAME:key}
                      </div>
                    </div>
                  </RadioButton>
                }
              })}
            </RadioGroup>,
          )}
        </FormItem>
        <FormItem label="" className={`${prefixCls}-input-col`}>
          {getFieldDecorator('name', {
            rules: [{ type: 'string', required: true, message: '' }]
          })(<Input size={"small"} disabled={!!creatingCellStatus.creatingCell}/>)}
        </FormItem>
        <FormItem label="" className={`${prefixCls}-input-col`}>
          {getFieldDecorator('w', {
            initialValue: (!!Cells[selectType] && !!Cells[selectType].WIDTH)?Cells[selectType].WIDTH:24,
            rules: [{ type: 'integer', required: true, message: '' }]
          })(<InputNumber  max={48} min={3} step={1}  size={"small"} disabled={!!creatingCellStatus.creatingCell}/>)}
        </FormItem>
        <FormItem label="" className={`${prefixCls}-input-col`}>
          {getFieldDecorator('h', {
            initialValue: (!!Cells[selectType] && !!Cells[selectType].HEIGHT)?Cells[selectType].HEIGHT:12,
            rules: [{ type: 'integer', required: true, message: '' }]
          })(<InputNumber max={48} min={3} step={1} size={"small"} disabled={!!creatingCellStatus.creatingCell}/>)}
        </FormItem>
      </Form>
      <div className={`${prefixCls}-status`}>
      {!!creatingCellStatus.creatingCell && <div className={"info"}>
       ...
      </div>}
      {!!creatingCellStatus.creatingCellFailure && <div className={"error"}>
        : {creatingCellStatus.creatingCellFailure}
      </div>}
      {!!creatingCellStatus.creatingCellSuccess && <div className={"success"}>
        
      </div>}
      </div>
    </Modal>
  }
}


CreateCellModal.propTypes = {
  createCell: PropTypes.func.isRequired,
  toggleCreateCellModal: PropTypes.func.isRequired,
  visible: PropTypes.bool,
  prefixCls: PropTypes.string.isRequired,
  creatingCellStatus: PropTypes.object.isRequired,
}

export default Form.create()(CreateCellModal)
