import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import { connect } from 'react-redux'
import {getStateCode} from "../../reducers/selectors";
import {getWebsocketConnected} from "../../selectors/websocket";

class RobotStatus extends Component {
  renderJogJoint = ({getPrefixCls}) => {
    let {
      prefixCls: customizePrefixCls,
      cell,
      websocketConnected,
      stateCode,
    } = this.props
    if(!stateCode) {
      stateCode = 100
    }
    const colors = {
      100:"#AAAAAA",
      200:"#FF851B",
      300:"#FF4136",
      400:"#0074D9",
      500:"#01FF70"
    }
    const names = {
      100:"",
      200:"",
      300:"",
      400:"",
      500:""
    }
    console.log("robot state code", stateCode)
    const color = colors[stateCode]
    const name = names[stateCode]
    const prefixCls = getPrefixCls('robot-status', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
    >
      <div>
        {!!websocketConnected && <Icon type="wifi" className={"success"}>
        </Icon> }
        {!!websocketConnected &&
        <span></span>
        }
        {!websocketConnected && <Icon type="wifi" className={"danger"}>
        </Icon> }
        {!websocketConnected &&
        <span></span>
        }
      </div>
      <div>
        <Icon
          style={{
            color: color,
            fill: color
          }}
          fill={color}
          type="check-circle" className="info" theme="filled"
        >
        </Icon>
        <span>{name || ""}</span>
      </div>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogJoint}</ConfigConsumer>
  }
}

RobotStatus.propTypes = {
  cell: PropTypes.object.isRequired,
  stateCode: PropTypes.number,
}

function mapStateToProps(state) {
  return {
    stateCode: getStateCode(state),
    websocketConnected: getWebsocketConnected(state)
  }
}

const ConnectedRobotStatus = connect(
  mapStateToProps,
)(RobotStatus)

ConnectedRobotStatus.NAME = ''
export default ConnectedRobotStatus

