import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import Radio from "antd/lib/radio"
import Row from "antd/lib/row"
import Col from "antd/lib/col"
import Input from "antd/lib/input"
import Button from "antd/lib/button"
import Select from "antd/lib/select"
import { connect } from 'react-redux'
import Popover from 'antd/lib/popover'
import Table from 'antd/lib/table'
import { createSelector } from 'reselect'


import CellBase from "./CellBase";
import {
  getToolIds,
  getMemoizeEndPE,
  getMemoizeResultByCell, getResultByCell
} from "../../selectors";
import {deleteResultByChannel} from "../../actions";
import Wifi from "../icons/Wifi";
const Option = Select.Option
const RadioGroup = Radio.Group

class ZeroCalib extends CellBase {
  calibMethods = {
    "CalibZF": "",
    "CalibZO": "",
    "CalibZL": ""
  }
  state = {
    calibMethod: 'CalibZF',
    toolId: 0,
    toolName: '',
    pes: [],
    showCalibResult: false,
  }
  handleSelectToolId = (toolId) => {
    this.setState({toolId, pes: [], showCalibResult: false})
  }
  handleSelectCalibMethod = (event) => {
    this.setState({calibMethod:event.target.value, showCalibResult: false})
  }

  renderContent = ({getPrefixCls}) => {
    const self = this
    const {
      prefixCls: customizePrefixCls,
      cell,
      toolIds,
      deleteResultByChannel,
    } = self.props
    const {toolId, calibMethod} = self.state
    const prefixCls = getPrefixCls('zero-calib', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
    >
      <Row>
        <Col>
          <RadioGroup
            onChange={this.handleSelectCalibMethod}
            value={calibMethod}
            className={`${prefixCls}-options`}
          >
            {Object.keys(self.calibMethods).map(method=>{
              return <Radio key={method} value={method}>{self.calibMethods[method]}</Radio>
            })}
          </RadioGroup>
        </Col>
      </Row>
      { calibMethod !== 'CalibZF' && <Row>
        <Col
          className={`${prefixCls}-select`}
        >
          <Select
            size={"small"}
            placeholder={""}
            onChange={self.handleSelectToolId}
            value={toolId}
          >
            {toolIds.map(id=> {
              return <Option value={id} key={id}>{id}</Option>
            })}
          </Select>
          <span>tool_{toolId}</span>
        </Col>
      </Row>
      }
      <Row>
        <Col className={`${prefixCls}-buttons`}>
        {[0, 1, 2].map((i) => <Row key={i}>
          <Button
            type="primary"
            // size={"large"}
            onClick={()=> {
              deleteResultByChannel(cell.i)
              self.setState({showCalibResult:true})
              if(calibMethod === 'CalibZF'){
                self.sendCmd(`${calibMethod} --axis_id=${2*i}`, cell.i)
              }else{
                self.sendCmd(`${calibMethod} --axis_id=${2*i} --tool_id=${toolId}`, cell.i)
              }
            }}
            onMouseDown={()=>{
              deleteResultByChannel(cell.i)
              self.setState({showCalibResult:true})
              if(calibMethod === 'CalibZF'){
                self.getOnMouseDownFn(
                  `${calibMethod} --axis_id=${2*i}`,
                  cell.i
                )()
              }else {
                self.getOnMouseDownFn(
                  `${calibMethod} --axis_id=${2 * i} --tool_id=${toolId}`,
                  cell.i
                )()
              }
            }}
            onMouseUp={()=>{
              self.getOnMouseUpFn()()
            }}
            onMouseOut={()=>{
              self.getOnMouseUpFn()()
            }}
          >A{2*i+1}</Button>
          <Button
            type="primary"
            // size={"large"}
            onClick={()=> {
              deleteResultByChannel(cell.i)
              self.setState({showCalibResult:true})
              if(calibMethod === 'CalibZF'){
                self.sendCmd(`${calibMethod} --axis_id=${2*i+1}`, cell.i)
              }else{
                self.sendCmd(`${calibMethod} --axis_id=${2*i+1} --tool_id=${toolId}`, cell.i)
              }
            }}
            onMouseDown={()=>{
              deleteResultByChannel(cell.i)
              self.setState({showCalibResult:true})
              if(calibMethod === 'CalibZF'){
                self.getOnMouseDownFn(
                  `${calibMethod} --axis_id=${2*i+1}`,
                  cell.i
                )()
              }else {
                self.getOnMouseDownFn(
                  `${calibMethod} --axis_id=${2 * i+1} --tool_id=${toolId}`,
                  cell.i
                )()
              }
            }}
            onMouseUp={()=>{
              self.getOnMouseUpFn()()
            }}
            onMouseOut={()=>{
              self.getOnMouseUpFn()()
            }}
          >A{2*i+2}</Button>
        </Row>)
        }
        </Col>
      </Row>
      <Row>
        {this.renderResult(prefixCls)}
      </Row>
    </div>
  }

  renderResult = (prefixCls) => {
      let {showCalibResult} = this.state
      let {result} = this.props
      if (!showCalibResult || !result) {
        return null

      }
      const filter = ['cmd_response_time','id']
      if (result.return_code !== 0) {
        return <div className={`${prefixCls}-calib-result error`}>
          <div></div>
          {
            Object.keys(result).map(key=>!!result[key] && filter.indexOf(key) === -1 && <div
              key={key}
            ><span>{key}</span>:<span>{result[key]}</span></div>)
          }
        </div>
      }else{
        return <div className={`${prefixCls}-calib-result success`}>
          <div></div>
          {
            Object.keys(result).map(key=>!!result[key] && filter.indexOf(key) === -1 && <div
              key={key}
            ><span>{key}</span>:<span>{result[key]}</span></div>)
          }
        </div>
      }

  }


  render(){
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }
}

ZeroCalib.propTypes = {
  toolIds: PropTypes.array.isRequired,
  result: PropTypes.object,
}


const getZeroCalibState = createSelector(
  getToolIds,
  getResultByCell,
  // getMemoizeResultByCell,
  (toolIds,  result) => {
    return {
      toolIds,
      result,
    }
  }
)

function mapStateToProps(state, props) {
  return getZeroCalibState(state, props)
}

const ConnectedZeroCalib =  connect(
  mapStateToProps,
  {
    deleteResultByChannel
  }
)(ZeroCalib)

ConnectedZeroCalib.NAME = ''
export default ConnectedZeroCalib

