import Blockly, { DropDownDiv } from "blockly"

let validator = function(newValue){
  let reg = /^[a-zA-Z][a-zA-Z0-9_]*$/;
  if(reg.test(newValue)){
    return newValue
  }
  else{
    return 'error'
  }
};

let list1 = [["int","int"], ["string","string"], ["boolen","boolen"], ["double","double"], ["int[][]","array"]];
Blockly.Blocks['base_variate'] = {
  init: function() {
    let field = new Blockly.FieldDropdown(list1,this.validate)
    this.appendDummyInput()
      .appendField(new Blockly.FieldLabelSerializable("VAR"), "NAME")
      .appendField(field, "DROPDOWN1");
    this.setPreviousStatement(true, null);
    this.setNextStatement(true, null);
    this.setColour(230);
    this.setTooltip("");
    this.setHelpUrl("");
  },

  validate: function(newValue) {
    this.getSourceBlock().updateConnections(newValue);
    return newValue;
  },

  updateConnections: function(newValue) {
    this.removeInput('other', /* no error */ true);
    this.removeInput('array1', /* no error */ true);

    let field2 = new Blockly.FieldTextInput("p1",validator);
    if (newValue == 'int') {
      this.appendDummyInput('other')
        .appendField(field2, "INPUT1")
        .appendField("=")
        .appendField(new Blockly.FieldTextInput("result"), "RESULT");

    }
    else if (newValue == 'string') {
      this.appendDummyInput('other')
        .appendField(field2, "INPUT1")
        .appendField("=")
        .appendField(new Blockly.FieldTextInput("result"), "RESULT");
    }
    else if (newValue == 'boolen') {
      this.appendDummyInput('other')
        .appendField(field2, "INPUT1")
        .appendField("=")
        .appendField(new Blockly.FieldTextInput("result"), "RESULT");
    }
    else if (newValue == 'double') {
      this.appendDummyInput('other')
        .appendField(field2, "INPUT1")
        .appendField("=")
        .appendField(new Blockly.FieldTextInput("result"), "RESULT");
    }
    else if (newValue == 'array') {
      this.appendDummyInput('array1')
        .appendField(field2, "INPUT1")
        .appendField(new Blockly.FieldTextInput("p2"), "INPUT2")
        .appendField("=")
        .appendField(new Blockly.FieldTextInput("result"), "RESULT");
    }
  },

};
