import React, {Component} from "react"
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import Upload from "antd/lib/upload"
import Button from "antd/lib/button"
import Icon from "antd/lib/icon"
import Row from "antd/lib/row"
import isEqual from "lodash/isEqual"
import {esiFileUpload, sendCmd, deleteResultByChannel} from "../../../actions"
import {getEsiPath, getWebsocketConnected} from "../../../selectors";
import ResultError from "../../../components/ResultError";
import {
  getSetEsiPathResult,
  getEsiFileUploadingRequest,
  getEsiFileUploadingFailure,
  getEsiFileUploadingSuccess,
  getScanSlaveResult,
  getScanSlaveJsonResult,
  getSetEsiPathJSONResult
} from './selectors'

class SideBar extends Component {
  render() {
    const {
      prefixCls,
      sendCmd,
      cell,
      esiFileUploadRequest,
      esiFileUploadFailure,
      esiFileUploadSuccess,
      ws,
      esiPath,
      esiFileUploadingRequest,
      esiFileUploadingSuccess,
      esiFileUploadingFailure,
      setEsiPathResult,
      scanSlaveResult,
      deleteResultByChannel,
      slaves,
      addSlave,
      removeSlave,
      selectSlave,
      selectedSlave,
    } = this.props
    return <div className={`${prefixCls}-sidebar`}>
      <Row className={`${prefixCls}-buttons`}>
        <Upload
          className={`${prefixCls}-esi-upload`}
          accept={".xml"}
          multiple={false}
          action={`/api/esi`}
          disabled={!ws}
          showUploadList={false}
          onChange={({file, fileList, event})=>{
            if(file.status === 'uploading'){
              deleteResultByChannel(`${cell.i}-setesipath`)
              deleteResultByChannel(`${cell.i}-scanslave`)
              esiFileUploadRequest()
            }else if(file.status === 'done'){
              esiFileUploadSuccess(file.response.path)
              sendCmd(`cs_stop`, `${cell.i}-stop`)
              sendCmd(`setesipath --path=${esiPath}`, `${cell.i}-setesipath`)
            }else if(file.status === 'error'){
              esiFileUploadFailure(file.response.error)
            }
          }}
        >
          <Button
            disabled={!ws}
          >
            <Icon type="upload" />ESI
          </Button>
        </Upload>
        <Button
          className={`${prefixCls}-scanslave`}
          disabled={!ws}
          onClick={()=>{
            deleteResultByChannel(`${cell.i}-scanslave`)
            if(!setEsiPathResult || !!setEsiPathResult.return_code){
              sendCmd(`cs_stop`, `${cell.i}-stop`)
              sendCmd(`setesipath --path=${esiPath}`, `${cell.i}-setesipath`)
            }
            sendCmd(`scanslave`, `${cell.i}-scanslave`)
          }}>
          <Icon type="monitor" />
        </Button>
      </Row>
      <Row className={`${prefixCls}-info`}>
        {!scanSlaveResult && !setEsiPathResult && esiFileUploadingRequest && <span className={"info"}>
          ESI...
        </span>}
        {!scanSlaveResult && !setEsiPathResult && esiFileUploadingSuccess && <span className={"success"}>
          ESI!
        </span>}
        {!scanSlaveResult && !setEsiPathResult && esiFileUploadingFailure && <span className={"info"}>
          ESI! ERROR:{esiFileUploadingFailure}
        </span>}
        {!scanSlaveResult && !!setEsiPathResult && !!setEsiPathResult.return_code && <ResultError
          className={"error"}
          filter={['cmd_response_time', 'id']}
          result={setEsiPathResult}
        />
        }
        {!scanSlaveResult && !!setEsiPathResult && !setEsiPathResult.return_code && <span className={"success"}>
          setesipath !
        </span>
        }

        {!!scanSlaveResult && !!scanSlaveResult.return_code && <ResultError
          className={"error"}
          filter={['cmd_response_time', 'id']}
          result={setEsiPathResult}
        />
        }
        {!!scanSlaveResult && !scanSlaveResult.return_code && <span className={"success"}>
          scanslave !
        </span>
        }
      </Row>
      {!!slaves && <Row className={`${prefixCls}-slaves-list`}>
        {Object.keys(slaves).map(key=>{
          return <div
            key={key}
            className={`${selectedSlave===key?`${prefixCls}-selected-slave`:''} ${prefixCls}-slaves-list-item`}
            onClick={()=>{
              selectSlave(key)
            }}
          >
            {slaves[key].isMotion?'':''}{slaves[key].phyId}
            <Button size={"small"} onClick={(e)=>{
              e.preventDefault()
              e.stopPropagation()
              removeSlave(key)
            }}>
              
            </Button>
          </div>
        })}
        <Row className={`${prefixCls}-add-slave`}>
          <Button onClick={addSlave}>
            
          </Button>
        </Row>
      </Row>}
    </div>
  }
}

SideBar.propTypes = {
  prefixCls: PropTypes.string.isRequired,
  sendCmd: PropTypes.func.isRequired,
  cell: PropTypes.object.isRequired,
  esiPath: PropTypes.string,


  addSlave: PropTypes.func.isRequired,
  removeSlave: PropTypes.func.isRequired,
  selectSlave: PropTypes.func.isRequired,
  selectedSlave: PropTypes.string,
}



const getState = createSelector(
  getEsiPath,
  getWebsocketConnected,
  getSetEsiPathResult,
  getScanSlaveResult,
  getScanSlaveJsonResult,
  getEsiFileUploadingRequest,
  getEsiFileUploadingSuccess,
  getEsiFileUploadingFailure,
  getSetEsiPathJSONResult,
  (
    esiPath,
    ws,
    setEsiPathResult,
    scanSlaveResult,
    scanSlaveJsonResult,
    esiFileUploadingRequest,
    esiFileUploadingSuccess,
    esiFileUploadingFailure,
    setEsiPathJSONResult,
  ) => {
    console.log(scanSlaveJsonResult, setEsiPathJSONResult)
    return {
      esiPath,
      ws,
      setEsiPathResult,
      scanSlaveResult,
      scanSlaveJsonResult,
      esiFileUploadingRequest,
      esiFileUploadingSuccess,
      esiFileUploadingFailure,
      setEsiPathJSONResult,
    }
  }
)

function mapStateToProps(state, props) {
  esiFileUpload.request()
  return getState(state, props)
}

export default connect(
  mapStateToProps,
  {
    esiFileUploadRequest: esiFileUpload.request,
    esiFileUploadFailure: esiFileUpload.failure,
    esiFileUploadSuccess: esiFileUpload.success,
    sendCmd,
    deleteResultByChannel,
  }
)(SideBar)


