import React, {Component} from 'react';
import PropTypes from 'prop-types'
import { withSize } from 'react-sizeme'


import {ConfigConsumer} from 'antd/lib/config-provider'
import DashboardEmpty from "./DashboardEmpty";
import LayoutRenderer from "../shared/containers/LayoutRenderer";
import {sendCmd} from "../actions";

class Dashboard extends Component {
  renderDashboard = ({getPrefixCls}) => {
    let {
      dashboard,
      prefixCls: customizePrefixCls,
      onZoom,
      onCloneCell,
      onDeleteCell,
      onPositionChange,
      sendCmd,
      onCellUpdate,
      layoutCols,
      layoutRowHeight,
      layoutMargin,
      layoutContainerPadding,
    } = this.props
    const prefixCls = getPrefixCls('dashboard', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
      id={'dashboard'}
    >
      {!!dashboard.cells && dashboard.cells.length ?(<LayoutRenderer
        layoutCols={layoutCols}
        layoutRowHeight={layoutRowHeight}
        layoutMargin={layoutMargin}
        layoutContainerPadding={layoutContainerPadding}

        cells={dashboard.cells}
        templates={[]}
        manualRefresh={10}
        onCellUpdate={onCellUpdate}
        isEditable={!!dashboard.editable}
        onZoom={onZoom}
        sendCmd={sendCmd}
        onCloneCell={onCloneCell}
        onDeleteCell={onDeleteCell}
        onPositionChange={onPositionChange}
      />) : (<DashboardEmpty
        dashboard={dashboard}
        onAddCell={()=>{}}
      />)
      }
    </div>
  }

  render(){
    // console.log('Dashboard render()')
    return <ConfigConsumer>{this.renderDashboard}</ConfigConsumer>
  }
}

Dashboard.propTypes = {
  layoutCols: PropTypes.number.isRequired,
  layoutRowHeight: PropTypes.number.isRequired,
  layoutMargin: PropTypes.number.isRequired,
  layoutContainerPadding: PropTypes.number.isRequired,
  dashboard: PropTypes.object.isRequired,
  inPresentationMode: PropTypes.bool.isRequired,
  onPositionChange: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired,
  onDeleteCell: PropTypes.func.isRequired,
  onCloneCell: PropTypes.func.isRequired,
  onAddCell: PropTypes.func.isRequired,
  sendCmd: PropTypes.func.isRequired,
  manualRefresh: PropTypes.number.isRequired,
  timeRange: PropTypes.object.isRequired,
  onZoom: PropTypes.func.isRequired,
  setScrollTop: PropTypes.func.isRequired,
  inView: PropTypes.func.isRequired,
}

// export default withSize({monitorHeight:true})(Dashboard)
export default Dashboard

