import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import Icon from 'antd/lib/icon';
import {getResultByChannel} from '../../selectors'; 
import CellBase from './CellBase';


class ShowGetContentCell extends CellBase{
    renderContent = ({getPrefixCls}) => {
        const {
          prefixCls: customizePrefixCls,
          get,
        } = this.props
        const prefixCls = getPrefixCls('ShowGetContent-cell', customizePrefixCls);
        return <div className={prefixCls}>
          <p>
          {!!get && JSON.stringify(get, null, "\n")}
          </p>
        </div>
      }
    }
    
    ShowGetContentCell.propTypes = {
      get: PropTypes.object
    }
    
    // reduxreact
    const mapStateToProps = (state, props) => {
      return {
        get: getResultByChannel(state, 'get'),
      }
    }
    // reduxreact
    const ConnectedShowGetContentCell = connect(
      mapStateToProps,
    )(ShowGetContentCell)
    
    ConnectedShowGetContentCell.NAME = 'GET'
    ConnectedShowGetContentCell.WIDTH =  12
    ConnectedShowGetContentCell.HEIGHT = 12
    // ConnectedShowGetContentCell.ICON = (props) => <Icon type={"codepen"} {...props} />
    // ConnectedExampleCell.CONFIG = config
    
    
    export default ConnectedShowGetContentCell

























