import { createSelector, createSelectorCreator } from 'reselect'
import memoize from 'lodash/memoize'
import isNumber from 'lodash/isNumber'
import isArray from 'lodash/isArray'
import isEqual from 'lodash/isEqual'

const hashFn = (...args) => {
  return args.reduce(
    (acc, val) => acc + '-' + JSON.stringify(val),
    ''
  )
}
export const hashEqualSelectorCreator = createSelectorCreator(memoize, hashFn)

export const getResultByCell = (state, props) => state.robot.resultsByChannel[props.cell.i]
export const getResultByChannel = (state, channel) => state.robot.resultsByChannel[channel]

export const getMemoizeResultByCell = hashEqualSelectorCreator(
  getResultByCell,
  (result)=>result
)

export const getMemoizeResultByChannel = hashEqualSelectorCreator(
  getResultByChannel,
  (result)=>result
)


export const getDataSources = (state, {cell}) => {
  const options = {...cell.options}
  const channel = options.channel === 'command'?cell.i:'get'
  const result = state.robot.resultsByChannel[channel]
  if(result){
    return Object.keys(result).filter(key=>{
      if(isNumber(result[key])){
        return true
      }
      if(isArray(result[key]) && isNumber(result[key][0])){
        return true
      }
      return false
    })
  }else{
    return []
  }
}

export const getMemoizeDataSources = hashEqualSelectorCreator(
  getDataSources,
  (result)=>result
)

export const getDataIndexesBySource = (state, {cell}) => {
  const options = {...cell.options}
  const channel = options.channel === 'command'?cell.i:'get'
  const result = state.robot.resultsByChannel[channel]
  const indexes = {}
  if(result){
    const sources = getDataSources(state, {cell})
    sources.forEach(source=>{
      if(isArray(result[source]) && isNumber(result[source][0])){
        indexes[source] = result[source].map((d,i)=>i)
      }
    })
  }
  return indexes
}

export const getMemoizeDataIndexesBySource = hashEqualSelectorCreator(
  getDataIndexesBySource,
  (result)=>result
)

export const getResultByCellChannel = (state, {cell}) => {
  if(cell.options.channel === 'command'){
    return {cell, result: getResultByChannel(state, cell.i)}
  } else{
    return {cell, result: getResultByChannel(state, "get")}
  }
}

export const makeGetResultsByCellChannel = (cacheSize=10000) => {
  let cachedResults = {}
  let cachedSources = null
  return createSelector(
    getResultByCellChannel,
    ({result, cell})=>{
      if(cell.options.paused){
        return cachedResults
      }
      if(!isEqual(cell.options.sources, cachedSources)){
        if(cell.options.sources){
          cachedSources = [...cell.options.sources]
        }else{
          cachedSources = []
        }
        cachedResults = {}
      }
      cachedResults = {...cachedResults}
      if(result){
        let d = new Date();
        let hr = d.getHours();
        let min = d.getMinutes();
        if (min < 10) {
          min = "0" + min;
        }
        let sec = d.getSeconds()
        if (sec<10){
          sec ="0"+sec
        }
        let name = d.toString()
        let time = `${hr}:${min}:${sec}`
        cachedSources.map(source=>{
          let key = source.source
          if(!!source.index || source.index === 0){
            key = `${key}_${source.index}`
          }
          if(!cachedResults[key]){
            cachedResults[key]=[]
          }
          if(isArray(result[source.source]) && isNumber(result[source.source][source.index])){
            cachedResults[key] = [...cachedResults[key], {
              name,
              value: [time, result[source.source][source.index]]
            }]
          }else if(isNumber(result[source.source])){
            cachedResults[key] = [...cachedResults[key], {
              name,
              value:[time, result[source.source]]
            }]
          }else{
            cachedResults[key] = [...cachedResults[key], {
              name,
              value:[time, 0]
            }]
          }
          let length = cachedResults[key].length;
          if(length > cacheSize){
            cachedResults[key].splice( 0, length - cacheSize);
          }
        })
      }
      return cachedResults
    }
  )
}

export const makeGetResultsByCell = (cacheSize=10000) => {
  let cachedResults = []
  return createSelector(
    getResultByCell,
    (result, cell)=>{
      console.log(cell)
      if(result){
        cachedResults.push(result)
      }
      let length = cachedResults.length;
      if(length > cacheSize){
        cachedResults.splice( 0, length - cacheSize);
      }
      return cachedResults
    }
  )
}

