export const makeGetResultsByChannel = (cacheSize=10000) => {
  let cachedResults = []
  return createSelector(
    getResultByCell,
    (result)=>{
      if(result){
        cachedResults.push(result)
      }
      let length = cachedResults.length;
      if(length > cacheSize){
        cachedResults.splice( 0, length - cacheSize);
      }
      return cachedResults
    }
  )
}

