import XMLJSONBase from "./XMLJSONBase";
import SyncManagerPoolObject from "./SyncManagerPoolObject";

export class EthercatSlave extends XMLJSONBase {
  constructor(uuid, phyId, vendorId, productCode, revisionNum, dcAssignActivate, syncManagerPoolObjects){
    super()
    this.uuid = uuid
    try{
      this.phyId = JSON.parse(phyId)
    }catch (e) {
      console.log(phyId)
      this.phyId = 0
      // console.error(e)
    }
    this.vendorId = this.vendorId
    this.productCode = this.productCode
    this.revisionNum = revisionNum
    this.dcAssignActivate = dcAssignActivate
    this.syncManagerPoolObjects = syncManagerPoolObjects
  }

  static FromXML = node => new EthercatSlave(
    Math.random().toString(36),
    node.getAttribute("phy_id"),
    node.getAttribute("vendor_id"),
    node.getAttribute("product_code"),
    node.getAttribute("revision_num"),
    node.getAttribute("dc_assign_activate"),
    [...node.getElementsByTagName("SyncManagerPoolObject")].filter(node=>node.nodeType === 1).map(SyncManagerPoolObject.FromXML)
  )

  static FromJSON = ({uuid, phyId, vendorId, productCode, revisionNum, dcAssignActivate, syncManagerPoolObjects}) => new EthercatSlave(uuid || Math.random().toString(36)
  , phyId, vendorId, productCode, revisionNum, dcAssignActivate, syncManagerPoolObjects && syncManagerPoolObjects.map(SyncManagerPoolObject.FromJSON))

  toXML = () => `<EthercatSlave ${EthercatSlave.ToXMLAttrString('phy_id', this.phyId)} ${EthercatSlave.ToXMLAttrString('vendor_id',this.vendorId)} ${EthercatSlave.ToXMLAttrString('product_code', this.productCode)} ${EthercatSlave.ToXMLAttrString('revision_num', this.revisionNum)} ${EthercatSlave.ToXMLAttrString('dc_assign_activate', this.dcAssignActivate)}> ${this.syncManagerPoolObjects? this.syncManagerPoolObjects.map(smpo=>smpo.toXML()).join(""):''}</EthercatSlave>`

  toJSON = () => ({
    uuid: this.uuid,
    isMotion: false,
    phyId: this.phyId,
    vendorId: this.vendorId,
    productCode: this.productCode,
    revisionNum: this.revisionNum,
    dcAssignActivate: this.dcAssignActivate,
    syncManagerPoolObjects: this.syncManagerPoolObjects && this.syncManagerPoolObjects.map(smpo=>smpo.toJSON()),
  })
}

export default EthercatSlave