import React, {Component} from 'react';
import Dashboard from "../components/Dashboard";
import { connect } from 'react-redux'
import {
  getDashboard,
  getDashboardCells,
  getLayoutCols, getLayoutContainerPadding,
  getLayoutMargin,
  getLayoutRowHeight
} from "../reducers/selectors";
import {updateDashboard, createCell, deleteCell, sendCmd} from "../actions";
import PropTypes from "prop-types";
import {getCurrentDashboardAndCells} from "../selectors";

class DashboardPage extends Component {

  onPositionChange = (cells) =>{
    //Returndashboardcells
    let {dashboard, updateDashboard} = this.props
    dashboard = {...dashboard, cells:[...cells]}
    updateDashboard(dashboard)
  }
  onCellUpdate = (cell) => {
    //Returndashboardcells
    let {dashboard, updateDashboard} = this.props
    let cells = [...dashboard.cells]
    cells.forEach((c, i)=>{
      if(cell.i === c.i){
        cells[i] = {...cell}
      }
    })
    dashboard = {...dashboard, cells}
    updateDashboard(dashboard)
  }
  onAddCell = (cell) => {
    console.log(cell)
    cell = {...cell}
    delete cell['i']
    this.props.createCell(this.props.dashboard.i, cell)
  }
  onDeleteCell = (cellId) => {
    this.props.deleteCell(this.props.dashboard.i, cellId)
  }
  render(){
    // console.log('DashboardPage render()')
    const {
      dashboard,
      cells,
      layoutCols,
      layoutRowHeight,
      layoutMargin,
      layoutContainerPadding,
      sendCmd,
    } = this.props
    return <Dashboard
      dashboard={dashboard}
      layoutCols={layoutCols}
      layoutRowHeight={layoutRowHeight}
      layoutMargin={layoutMargin}
      layoutContainerPadding={layoutContainerPadding}

      inPresentationMode={false}
      onPositionChange={this.onPositionChange}
      onCellUpdate={this.onCellUpdate}
      onDeleteCell={this.onDeleteCell}
      onCloneCell={this.onAddCell}
      onAddCell={this.onAddCell}
      sendCmd={sendCmd}
      onSummonOverlayTechnologies={()=>{}}
      manualRefresh={0}
      timeRange={{}}
      onZoom={()=>{}}
      setScrollTop={()=>{}}
      inView={()=>{}}
    />
  }
}

const makeMapStateToProps = () => {

}

function mapStateToProps(state, props) {
  const {match:{params:{id}}} = props
  return {
    dashboard: getCurrentDashboardAndCells(state),
    layoutCols: getLayoutCols(state),
    layoutRowHeight: getLayoutRowHeight(state),
    layoutMargin: getLayoutMargin(state),
    layoutContainerPadding: getLayoutContainerPadding(state)
  }
}


Dashboard.propTypes = {
  dashboard: PropTypes.object.isRequired,
  // cells: PropTypes.array.isRequired,
  // inPresentationMode: PropTypes.bool.isRequired,
  // onPositionChange: PropTypes.func.isRequired,
  // onDeleteCell: PropTypes.func.isRequired,
  // onCloneCell: PropTypes.func.isRequired,
  // onAddCell: PropTypes.func.isRequired,
  // onSummonOverlayTechnologies: PropTypes.func.isRequired,
  // manualRefresh: PropTypes.number.isRequired,
  // timeRange: PropTypes.object.isRequired,
  // onZoom: PropTypes.func.isRequired,
  // setScrollTop: PropTypes.func.isRequired,
  // inView: PropTypes.func.isRequired,
  layoutCols: PropTypes.number.isRequired,
  layoutRowHeight: PropTypes.number.isRequired,
  layoutMargin: PropTypes.number.isRequired,
  layoutContainerPadding: PropTypes.number.isRequired,
}

export default connect(
  mapStateToProps,
  {
    updateDashboard,
    sendCmd,
    createCell,
    deleteCell
  }
)(DashboardPage)
