import { createSelector } from 'reselect'
import { getPathname } from "./router";



const getFullDashboards = (state) => state.config.dashboards

export const getDashboards = createSelector(
  getFullDashboards,
  dashboards=> dashboards && dashboards.map(d=>({
    name: d.name,
    i: d.i,
    editable: d.editable
  }))
)

export const getDashboardAndCells = (state, id) => {
  const dashboards = getFullDashboards(state)
  if (dashboards) {
    return dashboards.filter(d=>d.i === id)[0]
  }
}

export const makeGetDashboard = (id) =>{
  return createSelector(
    getDashboards,
    dashboards=>dashboards && dashboards.filter(d=>d.i === id)[0]
  )
}

export const getCurrentDashboardAndCells = createSelector(
  getFullDashboards,
  getPathname,
  (dashboards, pathname) => {
    if(pathname !== '/' && pathname.indexOf('/dashboards/') !== -1){
      const id = pathname.replace("/dashboards/", "")
      const dashboard = dashboards && dashboards.filter(d=>d.i === id)[0]
      if(dashboard) {
        return dashboard
      }
    }
    return dashboards && dashboards[0]
  }
)

export const getCurrentDashboardId = createSelector(
  getDashboards,
  getPathname,
  (dashboards, pathname) => {
    if(pathname !== '/' && pathname.indexOf('/dashboards/') !== -1){
      const id = pathname.replace("/dashboards/", "")
      const dashboard = dashboards && dashboards.filter(d=>d.i === id)[0]
      if(dashboard) {
        return dashboard.i
      }
    }
    return dashboards && dashboards[0].i
  }
)

export const getCurrentDashboard = createSelector(
  getDashboards,
  getPathname,
  (dashboards, pathname) => {
    if(pathname !== '/' && pathname.indexOf('/dashboards/') !== -1){
      const id = pathname.replace("/dashboards/", "")
      const dashboard = dashboards && dashboards.filter(d=>d.i === id)[0]
      if(dashboard) {
        return dashboard
      }
    }
    return dashboards && dashboards[0]
  }
)

export const getCreatingCellStatus = state => ({
  showCreateCellModal: state.config.showCreateCellModal,
  creatingCell: state.config.creatingCell,
  creatingCellFailure: state.config.creatingCellFailure,
  creatingCellSuccess: state.config.creatingCellSuccess,
})
