export const getConfig = ({config}) => config

export const getDashboards = ({config}) => config && config.dashboards

export const getDashboard  = ({config, router:{pathname}}) => {
  if(config && config.dashboards){
    if(pathname === '/' || pathname.indexOf('/dashboards/') === -1){
      return config.dashboards[0]
    }else{
      let id = pathname.replace("/dashboards/", "")
      let fds = config.dashboards.filter(d=>d.i === id)
      if(fds.length>0){
        return fds[0]
      }else{
        return config.dashboards[0]
      }
    }
  }
}


export const getDashboardCells = (state, id) => {
  let dashboard = getDashboard(state, id)
  if(!dashboard){
    return []
  }else{
    return dashboard.cells || []
  }
}

export const getPartPQ = ({robot}) => robot.resultsByChannel && robot.resultsByChannel.get && robot.resultsByChannel.get.part_pq
export const getPartPE = ({robot}) => robot.resultsByChannel && robot.resultsByChannel.get && robot.resultsByChannel.get.part_pe
export const getMotionPos = ({robot}) => robot.resultsByChannel && robot.resultsByChannel.get && robot.resultsByChannel.get.motion_pos

export const getStateCode = ({robot}) => robot.resultsByChannel && robot.resultsByChannel.get && robot.resultsByChannel.get.state_code

export const getModel = ({config:{model}}) => model

export const getLayoutCols = ({config})=>config && config.layoutConfig && config.layoutConfig.cols
export const getLayoutRowHeight = ({config})=>config && config.layoutConfig && config.layoutConfig.rowHeight
export const getLayoutMargin = ({config})=>config && config.layoutConfig && config.layoutConfig.margin
export const getLayoutContainerPadding = ({config})=>config && config.layoutConfig && config.layoutConfig.containerPadding
