import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import Unity, { UnityContent } from "react-unity-webgl";
import throttle from 'lodash/throttle'
import {loadConfigXml, deleteResultByChannel} from "../../actions"

import {getMemoizeUnityPartPQString, getUnityUpdateInterval, getXml} from "../../selectors";

class View3D extends Component {
  constructor(props){
    super(props)
    this.unityContent = new UnityContent(
      "/unity/8.json",
      "/unity/UnityLoader.js"
    );
    this.state = {}
    this.unityContent.on("progress", progression => {
      this.setState({
        progression: progression
      });
      if(progression === 1){
        this.unityContent.send("empt", "GetUrlFromJs", window.location.origin)
        if(this.props.xml){
          this.unityContent.send("empt", "GetRobotXmlFromJS", this.props.xml)
        }
        if(this.props.partPQ){
          this.unityContent.send("empt", "ReceiveDataFromJs", this.props.partPQ)
        }
        this.unityContent.send("empt", "SetCameraBackGround", "0&0&0")
        this.unityContent.send("empt", "SetFPS", "20");
      }
    });
    this.throttleSend = throttle(this.unityContent.send.bind(this.unityContent), this.props.unityUpdateInterval)
  }

  componentWillMount() {
    this.props.deleteResultByChannel('xml')
    this.props.loadConfigXml()
  }

  componentWillUnmount() {
    this.unityContent.remove("progress")
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    if(nextProps.xml !== this.props.xml && nextProps.xml){
      if(this.state.progression === 1){
        this.unityContent.send("empt", "GetRobotXmlFromJS", nextProps.xml)
      }
    }
    if(nextProps.partPQ !== this.props.partPQ && nextProps.partPQ){
      if(this.state.progression === 1){
        setTimeout(()=>{
          this.throttleSend("empt", "ReceiveDataFromJs", nextProps.partPQ)
        },0)
      }
    }
    return false
  }

  unityProgress(gameInstance, progress) {
    if (!gameInstance.Module) {
      return;
    }
    this.setState({progress: gameInstance.progress})
  }

  componentDidMount() {
    // console.error('View3D componentDidMount()')
  }

  renderLogger = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('view3d', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
      ref={ref=>this.container=ref}
    >
      <Unity unityContent={this.unityContent} />
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderLogger}</ConfigConsumer>
  }
}

View3D.propTypes = {
  cell: PropTypes.object.isRequired,
  xml: PropTypes.string,
}

function mapStateToProps(state) {
  return {
    xml: getXml(state),
    partPQ: getMemoizeUnityPartPQString(state),
    unityUpdateInterval: getUnityUpdateInterval(state)
  }
}

const ConnectedView3D =  connect(
  mapStateToProps,
  {
    loadConfigXml,
    deleteResultByChannel,
  }
)(View3D)

ConnectedView3D.NAME = ''
export default ConnectedView3D

