import React, {Component} from "react"
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import {sendCmd} from "../../../actions"
import Button from "antd/lib/button"
import Select from "antd/lib/select"
import ResultError from "../../../components/ResultError";
const Option = Select.Option

class HeaderBar extends Component {
  state = {
    selectedVendor: null,
    selectedDevice: null,
  }
  render() {
    const {
      prefixCls,
      sendCmd,
      cell,
      selectedVendor,
      selectedDevice,
      selectDevice,
      selectVendor,
    } = this.props
    const {vendors} = this.props
    const vendorSelectProps = {
      size: "small",
      style: {width: 200},
      placeholder: "",
      onChange: (id) => {
        selectVendor(id)
      },
    }
    if(selectVendor && selectVendor.id){
      vendorSelectProps['value']=selectVendor.id
    }
    const deviceSelectProps = {
      size: "small",
      style: {width: 200},
      placeholder: "",
      onChange: (name) => {
        selectDevice(name)
      },
    }
    if(selectDevice && selectDevice.name){
      deviceSelectProps['value'] = selectDevice.name
    }
    return <div className={`${prefixCls}-header-bar`}>
      {vendors &&
      <Select
        {...vendorSelectProps}
      >
        {vendors.map(vendor=> {
          return <Option value={vendor.id} key={vendor.id}>{vendor.name}</Option>
        })}
      </Select>
      }
      {vendors && selectedVendor && selectedVendor.devices &&
      <Select
        {...deviceSelectProps}
      >
        {selectedVendor.devices.map((device, index)=>{
          return <Option value={device.name} key={index}>{device.name}</Option>
        })}
      </Select>
      }
      <Button onClick={this.props.save} type={"primary"}></Button>
    </div>
  }
}

HeaderBar.propTypes = {
  prefixCls: PropTypes.string.isRequired,
  cell: PropTypes.object.isRequired,
  vendors: PropTypes.array.isRequired,
}



const getState = createSelector(
  ()=>{},
  (
    result
  ) => {
    return {
      result
    }
  }
)

function mapStateToProps(state, props) {
  return getState(state, props)
}

export default connect(
  mapStateToProps,
  {
    sendCmd,
  }
)(HeaderBar)


