import React, {Component} from 'react'
import ReactGridLayout, {WidthProvider} from 'react-grid-layout'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Layout from "../components/Layout";
import windowSize from 'react-window-size';


const GridLayout = WidthProvider(ReactGridLayout)

class LayoutRenderer extends Component {
  componentDidMount() {
    // console.error("LayoutRenderer componentDidMount")
  }

  handleLayoutChange = (layout) => {
    if (!this.props.onPositionChange) {
      return
    }
    const self = this
    let changed = false
    const newCells = self.props.cells.map(cell => {
      const l = layout.find(ly => {
        return ly.i == cell.i
      })
      if(!l){
        return
      }
      if (
        cell.x !== l.x ||
        cell.y !== l.y ||
        cell.h !== l.h ||
        cell.w !== l.w
      ) {
        changed = true
      }
      const newLayout = {
        x: l.x,
        y: l.y,
        h: l.h,
        w: l.w,
      }
      return {
        ...cell,
        ...newLayout,
      }
    })

    if (changed) {
      this.props.onPositionChange(newCells)
    }
  }

  renderLayoutRender = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cells,
      onZoom,
      isEditable,
      onDeleteCell,
      onCloneCell,
      sendCmd,
      onCellUpdate,
      layoutCols,
      layoutMargin,
      layoutContainerPadding,
      layoutRowHeight,
      windowWidth,
      windowHeight,
    } = this.props
    const prefixCls = getPrefixCls('layout-renderer', customizePrefixCls)
    const draggableCls = `.${getPrefixCls('layout-cell', customizePrefixCls)}--draggable`
    let rowHeight = Math.floor(windowWidth/layoutCols)
    let rowCount = Math.floor((windowHeight-50-40)/rowHeight)
    // if(rowCount <= 1){
    //   rowCount = 1
    // }
    // rowHeight = Math.floor((windowHeight-50-40)/rowCount)
    return <div className={`${prefixCls} ${isEditable?'editable':""}`}>
      <GridLayout
        layout={cells}
        cols={layoutCols}
        rowHeight={rowHeight}
        margin={[layoutMargin, layoutMargin]}
        containerPadding={[layoutContainerPadding, layoutContainerPadding]}
        useCSSTransforms={false}
        onLayoutChange={this.handleLayoutChange}
        draggableHandle={draggableCls}
        isDraggable={true}
        isResizable={true}
      >
        {cells.map(cell=>(<div
          key={cell.i}
          className={isEditable?"editable":""}
        >
          <Layout
            cell={cell}
            onDeleteCell={onDeleteCell}
            onCloneCell={onCloneCell}
            onCellUpdate={onCellUpdate}
            onZoom={onZoom}
            sendCmd={sendCmd}
            isEditable={isEditable}
          />
        </div>))}
      </GridLayout>
    </div>
  }

  render(){
    // console.log('LayoutRenderer render()')
    return <ConfigConsumer>{this.renderLayoutRender}</ConfigConsumer>
  }
}

LayoutRenderer.propTypes = {
  cells: PropTypes.array.isRequired,
  layoutCols: PropTypes.number.isRequired,
  windowWidth: PropTypes.number.isRequired,
  windowHeight: PropTypes.number.isRequired,
  layoutRowHeight: PropTypes.number.isRequired,
  layoutMargin: PropTypes.number.isRequired,
  layoutContainerPadding: PropTypes.number.isRequired,
  isEditable: PropTypes.bool.isRequired,
  onZoom: PropTypes.func.isRequired,
  sendCmd: PropTypes.func.isRequired,
  onCloneCell: PropTypes.func.isRequired,
  onDeleteCell: PropTypes.func.isRequired,
  onPositionChange: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired,
}

export default windowSize(LayoutRenderer)