import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import LayoutCellMenu from "./LayoutCellMenu";
import LayoutCellHeader from "./LayoutCellHeader";

class LayoutCell extends Component {

  renderLayoutCell = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      isEditable,
      cell,
      onCloneCell,
      onCellUpdate,
      onDeleteCell,
      children,
    } = this.props
    const self = this
    const prefixCls = getPrefixCls('layout-cell', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
      ref={(ref)=>{self.container=ref}}
    >
      <LayoutCellMenu
        cell={cell}
        isEditable={isEditable}
        dataExists={false}
        onClone={onCloneCell}
        onDelete={onDeleteCell}
        onCellUpdate={onCellUpdate}
        getPopupContainer={()=>{
          return self.container
        }}
      />
      <LayoutCellHeader cell={cell} cellName={cell.name} isEditable={isEditable} />
      <div className={`${prefixCls}-body`}>{children}</div>
    </div>
  }

  render(){
    // console.log('LayoutCell render()')
    return <ConfigConsumer>{this.renderLayoutCell}</ConfigConsumer>
  }
}

LayoutCell.propTypes = {
  cell: PropTypes.object.isRequired,
  children: PropTypes.node.isRequired,
  onDeleteCell: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired,
  onCloneCell: PropTypes.func.isRequired,
  isEditable: PropTypes.bool.isRequired,
}

export default LayoutCell