import {
  put,
  call,
  fork,
  select,
  delay,
  takeLatest,
} from 'redux-saga/effects'
import { api } from '../services'
import * as actions from '../actions'
import {
  programsLoad,
  programLoad,
  programCreate,
  programDelete,
  programUpdate,
  programRename
} from "../actions";
const {
  robotModelPartDelete,
  robotModelDelete,
  robotModelCreate,
  robotModels,
  esiPath
} = actions

function* fetchRobotModels() {
  yield put( robotModels.request() )
  const {response, error} = yield call(api.fetchRobotModels)
  if(response)
    yield put( robotModels.success(response) )
  else
    yield put( robotModels.failure(error) )
}

function* fetchESIPath() {
  yield put(esiPath.request())
  const {response, error} = yield call(api.fetchESIPath)
  if(response)
    yield put(esiPath.success(response.path))
  else
    yield put(esiPath.failure( error ))
}

function* fetchPrograms() {
  yield put(programsLoad.request())
  const {response, error} = yield call(api.fetchPrograms)
  if(response)
    yield put(programsLoad.success(response))
  else
    yield put(programsLoad.failure( error ))
}

function* fetchProgram({name}) {
  yield put(programLoad.request())
  const {response, error} = yield call(api.fetchProgram, name)
  if(response)
    yield put(programLoad.success(response))
  else
    yield put(programLoad.failure( error ))
}

function* createProgram({name}) {
  yield put(programCreate.request())
  const {response, error} = yield call(api.createProgram, name)
  if(response)
    yield put(programCreate.success(response))
  else
    yield put(programCreate.failure( error ))
}

function* updateProgram({name, program}) {
  yield delay(500)
  yield put(programUpdate.request(name, program))
  const {response, error} = yield call(api.updateProgram, name, program)
  if(response)
    yield put(programUpdate.success(response))
  else
    yield put(programUpdate.failure(error))
}

function* deleteProgram({name}) {
  yield put(programDelete.request())
  const {response, error} = yield call(api.deleteProgram, name)
  if(response)
    yield put(programDelete.success(response))
  else
    yield put(programDelete.failure(error))
}

function* renameProgram({name, newName, copy}) {
  yield put(programRename.request())
  const {response, error} = yield call(api.renameProgram, name, newName, copy)
  if(response)
    yield put(programRename.success(name, response, copy))
  else
    yield put(programRename.failure(error))
}

function* deleteRobotModelPart({brand, model, part}) {
  yield put(robotModelPartDelete.request())
  const {response, error} = yield call(api.deleteRobotModelPart, brand, model, part)
  if(response)
    yield put( robotModelPartDelete.success(response) )
  else
    yield put( robotModelPartDelete.failure(error) )
}

function* deleteRobotModel({brand, model}) {
  yield put(robotModelDelete.request())
  const {response, error} = yield call(api.deleteRobotModel, brand, model)
  if(response)
    yield put( robotModelDelete.success(response) )
  else
    yield put( robotModelDelete.failure(error) )
}

function* createRobotModel({model}) {
  yield put(robotModelCreate.request())
  const {response, error} = yield call(api.createRobotModel, model)
  if(response)
    yield put( robotModelCreate.success(response) )
  else
    yield put( robotModelCreate.failure(error) )
}

function* watchLoadRobotModels() {
  yield takeLatest(actions.LOAD_ROBOT_MODELS, fetchRobotModels)
}

function* watchDeleteRobotModelPart() {
  yield takeLatest(actions.DELETE_ROBOT_MODEL_PART, deleteRobotModelPart)
}

function* watchDeleteRobotModel() {
  yield takeLatest(actions.DELETE_ROBOT_MODEL, deleteRobotModel)
}

function* watchCreateRobotModel() {
  yield takeLatest(actions.CREATE_ROBOT_MODEL, createRobotModel)
}

function* watchLoadESIPath() {
  yield takeLatest(actions.LOAD_ESI_PATH, fetchESIPath)
}

function* watchLoadPrograms() {
  yield takeLatest(actions.LOAD_PROGRAMS, fetchPrograms)
}

function* watchLoadProgram() {
  yield takeLatest(actions.LOAD_PROGRAM, fetchProgram)
}

function* watchCreateProgram() {
  yield takeLatest(actions.CREATE_PROGRAM, createProgram)
}

function* watchUpdateProgram() {
  yield takeLatest(actions.UPDATE_PROGRAM, updateProgram)
}

function* watchDeleteProgram() {
  yield takeLatest(actions.DELETE_PROGRAM, deleteProgram)
}

function* watchRenameProgram() {
  yield takeLatest(actions.RENAME_PROGRAM, renameProgram)
}

export default [
  fork(watchLoadRobotModels),
  fork(watchDeleteRobotModelPart),
  fork(watchDeleteRobotModel),
  fork(watchCreateRobotModel),
  fork(watchLoadESIPath),
  fork(watchLoadPrograms),
  fork(watchLoadProgram),
  fork(watchCreateProgram),
  fork(watchUpdateProgram),
  fork(watchDeleteProgram),
  fork(watchRenameProgram),
]
