import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types"
import Row from "antd/lib/row"
import Col from "antd/lib/col"
import Button from "antd/lib/button"
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import Select from "antd/lib/select"
import Modal from "antd/lib/modal"
import Input from "antd/lib/input"
import CellBase from "../CellBase"
import Icon from "antd/lib/icon"
import Collapse from "antd/lib/collapse"
import {
  getPrograms,
  getResultByCell,
  getCreatingProgramStatus,
  getDeletingProgramStatus,
  getEditingProgramNameStatus,
} from "../../../selectors"
import {
  deleteResultByChannel,
  loadPrograms,
  loadProgram,
  createProgram,
  toggleCreateProgramModal,
  deleteProgram,
  toggleDeleteProgramModal,
  toggleEditProgramName,
  renameProgram,
  resetRenameProgramStatus,
} from "../../../actions"
import "./blocks/index"
import "./generators/index"
import Editors from "./Editors";
import { withSize } from 'react-sizeme'
import { ResizableBox } from 'react-resizable';
import Tooltip from 'antd/lib/tooltip'
import CreateProgramModal from "./CreateProgramModal";
import DeleteProgramModal from "./DeleteProgramModal";



const { Panel } = Collapse

class Blockly extends CellBase {
  state = {
    selectedProgram: null,
  }

  componentWillMount() {
    this.props.loadPrograms()
  }

  componentWillUnmount() {
    clearTimeout(this.timeout)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if(prevProps.creatingProgramStatus.creatingProgramSuccess !== this.props.creatingProgramStatus.creatingProgramSuccess && this.props.creatingProgramStatus.creatingProgramSuccess){
      let options = this.getOptions()
      let name = this.props.creatingProgramStatus.creatingProgramSuccess.name
      if (!!options.selectedProgramName) {
        this.updateOptions({...options, selectedProgramName: null})
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          this.updateOptions({...options, selectedProgramName: name})
        }, 50)
      } else {
        this.updateOptions({...options, selectedProgramName: name})
      }
    }
    if(prevProps.editingProgramNameStatus.renamingProgramSuccess !== this.props.editingProgramNameStatus.renamingProgramSuccess && this.props.editingProgramNameStatus.renamingProgramSuccess){
      let options = this.getOptions()
      let name = this.props.editingProgramNameStatus.renamingProgramSuccess.name
      if (!!options.selectedProgramName) {
        this.updateOptions({...options, selectedProgramName: null})
        clearTimeout(this.timeout)
        this.timeout = setTimeout(() => {
          this.updateOptions({...options, selectedProgramName: name})
        }, 50)
      } else {
        this.updateOptions({...options, selectedProgramName: name})
      }
    }
  }

  run = () => {
    if(this.editors){
      this.editors.run()
    }
  }

  renderPrograms(prefixCls) {
    const {
      programs,
      createProgram,
      creatingProgramStatus,
      toggleCreateProgramModal,
      deleteProgram,
      deletingProgramStatus,
      toggleDeleteProgramModal,
      toggleEditProgramName,
      renameProgram,
      resetRenameProgramStatus,
      editingProgramNameStatus: {
        renamingProgramFailure,
        editingProgramName
      }
    } = this.props
    const options = this.getOptions()
    return <Fragment>
      <div className={`${prefixCls}-program-header`}>
       
        <Tooltip placement="top" title={''}>
          <Icon
            type="plus-square" theme="filled"
            className={`${prefixCls}-program-header-add`}
            onClick={toggleCreateProgramModal}
          />
        </Tooltip>
        <CreateProgramModal
          createProgram={createProgram}
          toggleCreateProgramModal={toggleCreateProgramModal}
          creatingProgramStatus={creatingProgramStatus}
          prefixCls={prefixCls}
        />
      </div>
      {Object.keys(programs).length === 0 &&
       <div style={{opacity: 0.5}}>
         
       </div>
      }
      {Object.keys(programs).length > 0 && <div className={`${prefixCls}-program-collapse`}>
      <Collapse
        accordion
        activeKey={options.selectedProgramName}
      >
        {Object.keys(programs).sort().map(name => {
          const header = <div
            className={`${prefixCls}-program-name`}
            onClick={()=>{
              if (!!options.selectedProgramName && options.selectedProgramName !== name) {
                this.updateOptions({...options, selectedProgramName: null})
                clearTimeout(this.timeout)
                this.timeout = setTimeout(() => {
                  this.updateOptions({...options, selectedProgramName: name})
                }, 50)
              }else{
                this.updateOptions({...options, selectedProgramName: name})
              }
            }}
          >
            {editingProgramName !== name &&
            <span className={`${prefixCls}-program-name-text`} onDoubleClick={(e)=>{
              e.preventDefault()
              e.stopPropagation()
              toggleEditProgramName(name)
            }}>
              {name}
            </span>
            }
            {editingProgramName === name && <Input
              size={"small"}
              onKeyUp={(e)=>{
                if(e.keyCode === 27){
                  e.preventDefault()
                  e.stopPropagation()
                  toggleEditProgramName()
                }
              }}
              onClick={(e)=>{
                e.preventDefault()
                e.stopPropagation()
              }}
              className={`${prefixCls}-program-name-input`}
              defaultValue={name}
              onPressEnter={(e)=>{
                if(name !== e.target.value){
                  renameProgram(name, e.target.value)
                }else{
                  toggleEditProgramName()
                }
              }}
            />
            }
            {editingProgramName !== name &&
            <span className={`${prefixCls}-program-name-actions`}>
              <Icon
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  toggleEditProgramName(name)
                }}
                type="edit" theme="filled"
              />
              <Icon
                type="copy"
                theme="filled"
                onClick={(e)=>{
                  e.preventDefault()
                  e.stopPropagation()
                  let newName = name + ' copy'
                  let getCopyName = false
                  while(!getCopyName){
                    getCopyName = true
                    Object.keys(programs).forEach(key=>{
                      if(key === newName){
                        newName += ' copy'
                        getCopyName = false
                      }
                    })
                  }
                  renameProgram(name, newName, true)
                }}
              />
              <Icon
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  toggleDeleteProgramModal(name)
                }}
                type="delete"
                theme="filled"
              />
            </span>
            }
          </div>
          return <Panel header={header} key={name}
          >
            {programs[name].files && programs[name].files.map(file => {
              return <p key={file.name}>{file.name}</p>
            })}
          </Panel>
        })}
      </Collapse>
        </div>
      }
      <DeleteProgramModal
        deleteProgram={deleteProgram}
        toggleDeleteProgramModal={toggleDeleteProgramModal}
        deletingProgramStatus={deletingProgramStatus}
        prefixCls={prefixCls}
      ></DeleteProgramModal>
      { !!editingProgramName && !!renamingProgramFailure &&
        <Modal
          title=""
          visible={!!editingProgramName && !!renamingProgramFailure}
          onCancel={resetRenameProgramStatus}
          onOk={resetRenameProgramStatus}
          okText={""}
          cancelButtonProps={{style: {display: 'none'}}}
        >
          <div className={"error"}>
            {!!renamingProgramFailure && renamingProgramFailure}
          </div>
        </Modal>
      }
      { !editingProgramName && !!renamingProgramFailure &&
      <Modal
        title=""
        visible={!editingProgramName && !!renamingProgramFailure}
        onCancel={resetRenameProgramStatus}
        onOk={resetRenameProgramStatus}
        okText={""}
        cancelButtonProps={{style: {display: 'none'}}}
      >
        <div className={"error"}>
          {!!renamingProgramFailure && renamingProgramFailure}
        </div>
      </Modal>
      }
    </Fragment>
  }

  renderContent = ({getPrefixCls}) => {
    const self = this
    const {
      prefixCls: customizePrefixCls,
      cell,
      size: {width, height},
      programs,
    } = self.props
    const {
      running,
    } = this.state
    const prefixCls = getPrefixCls('blockly', customizePrefixCls);
    this.prefixCls = prefixCls
    const options = this.getOptions()
    const sidebarWidth = options.sidebarWidth || 200
    let selectedProgram = programs[options.selectedProgramName]
    return <div
      className={`${prefixCls}`}
    >
      {(!options.hideSidebar || !selectedProgram) && <ResizableBox
        width={sidebarWidth}
        height={height}
        minConstraints={[100, height]}
        maxConstraints={[width/2, height]}
        className={`${prefixCls}-left-col`}
        axis={'x'}
        handle={<div className={`${prefixCls}-hdivider`}></div>}
        onResize={(e, {size:{width, height}})=>{
          self.updateOptions({...options, sidebarWidth: width})
        }}
      >
        {this.renderPrograms(prefixCls)}
      </ResizableBox>
      }
      <div
        className={`${prefixCls}-right-col`}
        style={{
          width: !options.hideSidebar?width-sidebarWidth:width
        }}
      >
        {!!selectedProgram && <Editors
          program={selectedProgram}
          cell={cell}
          prefixCls={prefixCls}
          updateOptions={(options)=>this.updateOptions(options)}
          options={this.getOptions()}
        ></Editors>
        }
        {!selectedProgram && <div
          className={`${prefixCls}-no-program`}
        >
          <span>, .</span>
        </div>}
      </div>
    </div>
  }
}

Blockly.propTypes = {
}

const getBlocklyState = createSelector(
  getPrograms,
  getResultByCell,
  getCreatingProgramStatus,
  getDeletingProgramStatus,
  getEditingProgramNameStatus,
  (programs, result, creatingProgramStatus, deletingProgramStatus, editingProgramNameStatus) => {
    return {
      programs,
      result,
      creatingProgramStatus,
      deletingProgramStatus,
      editingProgramNameStatus,
    }
  }
)

function mapStateToProps(state, props) {
  return getBlocklyState(state, props)
}

const ConnectedBlockly = connect(
  mapStateToProps,
  {
    deleteResultByChannel,
    loadPrograms,
    loadProgram,
    toggleCreateProgramModal,
    createProgram,
    toggleDeleteProgramModal,
    deleteProgram,
    toggleEditProgramName,
    renameProgram,
    resetRenameProgramStatus,
  }
)(withSize({monitorHeight: true, refreshRate: 30})(Blockly))

ConnectedBlockly.NAME = ''
ConnectedBlockly.WIDTH = 24
ConnectedBlockly.HEIGHT = 12
ConnectedBlockly.ICON = (props) => <Icon type={"codepen"} {...props} />


export default ConnectedBlockly
