import React, {Component} from 'react';
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import Select from "antd/lib/select"
import Checkbox from "antd/lib/checkbox"
import throttle from "lodash/throttle"
import {
  updateMotionVelPercent,
  updateToolId,
  updateWorkObjectId,
  updateCoordinateId,
  updateDashboard,
  createCell,
  navigate,
  sendCmd,
  toggleCreateCellModal
} from '../actions'

import {
  getCommandSendInterval,

  getWorkObjectIds,
  getToolIds,
  getCurrentMotionVelPercent,

  getCurrentWorkObjectId,
  getCurrentCoordinateId,
  getCurrentToolId,
  getDashboards,
  getPathname,
  getCreatingCellStatus
} from "../selectors";

import {ConfigConsumer} from 'antd/lib/config-provider'
import Menu from 'antd/lib/menu';
import Icon from 'antd/lib/icon';
import Slider from 'antd/lib/slider'
import InputNumber from 'antd/lib/input-number'
import CreateCellModal from "../components/CreateCellModal";


const MenuItem = Menu.Item
const Option = Select.Option
const OptGroup = Select.OptGroup



class Header extends Component {
  state = {
    fullscreen: false
  }
  escFunction = (event) =>{
    if(event.keyCode === 27) {
      if(!!document.fullscreenElement){
        document.exitFullscreen()
      }
    }
  }
  onFullscreenChange = (event) => {
    if(!!document.fullscreenElement){
      this.setState({fullscreen: true})
    }else{
      this.setState({fullscreen: false})
    }
  }
  componentDidMount(){
    const {interval, sendCmd} = this.props
    this.throttleSendCmd = throttle(sendCmd, interval)
    document.addEventListener("fullscreenchange", this.onFullscreenChange, false)
    document.addEventListener("keydown", this.escFunction, false);
  }
  componentWillUnmount(){
    document.removeEventListener("keydown", this.escFunction, false);
    document.removeEventListener("fullscreenchange", this.onFullscreenChange, false)
  }

  handleClick = e => {
    this.props.navigate(e.key)
  }

  createCell = (cell) => {
    let {createCell, pathname, dashboard} =  this.props
    if(dashboard){
      createCell(dashboard.i, cell)
    }
  }

  toggleLock = () => {
    let {updateDashboard, dashboard} = this.props
    if(dashboard){
      dashboard = {...dashboard, editable: !dashboard.editable}
      updateDashboard(dashboard)
    }
  }

  renderContent = ({getPrefixCls})=>{
    let {
      prefixCls: customizePrefixCls,
      pathname,
      dashboards,
      dashboard,

      workObjectIds,
      toolIds,

      updateMotionVelPercent,
      updateToolId,
      updateCoordinateId,
      updateWorkObjectId,

      toolId,
      coordinateId,
      workObjectId,

      motionVelPercent,
      toggleCreateCellModal,
      creatingCellStatus
    } = this.props
    const prefixCls = getPrefixCls('header', customizePrefixCls)
    if(pathname === '/' && dashboards.length > 0){
      pathname = `/dashboards/${dashboards[0].i}`
    }
    return <div
      className={prefixCls}
    >

      <Menu
        onClick={this.handleClick}
        selectedKeys={[pathname]}
        mode="horizontal"
      >
        {dashboards.map(d=>(
          <MenuItem key={`/dashboards/${d.i}`}>
            {d.name}
          </MenuItem>
        ))}
      </Menu>
      <div
        className={`${prefixCls}-actions`}
        style={{
          position: 'absolute',
          right: 0,
          top: 0,
          display: 'inline-block'
        }}
      >
        <Slider
          tooltipVisible={false}
          min={0}
          max={100}
          tipFormatter={(value)=>`${value}%`}
          value={motionVelPercent}
        onChange={(value)=>{
            this.throttleSendCmd(`setvel --vel_percent=${value}`, 'setvel')
            updateMotionVelPercent(value)
          }}
        />
        <InputNumber
          min={0}
          max={100}
          size={"small"}
          value={motionVelPercent}
          onChange={(value)=>{
            this.throttleSendCmd(`setvel --vel_percent=${value}`, 'setvel')
            updateMotionVelPercent(value)
          }}
        />
        <Checkbox
          checked={!!coordinateId}
          onChange={(e)=>{
            updateCoordinateId(e.target.checked?1:0)
          }}
        ></Checkbox>
        <Select
          size={"small"}
          placeholder={""}
          onChange={updateToolId}
          value={toolId}
        >
          <OptGroup label={""}>
            {toolIds.map(id=> {
              return <Option value={id} key={id}>{id}</Option>
            })}
          </OptGroup>
        </Select>
        <Select
          size={"small"}
          placeholder={""}
          onChange={updateWorkObjectId}
          value={workObjectId}
        >
          <OptGroup label={""}>
          {workObjectIds.map(id=> {
            return <Option value={id} key={id}>{id}</Option>
          })}
          </OptGroup>
        </Select>
        <Icon
          className={`${(dashboard && dashboard.editable)?"":"disabled"}`}
          type="block" onClick={()=>{
            if(dashboard && dashboard.editable){
              toggleCreateCellModal()
            }
          }}
        />
        <Icon
          type="lock"
          className={`${(dashboard && dashboard.editable)?"":"info"}`}
          onClick={this.toggleLock}
        />
        {
          !this.state.fullscreen && <Icon type="fullscreen" onClick={()=>{
            document.getElementById("body").requestFullscreen()
          }}/>
        }
        {
          !!this.state.fullscreen && <Icon type="fullscreen-exit" onClick={()=>{
            document.exitFullscreen()
          }}/>
        }
        <Icon type="sync" onClick={()=>{
          window.location.reload()
        }}/>
      </div>
      <CreateCellModal
        visible={creatingCellStatus.showCreateCellModal}
        toggleCreateCellModal={toggleCreateCellModal}
        createCell={this.createCell}
        creatingCellStatus={creatingCellStatus}
        prefixCls={prefixCls}
      >
      </CreateCellModal>
    </div>
  }
  render(){
    // console.log('Header render()')
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }

}

Header.propTypes = {
  className: PropTypes.string,
  pathname: PropTypes.string.isRequired,
  dashboard: PropTypes.object.isRequired,
  toolIds: PropTypes.array.isRequired,
  workObjectIds: PropTypes.array.isRequired,

  toolId: PropTypes.number,
  workObjectId: PropTypes.number,
  coordinateId: PropTypes.number,

  navigate: PropTypes.func.isRequired,
  createCell: PropTypes.func.isRequired,
  updateDashboard: PropTypes.func.isRequired,
  updateMotionVelPercent: PropTypes.func.isRequired,
  updateToolId: PropTypes.func.isRequired,
  updateCoordinateId: PropTypes.func.isRequired,
}


export const getHeaderState = createSelector(
  getDashboards,
  getPathname,
  getToolIds,
  getWorkObjectIds,

  getCurrentToolId,
  getCurrentWorkObjectId,
  getCurrentCoordinateId,
  getCurrentMotionVelPercent,
  getCommandSendInterval,
  getCreatingCellStatus,
  (dashboards, pathname, toolIds, workObjectIds, toolId, workObjectId, coordinateId, motionVelPercent, interval, creatingCellStatus) => {
    let dashboard = null
    if(pathname !== '/' && pathname.indexOf('/dashboards/') !== -1){
      const id = pathname.replace("/dashboards/", "")
      dashboard = dashboards && dashboards.filter(d=>d.i === id)[0]
    }
    return {
      dashboards,
      pathname,
      toolIds,
      workObjectIds,
      toolId,
      workObjectId,
      coordinateId,
      motionVelPercent,
      interval,
      dashboard: dashboard || (dashboards && dashboards[0]),
      creatingCellStatus,
    }
  }
)

function mapStateToProps(state) {
  return getHeaderState(state)
}

export default  connect(mapStateToProps, {
  createCell,
  navigate,
  updateDashboard,
  updateMotionVelPercent,
  updateToolId,
  updateCoordinateId,
  updateWorkObjectId,
  sendCmd,
  toggleCreateCellModal
})(Header)
