import React, {Component} from 'react';
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import Select from "antd/lib/select"

import {
  getCurrentCommand, getWebsocketConnected, getStateCode, getCurrentPlan
} from "../selectors";

import {ConfigConsumer} from 'antd/lib/config-provider'
import Menu from 'antd/lib/menu';
import Icon from 'antd/lib/icon';
import Slider from 'antd/lib/slider'
import InputNumber from 'antd/lib/input-number'
import Tooltip from 'antd/lib/tooltip'

const MenuItem = Menu.Item
const Option = Select.Option
const OptGroup = Select.OptGroup



class Footer extends Component {

  renderContent = ({getPrefixCls})=>{
    let {
      prefixCls: customizePrefixCls,
      currentCommand,
      websocketConnected,
      stateCode,
      currentPlan,
    } = this.props
    const prefixCls = getPrefixCls('footer', customizePrefixCls)
    const colors = {
      100:"#AAAAAA",
      200:"#FF851B",
      300:"#FF4136",
      400:"#0074D9",
      500:"#01FF70"
    }
    const names = {
      100:"",
      200:"",
      300:"",
      400:"",
      500:""
    }
    const color = colors[stateCode || 100]
    const name = names[stateCode]
    let commandClass = 'success'
    if(currentCommand.return_code===0){
      commandClass='success'
    }else if(!!currentCommand.return_code){
      commandClass='error'
    }
    currentPlan = !!currentPlan && currentPlan !== 'none'
    return <div
      className={prefixCls}
    >
      <div
        className={`${prefixCls}-command ${commandClass}`}
      >
        {!!currentCommand.cmd && <div
          className={`${prefixCls}-cmd ${currentCommand.isRunning && `${prefixCls}-cmd-running`}`}
        >
          <Tooltip placement="top" title={''}>
            <Icon type="code" />
          </Tooltip>
          <span>{currentCommand.cmd}</span>
        </div>
        }
        {!!currentCommand.cmd_response_time && <div className={`${prefixCls}-cmd-response-time`}>
          <Tooltip placement="top" title={''}>
            <Icon type="clock-circle" />
          </Tooltip>
          <span>{currentCommand.cmd_response_time}ms</span>
        </div>
        }
        {currentCommand.return_code !== undefined && <div className={`${prefixCls}-return-code`}>
          <Tooltip placement="top" title={''}>
            <Icon type="bulb"/>
          </Tooltip>
          <span>{currentCommand.return_code}</span>
        </div>
        }
        {!!currentCommand.return_message && <div className={`${prefixCls}-return-message`}>
          <Tooltip placement="top" title={''}>
            <Icon type="message"/>
          </Tooltip>
          <span>{currentCommand.return_message}</span>
        </div>
        }
      </div>
      <div
        className={`${prefixCls}-status`}
      >
        <div>
          {!!websocketConnected && <Tooltip placement="top" title={'Websocket'}>
            <Icon type="wifi" className={"success"} />
          </Tooltip>
          }
          {!!websocketConnected &&
          <span></span>
          }
          {!websocketConnected && <Tooltip placement="top" title={'Websocket'}>
            <Icon type="wifi" className={"danger"} />
          </Tooltip>
          }
          {!websocketConnected &&
          <span></span>
          }
        </div>
        <div
          className={!!name ? "info": "disabled"}
        >
          <Tooltip placement="top" title={'State Code'}>
          <Icon
            style={{
              color: color,
              fill: color
            }}
            fill={color}
            type="check-circle" className="info" theme="filled"
          >
          </Icon>
          </Tooltip>
          <span>{name || ""}</span>
        </div>
        <div
          className={!!currentPlan ? "success": "disabled"}
        >
          <Tooltip placement="top" title={'Current Plan'}>
            <Icon
              style={{
                color: color,
                fill: color
              }}
              fill={color}
              type="setting"
              theme="filled"
              spin={!!currentPlan}
            >
            </Icon>
          </Tooltip>
          <span>{!!currentPlan?'':''}</span>
        </div>
      </div>
    </div>
  }
  render(){
    // console.log('Footer render()')
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }

}

Footer.propTypes = {
  currentCommand: PropTypes.object.isRequired,
}


export const getFooterState = createSelector(
  getCurrentCommand,
  getWebsocketConnected,
  getStateCode,
  getCurrentPlan,
  (currentCommand, websocketConnected, stateCode, currentPlan) => {
    return {
      currentCommand,
      websocketConnected,
      stateCode,
      currentPlan
    }
  }
)

function mapStateToProps(state) {
  return getFooterState(state)
}

export default  connect(mapStateToProps, {
})(Footer)
