import XMLJSONBase from "./XMLJSONBase";
import SyncManagerPoolObject from "./SyncManagerPoolObject";


export class EthercatMotion extends XMLJSONBase {
  constructor(uuid, phyId, vendorId, productCode, revisionNum, dcAssignActivate, maxPos, minPos, maxVel, minVel, maxAcc, minAcc, maxPosFollowingError, maxVelFollowingError, posFactor, posOffset, homePos, syncManagerPoolObjects){
    super()
    this.uuid = uuid

    this.phyId = phyId
    this.vendorId = vendorId
    this.productCode = productCode
    this.revisionNum = revisionNum
    this.dcAssignActivate = dcAssignActivate

    this.maxPos = maxPos
    this.minPos = minPos
    this.maxVel = maxVel
    this.minVel = minVel
    this.maxAcc = maxAcc
    this.minAcc = minAcc
    this.maxPosFollowingError = maxPosFollowingError
    this.maxVelFollowingError = maxVelFollowingError
    this.posFactor = posFactor
    this.posOffset = posOffset
    this.homePos = homePos
    this.syncManagerPoolObjects = syncManagerPoolObjects
  }

  static FromXML = (node) => {
    let phyId = EthercatMotion.GetNumber(node.getAttribute("phy_id"))
    let vendorId = node.getAttribute("vendor_id")
    let productCode = node.getAttribute("product_code")
    let revisionNum = node.getAttribute("revision_num")
    let dcAssignActivate = node.getAttribute("dc_assign_activate")
    let maxPos = EthercatMotion.GetDegreeNumber(node.getAttribute("max_pos"))
    let minPos = EthercatMotion.GetDegreeNumber(node.getAttribute("min_pos"))
    let maxVel = EthercatMotion.GetDegreeNumber(node.getAttribute("max_vel"))
    let minVel = EthercatMotion.GetDegreeNumber(node.getAttribute("min_vel"))
    let maxAcc = EthercatMotion.GetDegreeNumber(node.getAttribute("max_acc"))
    let minAcc = EthercatMotion.GetDegreeNumber(node.getAttribute("min_acc"))
    let maxPosFollowingError = EthercatMotion.GetNumber(node.getAttribute("max_pos_following_error"))
    let maxVelFollowingError = EthercatMotion.GetNumber(node.getAttribute("max_vel_following_error"))
    let posFactor  = EthercatMotion.GetNumber(node.getAttribute("pos_factor"))
    let posOffset = EthercatMotion.GetDegreeNumber(node.getAttribute("pos_offset"))
    let homePos = EthercatMotion.GetDegreeNumber(node.getAttribute("home_pos"))

    return new EthercatMotion(
      Math.random().toString(36),
      phyId,
      vendorId,
      productCode,
      revisionNum,
      dcAssignActivate,
      maxPos,
      minPos,
      maxVel,
      minVel,
      maxAcc,
      minAcc,
      maxPosFollowingError,
      maxVelFollowingError,
      posFactor,
      posOffset,
      homePos,
      [...node.getElementsByTagName("SyncManagerPoolObject")].filter(node => node.nodeType === 1).map(SyncManagerPoolObject.FromXML)
    )
  }

  static FromJSON = ({uuid, phyId, vendorId, productCode, revisionNum, dcAssignActivate, maxPos, minPos, maxVel, minVel, maxAcc, minAcc, maxPosFollowingError, maxVelFollowingError, posFactor, posOffset, homePos, syncManagerPoolObjects}) => new EthercatMotion(uuid || Math.random().toString(36), phyId, vendorId, productCode, revisionNum, dcAssignActivate, maxPos, minPos, maxVel, minVel, maxAcc, minAcc, maxPosFollowingError, maxVelFollowingError, posFactor, posOffset, homePos, syncManagerPoolObjects && syncManagerPoolObjects.map(SyncManagerPoolObject.FromJSON))

 toXML = () => `<EthercatMotion ${EthercatMotion.ToXMLAttrString('phy_id',this.phyId)} ${EthercatMotion.ToXMLAttrString('vendor_id', this.vendorId)} ${EthercatMotion.ToXMLAttrString('product_code',this.productCode)} ${EthercatMotion.ToXMLAttrString('revision_num', this.revisionNum)} ${EthercatMotion.ToXMLAttrString('dc_assign_activate', this.dcAssignActivate)} ${EthercatMotion.ToXMLAttrString('max_pos', EthercatMotion.ToRadian(this.maxPos))} ${EthercatMotion.ToXMLAttrString('min_pos', EthercatMotion.ToRadian(this.minPos))}  ${EthercatMotion.ToXMLAttrString('max_vel',EthercatMotion.ToRadian(this.maxVel))} ${EthercatMotion.ToXMLAttrString('min_vel', EthercatMotion.ToRadian(this.minVel))} ${EthercatMotion.ToXMLAttrString('max_acc', EthercatMotion.ToRadian(this.maxAcc))} ${EthercatMotion.ToXMLAttrString('min_acc',EthercatMotion.ToRadian(this.minAcc))} ${EthercatMotion.ToXMLAttrString('max_pos_following_error', this.maxPosFollowingError)} ${EthercatMotion.ToXMLAttrString('max_vel_following_error', this.maxVelFollowingError)} ${EthercatMotion.ToXMLAttrString('pos_factor', this.posFactor)} ${EthercatMotion.ToXMLAttrString('pos_offset', EthercatMotion.ToRadian(this.posOffset))} ${EthercatMotion.ToXMLAttrString('home_pos', EthercatMotion.ToRadian(this.homePos))} >${this.syncManagerPoolObjects?this.syncManagerPoolObjects.map(smpo=>smpo.toXML()).join(''):''}</EthercatMotion>`


  toJSON = () => ({
    uuid: this.uuid,
    isMotion: true,
    phyId: this.phyId,
    vendorId: this.vendorId,
    productCode: this.productCode,
    revisionNum: this.revisionNum,
    dcAssignActivate: this.dcAssignActivate,
    maxPos: this.maxPos,
    minPos: this.minPos,
    maxVel: this.maxVel,
    minVel: this.minVel,
    maxAcc: this.maxAcc,
    minAcc: this.minAcc,
    maxPosFollowingError: this.maxPosFollowingError,
    maxVelFollowingError: this.maxVelFollowingError,
    posFactor: this.posFactor,
    posOffset: this.posOffset,
    homePos: this.homePos,
    syncManagerPoolObjects:this.syncManagerPoolObjects && this.syncManagerPoolObjects.map(smpo=>smpo.toJSON())
  })
}

export default EthercatMotion