import React from 'react'
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import CellBase from "./CellBase";
import Icon from "antd/lib/icon"
import {
  getResultByChannel
} from "../../selectors";

class ExampleCell extends CellBase {
  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      get,
    } = this.props
    const prefixCls = getPrefixCls('example-cell', customizePrefixCls);
    {/**/}
    return <div className={prefixCls}>
      <p>
      {!!get && JSON.stringify(get, null, "\n")}
      </p>
    </div>
  }
}

ExampleCell.propTypes = {
  get: PropTypes.object
}

// reduxreact
const mapStateToProps = (state, props) => {
  return {
    get: getResultByChannel(state, 'get'),
  }
}
// reduxreact
const ConnectedExampleCell = connect(
  mapStateToProps,
)(ExampleCell)

ConnectedExampleCell.NAME = ''
ConnectedExampleCell.WIDTH = 24
ConnectedExampleCell.HEIGHT = 12
ConnectedExampleCell.ICON = (props) => <Icon type={"codepen"} {...props} />
// ConnectedExampleCell.CONFIG = config


export default ConnectedExampleCell

