import { take, put, call, fork, select, all } from 'redux-saga/effects'
import { history } from '../services'
import * as actions from '../actions'
import configSaga from './config'
import websocketSaga from './websocket'
import robots from "./robots";

// resuable fetch Subroutine
// entity :  dashboards | dashboards
// apiFn  : api.fetchDashboards | api.fetchRepo | ...
// id     : login | fullName
// url    : next page url. If not provided will use pass id to apiFn
function* fetchEntity(entity, apiFn, id, url) {
  yield put( entity.request(id) )
  const {response, error} = yield call(apiFn, url || id)
  if(response)
    yield put( entity.success(id, response) )
  else
    yield put( entity.failure(id, error) )
}

function* fetchEntities(entities, apiFn, url) {
  yield put( entities.request() )
  const {response, error} = yield call(apiFn, url)
  if(response)
    yield put( entities.success(response) )
  else
    yield put( entities.failure(error) )
}

// trigger router navigation via history
function* watchNavigate() {
  while(true) {
    const {pathname} = yield take(actions.NAVIGATE)
    yield history.push(pathname)
  }
}

export default function* root() {
  yield all([
    ...configSaga,
    ...websocketSaga,
    ...robots,
    fork(watchNavigate),
  ])
}