import React, {Component} from "react"
import PropTypes from 'prop-types'
import Modal from 'antd/lib/modal'
import {showCreateRobotModelModal} from "../../../actions";


class CreateProgramModal extends  Component {
  handleSubmit = e => {
    e.preventDefault();
    const {
      deleteProgram,
      deletingProgramStatus
    } = this.props
      deleteProgram(deletingProgramStatus.showDeleteProgramModal)
  }
  render() {
    const {
      toggleDeleteProgramModal,
      prefixCls,
      deletingProgramStatus
    } = this.props
    return <Modal
      title=""
      visible={!!deletingProgramStatus.showDeleteProgramModal}
      okText={""}
      cancelText={""}
      onOk={this.handleSubmit}
      onCancel={()=>toggleDeleteProgramModal()}
      wrapClassName={`${prefixCls}-delete-program-modal`}
    >
      <div className={"info"}>
        {deletingProgramStatus.showDeleteProgramModal!==true?` "${deletingProgramStatus.showDeleteProgramModal}" `:''}!
      </div>
      <div className={`${prefixCls}-status`}>
        {!!deletingProgramStatus.deletingProgram && <div className={"info"}>
          ...
        </div>}
        {!!deletingProgramStatus.deletingProgramFailure && <div className={"error"}>
          : {deletingProgramStatus.deletingProgramFailure}
        </div>}
        {!!deletingProgramStatus.deletingProgramSuccess && <div className={"success"}>
          
        </div>}
      </div>
    </Modal>
  }
}


CreateProgramModal.propTypes = {
  deleteProgram: PropTypes.func.isRequired,
  toggleDeleteProgramModal: PropTypes.func.isRequired,
  prefixCls: PropTypes.string.isRequired,
  deletingProgramStatus: PropTypes.object.isRequired,
}

export default CreateProgramModal
