import React, {Fragment} from 'react'
import PropTypes from "prop-types";
import CellBase from "../CellBase";
import { connect } from 'react-redux'
import Button from "antd/lib/button"
import isEqual from 'lodash/isEqual'
import Icon from "antd/lib/icon"
import {deleteResultByChannel} from "../../../actions"
import {
  makeGetResultsByCellChannel,
} from "../../../selectors";
import Config from "./Config"
import Chart from "./Chart";

class RealtimeChart extends CellBase {

  interval = null

  state = {playing: true}

  resetInterval = () => {
    clearInterval(this.interval)
    const {cell, sendCmd} = this.props
    if(cell.options && !cell.options.showConfig && cell.options.channel === "command" && cell.options.command && cell.options.interval && !cell.options.paused){
      const command = cell.options.command
      const channel = cell.i
      this.interval = setInterval(()=>{
        sendCmd(command, channel)
      }, cell.options.interval)
    }
  }

  componentWillUnmount() {
    clearInterval(this.interval)
  }

  componentDidMount() {
    this.resetInterval()
    this.start = new Date()
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    let now = new Date()
    if(now - this.start > 1000){
      this.start = now
      return true
    }
    return false
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const {cell: oldCell} = prevProps
    const {cell: newCell} = this.props
    if (!isEqual(oldCell, newCell)) {
      this.resetInterval()
    }
    if(newCell.options.showConfig !== oldCell.options.showConfig && newCell.options.showConfig){
      this.props.deleteResultByChannel(newCell.i)
    }
  }

  handleChange = (value) => {
     const options = this.getOptions()
     this.updateOptions({...options, channel: value})
  }

  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      results,
    } = this.props
    const prefixCls = getPrefixCls('realtime-chart', customizePrefixCls);
    const hasDataSources = cell.options && cell.options.channel && cell.options.sources && cell.options.sources.length > 0
    const dataChannel = `${cell.options.channel==='get'?'get':cell.options.command}`
    return <div
      className={`${prefixCls} ${!hasDataSources && prefixCls+"-no-data"}`}
    >
      {!hasDataSources && <div>
        <span>  </span>
        <div style={{marginTop: 20}}>
          <Button type={"primary"} onClick={()=>{
            this.updateOptions({showConfig:true})
          }}></Button>
        </div>
      </div>}
      {hasDataSources && <Fragment>
        <Chart
          dataChannel={dataChannel}
          prefixCls={prefixCls}
          results={results}
        />

        {cell.options.paused && <Icon
          type="play-circle"
          className={`${prefixCls}-play ${prefixCls}-play-start`}
          onClick={()=>{
            this.updateOptions({
              paused: false
            })
          }}
        />}
        {!cell.options.paused && <Icon
          type="pause-circle"
          className={`${prefixCls}-play`}
          onClick={()=>{
            this.updateOptions({
              paused: true
            })
          }}
        />}
      </Fragment>
      }
      </div>
  }
}

RealtimeChart.propTypes = {
  results: PropTypes.object.isRequired
}

const makeMapStateToProps = () => {
  const getResultsByCellChannel = makeGetResultsByCellChannel(1000)
  const mapStateToProps = (state, props) => {
    return {
      results: getResultsByCellChannel(state, props),
    }
  }
  return mapStateToProps
}


const ConnectedRealtimeChart = connect(
  makeMapStateToProps,
  {deleteResultByChannel}
)(RealtimeChart)

ConnectedRealtimeChart.NAME = ''
ConnectedRealtimeChart.CONFIG = Config

export default ConnectedRealtimeChart



