import * as ActionTypes from '../actions'

export default (state = {}, action) => {
  const {type, error, errorType} = action
  if (type === ActionTypes.RESET_ERROR_MESSAGE) {
    if (errorType) {
      if (!!state[errorType]){
        state = {...state}
        delete state[errorType]
      }
    }else{
      state = {}
    }
  } else if(!!error){
    state = {...state}
    state[type] = error
  }
  return state
}