import React from 'react'
import PropTypes from "prop-types";
import Row from "antd/lib/row"
import Button from "antd/lib/button"
import Input  from 'antd/lib/input'
import CellBase from "./CellBase";
import { connect } from 'react-redux'
import Terminal from 'terminal-in-react'
import {
  getResultByCell, getResultByChannel,
  makeGetResultsByCell
} from "../../selectors";
const { TextArea } = Input;

class Debugger extends CellBase {
  interval = null
  timeout = null
  getSendCmdFn = () => {
    return (()=>{
      const {sendCmd, cell} = this.props
      const options = this.getOptions()
      if(options.command){
        options.command.split("\n").forEach(cmd=>{
          cmd = cmd.trim()
          if(!!cmd){
            sendCmd(cmd,cell.i)
            this.terminal.runCommandOnActive("showCmd "+cmd)
          }
        })
      }
    }).bind(this)
  }

  getOnMouseDownFn = () => {
    return (()=>{
      if(this.interval){
        clearInterval(this.interval)
      }
      const fn = this.getSendCmdFn()
      const self = this
      const {commandSendInterval, commandSendDelay} = this.props
      self.timeout = setTimeout(()=>{
        self.interval = setInterval(fn, commandSendInterval)
      }, commandSendDelay)
    }).bind(this)
  }
  getOnMouseUpFn = () => {
    return (()=>{
      if(this.interval){
        clearInterval(this.interval)
      }
      if(this.timeout){
        clearTimeout(this.timeout)
      }
    }).bind(this)
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    if(nextProps.result !== this.props.result){
      this.result = nextProps.result
      this.terminal.runCommandOnActive("showResult")
    }
    if(nextProps.cell !== this.props.cell || nextProps.result !== this.props.result){
      return true
    }
    return false
  }

  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      results
    } = this.props
    const options = this.getOptions()
    const prefixCls = getPrefixCls('debugger', customizePrefixCls);
    let self = this
    return <div
      className={`${prefixCls}`}
    >
      <Row>
        <TextArea
          rows={2}
          value={options.command}
          onChange={(e)=>{
            this.updateOptions({command:e.target.value})
          }}
          placeholder={""}
        />
        <Button
          onClick={this.getSendCmdFn()}
          onMouseDown={this.getOnMouseDownFn()}
          onMouseUp={this.getOnMouseUpFn()}
          onMouseOut={this.getOnMouseUpFn()}
          type="primary"
          shape="round"
          icon="play-circle"
          size="large"
        >
          Send
        </Button>
        <div
          className={`${prefixCls}-terminal`}
        >
          <Terminal
            color='green'
            backgroundColor='black'
            barColor='black'
            hideTopBar={true}
            allowTabs={false}
            commands={{
              "showCmd":(args, print, runCommand)=>{
                if(args[1]){
                  print(args.slice(1).join(" "))
                }else{
                  const options = self.getOptions()
                  if(options.command){
                    print(options.command)
                  }
                }
              },
              "send":(args, print, runCommand)=>{
                self.props.sendCmd(args[1], self.props.cell.i)
              },
              "showResult":(args, print, runCommand)=>{
                print(JSON.stringify(this.result))
              },
            }}
            descriptions={{
              'send': ', "send []",  "send get"',
              'showResult': '',
              'showCmd': ''
            }}
            ref={ref=>this.terminal=ref}
          />
        </div>
      </Row>
    </div>
  }

}

Debugger.propTypes = {
  results: PropTypes.array.isRequired
}

const makeMapStateToProps = () => {
  const getResultsByCell = makeGetResultsByCell(10)
  const mapStateToProps = (state, props) => {
    return {
      results: getResultsByCell(state, props),
      result: getResultByChannel(state, props.cell.i),
    }
  }
  return mapStateToProps
}

const ConnectedDebugger =  connect(
  makeMapStateToProps,
)(Debugger)

ConnectedDebugger.NAME = ""

export default  ConnectedDebugger
