import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import { connect } from 'react-redux'
import Switch from "antd/lib/switch"
import Modal from "antd/lib/modal"
import Button from "antd/lib/button"
import CellBase from "./CellBase";
import isEqual from "lodash/isEmpty"
import {getMemoizeResultByChannel, getResultByCell, getStateCode, makeGetDataKeysByChannel} from "../../selectors";
import {deleteResultByChannel} from "../../actions";
import {hashEqualSelectorCreator} from "../../selectors";

class ManualSwitch extends CellBase {
  state = {
    enable: true
  }
  onChange = (checked)=>{
    let {cell} = this.props
    if(checked){
      this.sendCmd("md", `${cell.i}-md`)
      this.sendCmd("rc", `${cell.i}-rc`)
      this.sendCmd("en", `${cell.i}-en`)
      this.setState({enable: true})
    }else{
      this.sendCmd("ds", `${cell.i}-ds`)
      this.setState({enable: false})
    }
  }

  resetEnable = () => {
    const {cell} = this.props
    this.props.deleteResultByChannel(`${cell.i}-md`)
    this.props.deleteResultByChannel(`${cell.i}-rc`)
    this.props.deleteResultByChannel(`${cell.i}-en`)
    this.setState({enable: false})
  }

  resetDisable = () => {
    const {cell} = this.props
    this.props.deleteResultByChannel(`${cell.i}-ds`)
    this.setState({enable: true})
  }

  renderManualSwitch = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      stateCode,
      resultMD,
      resultRC,
      resultEN,
      resultDS,
    } = this.props
    const colors = ['#AAAAAA', '#FF851B','#FF4136','#0074D9', '#01FF70']
    const names = ["","", "", "", ""]
    const color = colors[Math.floor(stateCode)%5]
    const name = names[Math.floor(stateCode)%5]
    const prefixCls = getPrefixCls('manual-switch', customizePrefixCls);
    const self = this
    const {enable} = this.state
    const showEnableModel = enable && (resultMD && resultRC && resultEN ) && (resultMD.return_code !== 0 || resultRC.return_code !== 0 || resultEN.return_code !== 0)
    const showDisableModel = !enable && resultDS && resultDS.return_code !== 0

    return <div
      className={`${prefixCls}`}
    >
      <div>
        <Switch
          checkedChildren=""
          unCheckedChildren=""
          checked={
            enable && resultMD && resultRC && resultEN && resultMD.return_code === 0 && resultRC.return_code === 0 && resultEN.return_code === 0
          }
          onChange={this.onChange}
        />
        <span>{stateCode===100?'':''}</span>
      </div>
      <div>
        <Icon type="drag"
              onClick={()=>{
                this.sendCmd("mvJoint", cell.i)
              }}
        />
        <span></span>
      </div>
      <div>
        <Icon type="pause-circle"
              onClick={()=>{
                this.sendCmd("FCStop", cell.i)
              }}
        />
        <span></span>
      </div>
      <div>
        <Icon type="swap" onClick={()=>{
          this.sendCmd("mvf", cell.i)
        }}/>
        <span></span>
      </div>
      <div>
        <Icon type="rollback" onClick={()=>{
          this.sendCmd("rs --pos={0.5,0.39252,0.7899,0.5,0.5,0.5} --vel=0.02", cell.i)
        }}/>
        <span></span>
      </div>
      <Modal
        visible={showEnableModel}
        title=""
        onOk={this.resetEnable}
        onCancel={this.resetEnable}
        centered={true}
        footer={[
          <Button key="close" onClick={this.resetEnable}>
            
          </Button>,
        ]}
      >
        {
          !!(resultMD && resultMD.return_code !== 0) && <p className={"danger"}>
          MD {resultMD && (resultMD.return_message ? `${resultMD.return_code} ${resultMD.return_message} ` : `${resultMD.return_code}`)}
        </p>
        }
        {
          !!(resultRC && resultRC.return_code !== 0) && <p className={"danger"}>
          RC {resultRC && (resultRC.return_message ? `${resultRC.return_code} ${resultRC.return_message} ` : `${resultRC.return_code}`)}
        </p>
        }
        {
          !!(resultEN && resultEN.return_code !== 0) && <p className={"danger"}>
          EN {resultEN && (resultEN.return_message ? `${resultEN.return_code} ${resultEN.return_message} ` : `${resultEN.return_code}`)}
        </p>
        }
      </Modal>
      <Modal
        visible={showDisableModel}
        title=""
        onOk={this.resetDisable}
        onCancel={this.resetDisable}
        centered={true}
        footer={[
          <Button key="close" onClick={this.resetDisable}>
            
          </Button>,
        ]}
      >
        <span className={"danger"}>
          {resultDS && (resultDS.return_message?`${resultDS.return_code} ${resultDS.return_message} `:`${resultDS.return_code}`)}</span>
      </Modal>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderManualSwitch}</ConfigConsumer>
  }
}

ManualSwitch.propTypes = {
  cell: PropTypes.object.isRequired,
  stateCode: PropTypes.number,
}


const makeMapStateToProps = () => {
  const getMemoizeResult = hashEqualSelectorCreator(
    (state, props) => state.robot.resultsByChannel[`${props.cell.i}-md`],
    (state, props) => state.robot.resultsByChannel[`${props.cell.i}-rc`],
    (state, props) => state.robot.resultsByChannel[`${props.cell.i}-en`],
    (state, props) => state.robot.resultsByChannel[`${props.cell.i}-ds`],
    (resultMD, resultRC, resultEN, resultDS) => ({resultMD, resultRC, resultEN, resultDS})
  )
  return (state, props) => {
    return getMemoizeResult(state, props)
  }
}

const ConnectedManualSwitch = connect(
  makeMapStateToProps,
  {deleteResultByChannel}
)(ManualSwitch)

ConnectedManualSwitch.NAME = ''

export default ConnectedManualSwitch

