import {
  put,
  call,
  fork,
  select,
  delay,
  takeLatest,
} from 'redux-saga/effects'
import isEmpty from 'lodash/isEmpty'
import { api } from '../services'
import * as actions from '../actions'
import { getConfig } from '../reducers/selectors'
import {getDashboardAndCells} from "../selectors/config";
import { dashboardUpdate } from "../actions";
import { cellCreate, cellDelete } from "../actions";
import {configXml} from "../actions";
const { interfaceConfig } = actions

function* fetchConfig() {
  yield put( interfaceConfig.request() )
  const {response, error} = yield call(api.fetchInterfaceConfig)
  if(response)
    yield put( interfaceConfig.success(response) )
  else
    yield put( interfaceConfig.failure(error) )
}

function* loadConfig() {
  yield delay(50)
  // const config = yield select(getConfig)
  yield call(fetchConfig)
}

function* loadConfigXml() {
  yield put( configXml.request() )
  const {response, error} = yield call(api.fetchConfigXml)
  if(response)
    yield put( configXml.success(response) )
  else
    yield put( configXml.failure(error) )
}


function* updateDashboard({dashboard}){
  // delaydebounce effect
  yield delay(500)
  dashboard = yield select(getDashboardAndCells, dashboard.i)
  if(!isEmpty(dashboard)){
    yield put( dashboardUpdate.request() )
    const {response, error} = yield call(api.updateDashboard, dashboard)
    if(response)
      yield put( dashboardUpdate.success(response) )
    else
      yield put( dashboardUpdate.failure(error) )
  }
}

function* createCell({dashboardId, cell}){
  yield delay(50)
  yield put( cellCreate.request() )
  const {response, error} = yield call(api.createCell, dashboardId, cell)
  if(response)
    yield put( cellCreate.success(dashboardId, response) )
  else
    yield put( cellCreate.failure(error) )
}

function* deleteCell({dashboardId, cellId}){
  yield delay(50)
  yield put( cellDelete.request(dashboardId, cellId) )
  const {response, error} = yield call(api.deleteCell, dashboardId, cellId)
  if(response)
    yield put( cellDelete.success(dashboardId, response) )
  else
    yield put( cellDelete.failure(error) )
}

function* watchUpdateDashboard() {
  yield  takeLatest(actions.UPDATE_DASHBOARD, updateDashboard)
}

function* watchCreateCell() {
  yield takeLatest(actions.CREATE_CELL, createCell)
}

function* watchLoadConfig() {
  yield takeLatest(actions.LOAD_INTERFACE_CONFIG, loadConfig)
}

function* watchDeleteCell() {
  yield takeLatest(actions.DELETE_CELL, deleteCell)
}

function* watchLoadConfigXml() {
  yield takeLatest(actions.LOAD_CONFIG_XML, loadConfigXml)
}

export default [
  fork(watchLoadConfig),
  fork(watchUpdateDashboard),
  fork(watchCreateCell),
  fork(watchDeleteCell),
  fork(watchLoadConfigXml)
]
