import * as actions from '../actions'
import {SHOW_CREATE_ROBOT_MODEL_MODAL} from "../actions";
import isEmpty from 'lodash/isEmpty'

let currentToolId = 0
if(window.localStorage.getItem("currentToolId")){
  try{
    currentToolId = JSON.parse(window.localStorage.getItem("currentToolId"))
  }catch (e) {
  }
}
let currentWorkObjectId = 0
if(window.localStorage.getItem("currentWorkObjectId")){
  try{
    currentWorkObjectId = JSON.parse(window.localStorage.getItem("currentWorkObjectId"))
  }catch (e) {
  }
}

let currentCoordinateId = 0
if(window.localStorage.getItem("currentCoordinateId")){
  try{
    currentCoordinateId = JSON.parse(window.localStorage.getItem("currentCoordinateId"))
  }catch (e) {
  }
}

const initialState = {
  resultsByChannel: {},
  websocketConnected: false,
  websocketError: null,
  channelsById: {},
  commandsById: {},
  currentCommand: {},
  robotStatus: 4,
  toolIds: [...Array(16).keys()],
  workObjectIds: [...Array(32).keys()],

  currentToolId,
  currentWorkObjectId,
  currentCoordinateId,

  currentMotionVelPercent: 0,
  firstGet: true,

  createRobotModelModalVisible: false,
  currentRobotBrand: null,
  currentRobotModel: null,

  esiFileUploadingSuccess: null,
  esiFileUploadingRequest: null,
  esiFileUploadingFailure: null,

  esiPathFetchingSuccess: null,
  esiPathFetchingRequest: null,
  esiPathFetchingFailure: null,
  esiPath: null,

  xml: null,

  programs: {},
  showCreatingProgramModal: false,
  creatingProgram: false,
  creatingProgramFailure: false,
  creatingProgramSuccess: false,

  showDeletingProgramModal: false,
  deletingProgram: false,
  deletingProgramFailure: false,
  deletingProgramSuccess: false,

  editingProgramName: null,
}

const callbacksById = {}

//
function utf8ArrayToStr(array) {
  let out, i, len, c
  let char2, char3

  out = ""
  len = array.length
  i = 0
  while(i < len) {
    c = array[i++]
    switch(c >> 4)
    {
      case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
      // 0xxxxxxx
      out += String.fromCharCode(c)
      break
      case 12: case 13:
      // 110x xxxx   10xx xxxx
      char2 = array[i++]
      out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F))
      break
      case 14:
        // 1110 xxxx  10xx xxxx  10xx xxxx
        char2 = array[i++]
        char3 = array[i++]
        out += String.fromCharCode(((c & 0x0F) << 12) |
          ((char2 & 0x3F) << 6) |
          ((char3 & 0x3F) << 0))
        break;
    }
  }

  return out
}



function analyzeBotData(buffer, channelsByID, anyRobot) {
  let dataView=new DataView(buffer);
  let cmdLength=dataView.getInt32(0);
  let cmdId=dataView.getInt32(4,true);
  let cmdCode=dataView.getFloat64(16,true);
  //channelcmd
  if(!channelsByID[cmdCode]){
    return
  }
  try{
    let str = utf8ArrayToStr(new Uint8Array(buffer, 40))
    return {...JSON.parse(str), id: cmdCode, cmd_response_time: Date.now() - cmdCode}
  }catch (e) {
    console.error(e)
  }
}

export default (state = initialState, action) => {
  switch (action.type) {
    case actions.WEBSOCKET_CONNECTED:
      return {...state, websocketConnected: true}
    case actions.WEBSOCKET_DISCONNECTED:
      return {...state, websocketConnected: false}
    case actions.WEBSOCKET_ERROR:
      return {...state, websocketError: action.payload}
    case actions.SEND_CMD:
    {
      let {id, channel, cmd, callback} = action
      //channelcmd
      if(!!channel){
        let channelsById = {...state.channelsById}
        channelsById[id] = channel
        let commandsById = state.commandsById
        let currentCommand = state.currentCommand
        if(channel !== 'get'){
          commandsById = {...commandsById}
          commandsById[id] = cmd
          if(callback){
            callbacksById[id] = callback
          }
          currentCommand = {id, cmd, channel, isRunning: true}
        }
        return {...state, channelsById, commandsById, currentCommand}
      }else{
        return state
      }
    }
    case actions.CMD_RESPONSE:
    {
      let {data} = action
      let channelsById = state.channelsById
      let resultsByChannel = state.resultsByChannel
      let commandsById = state.commandsById
      let currentCommand = state.currentCommand
      let firstGet = state.firstGet
      let result = analyzeBotData(data, state.channelsById, state.model)
      if(result){
        channelsById = {...channelsById}
        let channel = channelsById[result.id]
        let callback = callbacksById[result.id]
        delete channelsById[result.id]
        delete callbacksById[result.id]
        if(callback && typeof(callback) === 'function'){
          callback(result)
        }
        if(channel !== 'get' && commandsById[result.id]){
          commandsById = {...commandsById}
          delete commandsById[result.id]
        }
        if(channel){
          resultsByChannel={...resultsByChannel}
          resultsByChannel[channel] = result
        }
        if(currentCommand.id === result.id){
          currentCommand = {...currentCommand, ...result, isRunning: false}
        }
        if(firstGet && channel === 'get'){
          return {
            ...state,
            channelsById,
            resultsByChannel,
            commandsById,
            currentCommand,
            currentMotionVelPercent: result.vel_percent || 0,
            firstGet: false
          }
        }else{
          return {...state, channelsById, resultsByChannel, commandsById, currentCommand}
        }
      }else{
        return state
      }
    }
    case actions.DELETE_RESULT_BY_CHANNEL:
    {
      let {channel} = action
      let resultsByChannel = {...state.resultsByChannel}
      delete resultsByChannel[channel]
      return {...state, resultsByChannel }
    }
    case actions.UPDATE_MOTION_VEL_PERCENT:
      return {
        ...state,
        currentMotionVelPercent: action.motionVelPercent,
      }
    case actions.UPDATE_COORDINATE_ID:
      window.localStorage.setItem("currentCoordinateId", action.coordinateId)
      return {
        ...state,
        currentCoordinateId:  action.coordinateId,
      }
    case actions.UPDATE_TOOL_ID:
      window.localStorage.setItem("currentToolId", action.toolId)
      return {
        ...state,
        currentToolId:  action.toolId,
      }
    case actions.UPDATE_WORK_OBJECT_ID:
      window.localStorage.setItem("currentWorkObjectId", action.workObjectId)
      return {
        ...state,
        currentWorkObjectId: action.workObjectId,
      }
    case actions.ROBOT_MODELS_FETCH_SUCCESS:
    {
      const {robotModels} = action
      return {...state, robotModels}
    }
    // case actions.DELETE_ROBOT_MODEL:
    // {
    //   const {model} = action
    //   const robotModels = {...state.robotModels}
    //   robotModels[model.brand] = {...robotModels[model.brand]}
    //   delete robotModels[model.brand][model.model]
    //   return {...state, robotModels}
    // }
    case actions.ROBOT_MODEL_DELETE_SUCCESS:
    {
      const {model} = action
      const robotModels = {...state.robotModels}
      robotModels[model.brand] = {...robotModels[model.brand]}
      delete robotModels[model.brand][model.model]
      if(isEmpty(robotModels[model.brand])){
        delete robotModels[model.brand]
      }
      return {...state, robotModels}
    }
    case actions.ROBOT_MODEL_PART_UPLOAD_SUCCESS:
    {
      const {model} = action
      const robotModels = {...state.robotModels}
      robotModels[model.brand] = {...robotModels[model.brand]}
      robotModels[model.brand][model.model] = model
      return {...state, robotModels}
    }
    case actions.ROBOT_MODEL_PART_DELETE_SUCCESS:
    {
      const {model} = action
      const robotModels = {...state.robotModels}
      robotModels[model.brand] = {...robotModels[model.brand]}
      robotModels[model.brand][model.model] = model
      return {...state, robotModels}
    }
    case actions.ROBOT_MODEL_CREATE_REQUEST:
    {
      return {
        ...state,
        creatingRobotModel: true,
        creatingRobotModelError: false,
        creatingRobotModelResult:  null,
      }
    }
    case actions.ROBOT_MODEL_CREATE_SUCCESS:
    {
      const {model} = action
      const robotModels = {...state.robotModels}
      robotModels[model.brand] = {...robotModels[model.brand]}
      robotModels[model.brand][model.model] = model
      return {
        ...state,
        robotModels,
        currentRobotBrand: model.brand,
        currentRobotModel: model.model,
        createRobotModelModalVisible: false,
        creatingRobotModel: false,
        creatingRobotModelError: false,
        creatingRobotModelResult:  model,
      }
    }
    case actions.ROBOT_MODEL_CREATE_FAILURE:
    {
      const {error} = action
      return {
        ...state,
        creatingRobotModel: false,
        creatingRobotModelError: error,
        creatingRobotModelResult:  false,
      }
    }
    case actions.SHOW_CREATE_ROBOT_MODEL_MODAL:
      return {
        ...state,
        createRobotModelModalVisible: true,
        creatingRobotModel: false,
        creatingRobotModelError: null,
        creatingRobotModelResult:  null,
      }
    case actions.HIDE_CREATE_ROBOT_MODEL_MODAL:
      return {
        ...state,
        createRobotModelModalVisible: false,
      }
    case actions.SELECT_CURRENT_ROBOT_BRAND:
    {
      const {brand} = action
      return {
        ...state,
        currentRobotBrand: brand
      }
    }
    case actions.SELECT_CURRENT_ROBOT_MODEL:
    {
      const {model} = action
      return {
        ...state,
        currentRobotModel: model
      }
    }
    case actions.UPLOAD_ESI_FILE_REQUEST:
    {
      return {
        ...state,
        esiFileUploadingSuccess: null,
        esiFileUploadingRequest: true,
        esiFileUploadingFailure: null
      }
    }
    case actions.UPLOAD_ESI_FILE_FAILURE:
    {
      return {
        ...state,
        esiFileUploadingSuccess: null,
        esiFileUploadingRequest: false,
        esiFileUploadingFailure: action.error
      }
    }
    case actions.UPLOAD_ESI_FILE_SUCCESS:
    {
      return {
        ...state,
        esiFileUploadingRequest: false,
        esiFileUploadingSuccess: true,
        esiFileUploadingFailure: false,
      }
    }
    case actions.ESI_PATH_FETCH_REQUEST:
      return {
        ...state,
        esiPath: null,
        esiPathFetchingRequest: true,
        esiPathFetchingSuccess: false,
        esiPathFetchingFailure: false,
      }
    case actions.ESI_PATH_FETCH_FAILURE:
      return {
        ...state,
        esiPath: null,
        esiPathFetchingRequest: false,
        esiPathFetchingSuccess: false,
        esiPathFetchingFailure: action.error
      }
    case actions.ESI_PATH_FETCH_SUCCESS:
      return {
        ...state,
        esiPath: action.path,
        esiPathFetchingRequest: false,
        esiPathFetchingSuccess: true,
        esiPathFetchingFailure: false
      }
    case actions.CONFIG_XML_FETCH_SUCCESS:
    {
      return {
        ...state,
        xml: action.xml
      }
    }
    case actions.PROGRAMS_FETCH_REQUEST:
      return {
        ...state,
        loadingProgram: true,
        loadingProgramError: null,
      }
    case actions.PROGRAMS_FETCH_FAILURE:
      return {
        ...state,
        loadingProgram: false,
        loadingProgramError: action.error,
      }
    case actions.PROGRAMS_FETCH_SUCCESS:
      return {
        ...state,
        loadingProgram: false,
        loadingProgramError: null,
        programs: action.programs
      }
    case actions.PROGRAM_UPDATE_REQUEST:
    {
      const programs = {...state.programs}
      programs[action.name] = action.program
      return {
        ...state,
        updatingProgram: true,
        updatingProgramError: null,
        programs
      }
    }
    case actions.PROGRAM_UPDATE_FAILURE:
    {
      return {
        ...state,
        updatingProgram: false,
        updatingProgramError: action.error,
      }
    }
    case actions.PROGRAM_UPDATE_SUCCESS:
    {
      const programs = {...state.programs}
      programs[action.program.name] = action.program
      return {
        ...state,
        updatingProgram: false,
        updatingProgramError: null,
        programs
      }
    }
    case actions.TOGGLE_CREATE_PROGRAM_MODAL:
    {
      return {
        ...state,
        showCreateProgramModal: !state.showCreateProgramModal,
        creatingProgram: false,
        creatingProgramFailure: null,
        creatingProgramSuccess: null
      }
    }
    case actions.PROGRAM_CREATE_REQUEST:
    {
      return {
        ...state,
        creatingProgram: false,
        creatingProgramFailure: null,
        creatingProgramSuccess: null,
      }
    }
    case actions.PROGRAM_CREATE_FAILURE:
    {
      return {
        ...state,
        creatingProgram: false,
        creatingProgramFailure: action.error,
        creatingProgramSuccess: null,
      }
    }
    case actions.PROGRAM_CREATE_SUCCESS:
    {
      let {program} = action
      let programs = {...state.programs}
      programs[program.name] = program
      return {
        ...state,
        programs,
        showCreateProgramModal: false,
        creatingProgramFailure: null,
        creatingProgram: null,
        creatingProgramSuccess: program,
      }
    }


    case actions.TOGGLE_DELETE_PROGRAM_MODAL:
    {
      return {
        ...state,
        showDeleteProgramModal: action.name?action.name:!state.showDeleteProgramModal,
        deletingProgram: false,
        deletingProgramFailure: null,
        deletingProgramSuccess: null
      }
    }
    case actions.PROGRAM_DELETE_REQUEST:
    {
      return {
        ...state,
        deletingProgram: false,
        deletingProgramFailure: null,
        deletingProgramSuccess: null,
      }
    }
    case actions.PROGRAM_DELETE_FAILURE:
    {
      return {
        ...state,
        deletingProgram: false,
        deletingProgramFailure: action.error,
        deletingProgramSuccess: null,
      }
    }
    case actions.PROGRAM_DELETE_SUCCESS:
    {
      let {program} = action
      let programs = {...state.programs}
      delete programs[program.name]
      return {
        ...state,
        programs,
        showDeleteProgramModal: false,
        deletingProgramFailure: null,
        deletingProgram: null,
        deletingProgramSuccess: program,
      }
    }

    case actions.TOGGLE_EDIT_PROGRAM_NAME:
    {
      let {name} = action
      return {
        ...state,
        renamingProgram: false,
        renamingProgramFailure: null,
        renamingProgramSuccess: null,
        editingProgramName: state.editingProgramName == name?null:name
      }
    }

    case actions.PROGRAM_RENAME_REQUEST:
    {
      return {
        ...state,
        renamingProgram: false,
        renamingProgramFailure: null,
        renamingProgramSuccess: null,
      }
    }
    case actions.PROGRAM_RENAME_FAILURE:
    {
      return {
        ...state,
        renamingProgram: false,
        renamingProgramFailure: action.error,
        renamingProgramSuccess: null,
      }
    }
    case actions.PROGRAM_RENAME_SUCCESS:
    {
      let {program, name, copy} = action
      let programs = {...state.programs}
      if(!copy){
        delete programs[name]
      }
      programs[program.name] = program
      return {
        ...state,
        programs,
        editingProgramName: null,
        renamingProgram: null,
        renamingProgramFailure: null,
        renamingProgramSuccess: program,
      }
    }

    case actions.RESET_RENAME_PROGRAM_STATUS:
    {
      return {
        ...state,
        renamingProgram: null,
        renamingProgramFailure: null,
        renamingProgramSuccess: null,
      }

    }
    default:
      return state
  }
  return state
}