import React, {Component, Fragment} from 'react';
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import routes from '../routes'
import Header from './Header'
import Footer from './Footer'
import Icon from 'antd/lib/icon'
import {getCurrentDashboard, getDashboards} from "../selectors/config";
import {
  loadInterfaceConfig,
  navigate,
  resetErrorMessage,
  updateRouterState,
  startWebsocket,
  stopWebsocket,
  sendCmd,
  createCell,
  updateDashboard,
} from '../actions'
import {ConfigConsumer} from 'antd/lib/config-provider'
import {getGetInterval, getXml, getWsUrl} from "../selectors";

class App extends Component {
  constructor(props) {
    super(props)
    this.handleChange = this.handleChange.bind(this)
    this.handleDismissClick = this.handleDismissClick.bind(this)
    this.getData()
  }

  getData(){
  }

  componentWillMount() {
    this.props.loadInterfaceConfig()
    this.props.updateRouterState({
      pathname: this.props.location.pathname,
      params: this.props.params,
    })
    if(!!this.props.ws){
      this.props.startWebsocket(this.props.ws)
    }
  }

  componentWillUnmount() {
    clearInterval(this.intervalTimer)
    this.props.stopWebsocket()
  }

  componentWillReceiveProps(nextProps, nextContext) {
    if (this.props.location.pathname !== nextProps.location.pathname)
      this.props.updateRouterState({
        pathname: nextProps.location.pathname,
        params: nextProps.params,
      })
    if(this.props.ws !== nextProps.ws && !!nextProps.ws){
      this.props.startWebsocket(nextProps.ws)
    }
    if(this.props.getInterval !== nextProps.getInterval && !!nextProps.getInterval){
      if(this.intervalTimer){
        clearInterval(this.intervalTimer)
      }
      const {sendCmd, getInterval} = nextProps
      this.intervalTimer = setInterval(()=>{
        const {websocketConnected, xml} = this.props
        if(websocketConnected){
          sendCmd('get', 'get')
          // if(!xml){
          //   sendCmd('get_xml', 'xml')
          // }
        }
      }, getInterval)
    }
  }

  handleDismissClick(e) {
    this.props.resetErrorMessage()
    e.preventDefault()
  }


  handleChange(nextValue) {
    this.props.navigate(`/${nextValue}`)
  }

  renderErrorMessage() {
    const { errorMessage } = this.props
    if (!errorMessage) {
      return null
    }

    return (
      <p style={{ backgroundColor: '#e99', padding: 10 }}>
        <b>{errorMessage}</b> (<a href="#" onClick={this.handleDismissClick}>
        Dismiss
      </a>)
      </p>
    )
  }

  renderApp = ({getPrefixCls}) => {
    const {location:{pathname}, dashboards, navigate, prefixCls: customizePrefixCls, createCell, dashboard, updateDashboard} = this.props
    const prefixCls = getPrefixCls('app', customizePrefixCls);
    if(!dashboards){
      return <div className={`${prefixCls}-loading`}>
        <Icon type="loading" />
      </div>
    }
    return <div className={prefixCls}>
      <Header />
      <div className={`${prefixCls}-content`}>
      {this.renderErrorMessage()}
      {routes}
      </div>
      <Footer />
    </div>
  }

  render(){
    // console.log('App render()')
    return <ConfigConsumer>{this.renderApp}</ConfigConsumer>
  }
}

App.propTypes = {
  // Injected by React Redux
  errorMessage: PropTypes.string,
  navigate: PropTypes.func.isRequired,
  updateRouterState: PropTypes.func.isRequired,
  resetErrorMessage: PropTypes.func.isRequired,
  loadInterfaceConfig: PropTypes.func.isRequired,
  startWebsocket: PropTypes.func.isRequired,
  stopWebsocket: PropTypes.func.isRequired,
  updateDashboard: PropTypes.func.isRequired,

  websocketConnected: PropTypes.bool.isRequired,
  // Injected by React Router
  children: PropTypes.node,
  location: PropTypes.any,
  params: PropTypes.any,
}

function mapStateToProps(state) {
  return {
    dashboards: getDashboards(state),
    dashboard: getCurrentDashboard(state),
    xml: getXml(state),
    errorMessage: state.errorMessage,
    websocketConnected: state.robot.websocketConnected,
    getInterval: getGetInterval(state),
    ws: getWsUrl(state),
  }
}

export default connect(
  mapStateToProps,
  {
    loadInterfaceConfig,
    navigate,
    updateRouterState,
    resetErrorMessage,
    startWebsocket,
    stopWebsocket,
    createCell,
    sendCmd,
    updateDashboard
  },
)(App)

