import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Menu from 'antd/lib/menu'
import Dropdown from 'antd/lib/dropdown'
import Icon from 'antd/lib/icon'
import CellByTypes from "../cells";
const MenuItem = Menu.Item

class LayoutCellMenu extends Component {

  constructor(props) {
    super(props)
    this.state = {
      subMenuIsOpen: false,
    }
    const self = this
    self.cellByTypes = {}
    Object.keys(CellByTypes).forEach(key=>{
      self.cellByTypes[key.toLowerCase()]=CellByTypes[key]
    })
  }

  getOptions = () => {
    let {cell} = this.props
    return {...cell.options}
  }
  updateOptions = options => {
    let cell = {...this.props.cell}
    cell.options = {...cell.options, ...options}
    this.props.onCellUpdate(cell)
  }


  handleConfigCell = () => {
    this.updateOptions({
      ...this.getOptions(),
      showConfig: true,
    })
  }

  handleDeleteCell = () => {
    const {onDelete, cell} = this.props
    onDelete(cell.i)
  }

  handleCloneCell = () => {
    let {onClone, cell} = this.props
    cell = {...cell}
    cell['y'] = cell['y'] + 1
    onClone(cell)
  }

  editMenu = (prefixCls) => {
    const self = this
    const {
      cell,
      onCellUpdate,
    } = this.props
    const Config = (cell && cell.type && this.cellByTypes[cell.type.toLowerCase()])?this.cellByTypes[cell.type.toLowerCase()].CONFIG:null
    const menu = <Menu onClick={(e)=>{
      switch (e.key) {
        case 'config':
          self.handleConfigCell()
          break
        case 'clone':
          self.handleCloneCell()
          break
        case 'delete':
          self.handleDeleteCell()
          break
        default:
          break
      }
    }}>
      {!!Config &&
      <MenuItem key="config">
        
        <Config cell={cell} onCellUpdate={onCellUpdate}/>
      </MenuItem>
      }
      <MenuItem key="clone">
        
      </MenuItem>
      <MenuItem key="delete">
        
      </MenuItem>
    </Menu>
    return <Dropdown
      overlay={menu}
      trigger={['click']}
      placement={'bottomRight'}
      className={`${prefixCls}--button`}
      overlayClassName={`${prefixCls}--dropdown-menu`}
      getPopupContainer={self.props.getPopupContainer}
    >
      <Icon type="edit" />
    </Dropdown>
  }

  renderMenu = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      isEditable,
    } = this.props
    if (!isEditable) {
      return
    }
    const prefixCls = getPrefixCls('layout-cell-context', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
    >
      {this.editMenu(prefixCls)}
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderMenu}</ConfigConsumer>
  }
}

LayoutCellMenu.propTypes = {
  cell: PropTypes.object.isRequired,
  isEditable: PropTypes.bool.isRequired,
  getPopupContainer: PropTypes.func.isRequired,
  onClone: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired,
}

export default LayoutCellMenu