import React, {Component} from "react"
import PropTypes from 'prop-types'
import Form from "antd/lib/form"
import Modal from 'antd/lib/modal'
import Radio from 'antd/lib/radio'
import Icon from 'antd/lib/icon'
import Input from 'antd/lib/input'
import InputNumber from 'antd/lib/input-number'
import {createProgram} from "../../../actions";

const FormItem = Form.Item
const RadioGroup = Radio.Group
const RadioButton = Radio.Button

class CreateProgramModal extends  Component {
  handleSubmit = e => {
    e.preventDefault();
    const {createProgram} = this.props
    this.props.form.validateFields((err, fieldsValue) => {
      if (err) {
        console.error(err)
        return
      }
      createProgram(fieldsValue)
    })
  }
  render() {
    const {
      toggleCreateProgramModal,
      prefixCls,
      creatingProgramStatus
    } = this.props
    const { getFieldDecorator } = this.props.form;
    return <Modal
      title=""
      visible={!!creatingProgramStatus.showCreateProgramModal}
      okText={""}
      cancelText={""}
      onOk={this.handleSubmit}
      onCancel={toggleCreateProgramModal}
      wrapClassName={`${prefixCls}-create-program-modal`}
    >
      <Form>
        <FormItem label="" className={`${prefixCls}-input-col`}>
          {getFieldDecorator('name', {
            rules: [{ type: 'string', required: true, message: '' }]
          })(<Input size={"small"} disabled={!!creatingProgramStatus.creatingProgram}/>)}
        </FormItem>
      </Form>
      <div className={`${prefixCls}-status`}>
        {!!creatingProgramStatus.creatingProgram && <div className={"info"}>
          ...
        </div>}
        {!!creatingProgramStatus.creatingProgramFailure && <div className={"error"}>
          : {creatingProgramStatus.creatingProgramFailure}
        </div>}
        {!!creatingProgramStatus.creatingProgramSuccess && <div className={"success"}>
          
        </div>}
      </div>
    </Modal>
  }
}


CreateProgramModal.propTypes = {
  createProgram: PropTypes.func.isRequired,
  toggleCreateProgramModal: PropTypes.func.isRequired,
  prefixCls: PropTypes.string.isRequired,
  creatingProgramStatus: PropTypes.object.isRequired,
}

export default Form.create()(CreateProgramModal)
